import React from 'react';
import PropTypes from 'prop-types';
import Protected from './ProtectedComponent'

function FormActions(props) {
    return (
        <div className="form-actions">
            <div className="row">
                <div className="col-md-12">
                    <div className="row">
                        <div className="col"></div>
                        <div className="col-md-5">
                            {props.actions.map(action => {
                                return (
                                    <Protected key={action.key ?? action.text} requirePermissions={action.requirePermissions} action="disable">
                                    	<button type="button" onClick={action.onClick} className={action.className}>
                                            {action.text}
                                        </button>
                                    </Protected>
                                )
                            })}
                        </div>
                        <div className="col"></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

FormActions.propTypes = {
    actions: PropTypes.array.isRequired
}

export default FormActions