import React from 'react'
import logo from '../assets/img/ascentis_logo.png'
import NavItem from './NavItem'
import { Link } from "react-router-dom"
import { UserContext } from '../Context/UserContext'
import Protected from './ProtectedComponent'
import PermissionsConstant from '../assets/permission.config.json'

class SideBar extends React.Component {
    static contextType = UserContext;
    render() {
        let organizationId = this.context.organizationId;
        let userId = this.context.id;

        return (
            <div className="compact-menu" style={{ zIndex: 49 }}>
                <aside style={{ height: "100%" }} className="sidebar sidebar-left">
                    <div className="sidebar-content" style={{ zIndex: 7 }}>
                        <div className="aside-toolbar">
                            <ul className="site-logo">
                                <li>
                                    <Link to="/">
                                        <span className="logo">
                                            <img src={logo} />
                                        </span>
                                    </Link>
                                </li>
                            </ul>
                        </div>
                        <nav className="main-menu"  >
                            <ul className="nav metismenu">
                                <NavItem clickable to="/appLink" or="/" icon="icon la la-home" text="Home" />

                                <Protected requirePermissions={[PermissionsConstant.OrganizationsRead, PermissionsConstant.OrganizationsModify, PermissionsConstant.OrganizationsDelete, PermissionsConstant.RolesModify, PermissionsConstant.PermissionsModify, PermissionsConstant.PermissionsRead]}>
                                    <NavItem to="/organizations" icon="icon la la-building" text="Organization">
                                        <ul className="sidebar-submenu" style={{ listStyle: "none" }}>
                                            <Protected requirePermissions={[PermissionsConstant.OrganizationsRead, PermissionsConstant.OrganizationsDelete]}>
                                                <NavItem isExact isChildItem clickable to="/organizations" text="Search" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                                                <NavItem isChildItem clickable to="/organizations/create" text="Create new organization" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                                                <NavItem isChildItem clickable to={`/organizations/edit/${organizationId}`} text="Update organization" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.RolesModify]}>
                                                <NavItem isExact isChildItem clickable to={`/roles`} text="Manage roles" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.PermissionsModify, PermissionsConstant.PermissionsRead]}>
                                                <NavItem isExact isChildItem clickable to={`/permissions`} text="Manage permissions" />
                                            </Protected>
                                        </ul>
                                    </NavItem>
                                </Protected>

                                <Protected requirePermissions={[PermissionsConstant.UserRead, PermissionsConstant.UserModify, PermissionsConstant.UserDelete, PermissionsConstant.ApplicationGroupRead]}>
                                    <NavItem to="/users" icon="icon la la-users" text="Users">
                                        <ul className="sidebar-submenu" className="sidebar-submenu" style={{ listStyle: "none" }}>
                                            <Protected requirePermissions={[PermissionsConstant.UserRead, PermissionsConstant.UserDelete]}>
                                                <NavItem isExact isChildItem clickable to="/users" text="Search" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.UserModify]}>
                                                <NavItem isChildItem clickable to="/users/create" text="Create new user" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.UserModify]}>
                                                <NavItem isChildItem clickable to={`/users/edit/${userId}`} text="Update user" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.ApplicationGroupRead]}>
                                                <NavItem isChildItem clickable to="/users/group/viewall/" text="Manage groups" />
                                            </Protected>
                                        </ul>
                                    </NavItem>
                                </Protected>

                                <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                    <NavItem icon="icon la la-wrench" to="/apiClients" text="API Credential">
                                        <ul className="sidebar-submenu" style={{ listStyle: "none" }}>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialModify]}>
                                                <NavItem isChildItem clickable to="/apiClients/oauthClientId/create" text="Register client app" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to="/apiClients/oauthClientId" text="View client app Ids" />
                                            </Protected>
                                            {/* <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="API Keys" />
                                            </Protected> */}
                                            {/* <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Tracking" />
                                            </Protected> */}

                                        </ul>
                                    </NavItem>
                                </Protected>

                                {/* <Protected requirePermissions={[PermissionsConstant.APICredentialRead, PermissionsConstant.UserModify, PermissionsConstant.UserRead]}>
                                    <NavItem icon="icon la la-user-plus" text="Members">
                                        <ul style={{ listStyle: "none" }}>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Settings" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.UserModify]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Add member" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.UserRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Update members" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Bulk upload" />
                                            </Protected>
                                        </ul>
                                    </NavItem>
                                </Protected> */}

                                <Protected requirePermissions={[PermissionsConstant.AppsRead, PermissionsConstant.ScopeRead, PermissionsConstant.AppsModify]}>
                                    <NavItem icon="icon la la-code-fork" to="/AppSubscription" text="Applications">
                                        <ul className="sidebar-submenu" style={{ listStyle: "none" }}>
                                            {/* <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Service Definitions"></NavItem>
                                            </Protected> */}
                                            <Protected requirePermissions={[PermissionsConstant.AppsRead]}>
                                                <NavItem isChildItem clickable to="/AppSubscription/ViewAll" text="Search" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.AppsModify]}>
                                                <NavItem isChildItem clickable to="/AppSubscription/RegisterService" text="Register Service" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.ScopeRead]}>
                                                <NavItem isChildItem clickable to="/AppSubscription/apiscopes" text="Manage API scopes" />
                                            </Protected>
                                            {/* <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="API Keys" />
                                            </Protected> */}
                                            {/* <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Tracking" />
                                            </Protected> */}
                                        </ul>
                                    </NavItem>
                                </Protected>

                                {/*<Protected requirePermissions={[PermissionsConstant.TermAndConditionRead]}>
                                    <NavItem icon="icon la la-user-secret" to="/PlatformAdmin" text="Platform Admin">
                                        <ul style={{ listStyle: "none" }}>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Authorization Servers" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="CX Settings" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Health Monitoring" />
                                            </Protected>
                                            <Protected requirePermissions={[PermissionsConstant.APICredentialRead]}>
                                                <NavItem isChildItem clickable to={`/error`} text="Billing" />
                                            </Protected> 
                                            <Protected requirePermissions={[PermissionsConstant.TermAndConditionRead]}>
                                                <NavItem isChildItem clickable to={`/PlatformAdmin/termsandconditions`} text=" Manage terms and conditions" />
                                            </Protected>
                                        </ul>
                                    </NavItem>
                                </Protected>*/}
                                {/* <NavItem icon="icon la la-question-circle la-2x" text="Support">
                                    <ul style={{ listStyle: "none" }}>
                                        <NavItem isChildItem clickable to={`/error`} text="Raise Service Ticket" />
                                        <NavItem isChildItem clickable to={`/error`} text="Contact Us" />
                                    </ul>
                                </NavItem> */}
                            </ul>
                        </nav>
                    </div>
                </aside>
            </div >
        )
    }
}

export default SideBar