import React from "react";
import { Link, useLocation } from "react-router-dom";
import { matchPath } from 'react-router';
import PropTypes from 'prop-types';

function NavItem(props) {
    const location = useLocation();
    const navClass = `nav-dropdown padding-nav-dropdown ${(matchPath(location.pathname, { path:props.to, exact:props.isExact }) || location.pathname == props.or) ? "active" : ""}`;
    return (
        <li className={navClass}>
            {
                props.clickable
                    ? <Link to={props.to} onClick={() => { document.body.classList.remove('aside-left-open'); }} aria-expanded="false">{props.icon && <i className={props.icon}></i>}<span>{props.text}</span></Link>
                    : <a style={{cursor: "pointer"}}>{props.icon && <i className={props.icon}></i>}<span>{props.text}</span></a>
            }
            {props.children}
        </li>
    );
}

NavItem.propTypes = {
    icon: PropTypes.string,
    text: PropTypes.string.isRequired,
    to: PropTypes.string.isRequired,
}

export default NavItem