import React, { Fragment, useEffect, useState } from 'react'
import SideBar from '../../Components/SideBar'
import TopNav from '../../Components/TopNav'
import SignUpOrganization from '../../Pages/SignUp/SignUpOrganization'
import ROUTES, { RenderRoutes } from '../../routes'
import { UserContext } from '../../Context/UserContext'
import { useAuth } from 'oidc-react';
import { useHistory } from 'react-router-dom'
import OrganizationSwitcher from './OrganizationSwitcher'
import fetcher from '../../utils/fetcher'
import { webserviceUrl } from '../../utils/configuration';
import { showErrorAlert } from '../../Components/Alert'
import { DefaultErrorMessage } from '../../utils/Constant'
import jwt from 'jwt-decode'
import WithLoadingBackDrop from '../../Components/HOC/WithLoadingBackDrop'

function Home(props) {
    const auth = {};
    const history = useHistory();
    const setUserImage = (profilePicture) => {
        const { user } = state;
        const newUser = { ...user, profilePicture }
        setState(state => ({ ...state, user: newUser }));
    }
    const [state, setState] = useState({
        user: {
            setUserImage: setUserImage
        },
        isLoading: false,
        isOrganizationSelected: false,
        previousOrganizationId: "",
        previousOrganizationName: "",
        previousUserRoles: [],
        role: "",
        userHasOrganization: true,
    })

    const isLoggedIn = () => {
        return true;
        return !!auth.userData;
    }

    useEffect(() => {
        if (!user.organizationId) return;
        fetcher(`${webserviceUrl}/api/User/GetPermissions?OrganizationId=${user.organizationId}`, {},
            (result) => {
                const { user } = state;
                user.permissions = result.data;
                setState(state => ({ ...state, isLoading: false, user, userHasOrganization: true }))
            },
            (ex) => showErrorAlert(DefaultErrorMessage)
        );
    }, [state.user.organizationId])



    useEffect(() => {
        setState(state => ({ ...state, isLoading: false, user, userHasOrganization: true }))
        props.setLoading(false);
        return;
        if (!auth.userData) return;
        if (auth.userData.scope.split(' ').indexOf("ascentiscx") <= -1) {
            history.push('/Unauthorized')
        }
        const profile = auth.userData?.profile;
        const claims = jwt(auth.userData?.access_token);
        const organizationId = claims["https://ascentis.com.sg/organizationid"];
        const organizationName = claims["https://ascentis.com.sg/organizationname"]
        if (profile) {
            const { user } = state;
            user.id = profile.sub;
            window.organizationId = organizationId;
            user.organizationId = organizationId;
            user.organizationName = organizationName;
            if (!user.organizationId) {
                setState(state => ({ ...state, isLoading: false, user, userHasOrganization: false }))
                props.setLoading(false)
            } else {
                fetcher(
                    `${webserviceUrl}/api/User/GetOwnProfile?organizationId=${user.organizationId}`,
                    {},
                    (result) => {
                        const { profilePicture, userFullName, email, staffId, mobileNumber, roles } = result.data;
                        user.userFullName = userFullName ?? email ?? staffId ?? mobileNumber;
                        user.userName = email ?? mobileNumber ?? staffId;
                        user.userRoles = roles.map(x => x.name);
                        user.profilePicture = profilePicture;
                        setState(state => ({ ...state, user, isOrganizationSelected: true }))
                        props.setLoading(false)
                    },
                    () => {
                        showErrorAlert(DefaultErrorMessage)
                        props.setLoading(false)
                    }
                )
            }
        }
    }, [isLoggedIn()])

    const { isLoading, user, isOrganizationSelected, userHasOrganization } = state;

    const setOrganization = (organizationId, organizationName, userRoles) => {
        const { user } = state;
        window.organizationId = organizationId;
        user.organizationId = organizationId;
        user.organizationName = organizationName;
        user.userRoles = userRoles;
        setState(state => ({ ...state, user, isOrganizationSelected: true }))
    };

    const showOrganizationSwitcher = () => {
        setState(state => ({ ...state, isOrganizationSelected: false }));
    }

    const closeOrganizationSwitcher = () => {
        setState(state => ({ ...state, isOrganizationSelected: true }));
    }

    return !isLoading && (
        <UserContext.Provider value={user}>
            {true
                ? (
                    <Fragment>
                        <SideBar />
                        <div className="content-wrapper">
                            <TopNav setOrganization={setOrganization} showOrganizationSwitcher={showOrganizationSwitcher} />
                            <RenderRoutes routes={ROUTES} />
                        </div>
                    </Fragment>
                )
                :
                (<Fragment>
                    {userHasOrganization ?
                        <OrganizationSwitcher setOrganization={setOrganization} closeOrganizationSwitcher={closeOrganizationSwitcher} />
                        : <SignUpOrganization setOrganization={setOrganization} />
                    }
                </Fragment>)
            }
        </UserContext.Provider>
    )
}

export default WithLoadingBackDrop(Home)