import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import PropTypes from 'prop-types';
import { webserviceUrl } from '../utils/configuration'
import fetcher from '../utils/fetcher';
import Checkbox from './Checkbox';
import RoleSelector from './RoleSelector'

function RoleEditor(props) {
    const [state, setState] = useState({
        name: props.name ?? "",
        description: props.description ?? "",
        permissions: props.permissions ?? [],
        wasValidated: false,
        errors: {},
        showUploadModel: false,
        permissionList: {},
        parent: props.parent ?? ""
    });

    useEffect(() => {
        fetcher(`${webserviceUrl}/api/permission/GetPermissions?SortBy=group`,
            { method: 'GET', },
            (result) => {
                const permissionList = result.data.data.reduce((r, a) => {
                    r[a.group] = [...r[a.group] || [], a];
                    return r;
                }, {})
                setState(state => ({ ...state, permissionList }));
            }
        );
    }, [])


    const handleInputChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value }));
    }

    function validate() {
        const { name, description } = state;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Role Name is a required field."
        }
        if (!description.trim()) {
            isValid = false
            errors.description = "Description is a required field."
        }
        setState(state => ({ ...state, errors, wasValidated: true }));
        return isValid;
    }

    const onSubmit = () => {
        const {
            name,
            description,
            permissions,
            parent
        } = state;
        if (!validate()) return;

        props.submit({
            name: name.trim(),
            description: description.trim(),
            permissions,
            parent
        })
    }

    const handleChangeParent = (event) => {
        if (event) {
            setState(state => ({ ...state, parent: { id: event.value, name: event.label } }));
            return;
        }
        setState(state => ({ ...state, parent: null }));
    }

    const formActions = [
        {
            text: props.buttontype,
            onClick: onSubmit,
            className: "btn btn-primary submitButton"
        },
        {
            text: "Cancel",
            onClick: () => props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const handlePermissionChange = (event) => {
        const permissions = [...state.permissions];
        if (event.target.checked) {
            permissions.push(event.target.value);
        } else {
            var index = permissions.indexOf(event.target.value);
            if (index !== -1) {
                permissions.splice(index, 1);
            }
        }
        setState(state => ({ ...state, permissions }))
    }

    useEffect(() => {
        setState(state => ({ ...state, errors: props.errors }));
    }, [props.errors])

    const {
        name,
        description,
        errors,
        wasValidated,
        permissionList,
        permissions,
        parent
    } = state;

    const renderPermission = () => Object.keys(permissionList).map(property => (
        <div key={property} className="form-group row">
            <label htmlFor="permissions" className="control-label col-md-3">{property}</label>
            <div className="col-md-5">
                {renderPermissionCheckbox(permissionList[property])}
            </div>
        </div>
    ));

    const renderPermissionCheckbox = (permissionList) => (
        <React.Fragment>
            {
                permissionList.map(item => (
                    <div key={item.id} className="divPadRight15 divorganizationz1">
                        {<Checkbox
                            id={item.id}
                            name={item.name}
                            value={item.id}
                            checked={permissions.includes(item.id)}
                            label={item.displayName}
                            onChange={handlePermissionChange}
                        />}
                    </div>
                ))
            }
        </React.Fragment>
    )

    return (
        <React.Fragment>
            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                {props.header}
                                <div className="card-body p-b-20">
                                    <form action="#" className="form-horizontal">
                                        <div className="form-body">
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Role Name"
                                                    value={name}
                                                    onChange={handleInputChange}
                                                    name="name"
                                                    autoComplete="role-name"
                                                    type="text"
                                                    id="role-name"
                                                    error={errors.name}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Description"
                                                    onChange={handleInputChange}
                                                    name="description"
                                                    autoComplete="role-description"
                                                    type="textarea"
                                                    id="role-description"
                                                    rows="3"
                                                    value={description}
                                                    error={errors.description}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <label
                                                    htmlFor="parentRole"
                                                    className="control-label col-md-3"
                                                >
                                                    Parent Role
                                                </label>
                                                <div className="col-md-8">
                                                    <RoleSelector onChange={handleChangeParent} value={parent ? { value: parent.id, label: parent.name } : null} />
                                                </div>
                                            </div>
                                            {props.subHeader}
                                            {renderPermission()}
                                        </div>
                                    </form>
                                </div>
                                <div className="card-footer bg-light">
                                    <FormActions actions={formActions} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </React.Fragment>
    )
}

RoleEditor.propTypes = {
    name: PropTypes.string,
    description: PropTypes.string,
    permissions: PropTypes.array
}

export default withRouter(RoleEditor)