import React from "react";
import LinkedAppEditor from "../../../Components/LinkedAppEditor";
import fetcher from '../../../utils/fetcher'
import { showErrorAlert } from '../../../Components/Alert'
import { webserviceUrl } from '../../../utils/configuration'
import ChangePageTitle from "../../../Components/ChangePageTitle";

class RegisterService extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {},
            app: {
                name: "",
                description: "",
                appUrl: "",
                icon: "",
                preview: false,
                enabled: false,
                hideFromHome: false,
                adminPortal: "",
                documentLink: "",
            },
            wasValidated: false
        }
    }

    changeAppValue = (name, value) => {
        const app = { ...this.state.app };
        app[name] = value;
        this.setState({ app });
    }

    setErrors = (errors) => {
        this.setState({ errors });
    }

    submit = () => {
        const isValid = this.validate(this.state.app)
        if (!isValid) return;
        fetcher(
            `${webserviceUrl}/api/Application/CreateApplication`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify(this.state.app),
            },
            () => this.props.history.push('/AppSubscription/ViewAll'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }

        )
    }

    validate = () => {
        const { name, description, appUrl, icon } = this.state.app;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Application Name is a required field."
        }
        if (!description || !description.trim()) {
            isValid = false
            errors.description = "Description is a required field."
        }
        if (!appUrl || !appUrl.trim()) {
            isValid = false
            errors.appUrl = "Application Url is a required field."
        }
        if (!icon || !icon.key.trim()) {
            isValid = false
            errors.icon = "Icon is a required field."
        }
        this.setState({ errors, wasValidated: true })
        return isValid;
    }
  
    render() {
        const title = "Create Application";
        const { errors, wasValidated } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}</h5>
            </div>
        );
        return (
            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                {header}
                                <LinkedAppEditor app={this.state.app} wasValidated={wasValidated} submit={this.submit} changeAppValue={this.changeAppValue} errors={errors} />
                            </div>
                        </div>
                    </div>
                </section>
                <ChangePageTitle pageTitle={title} />
            </div>
        )
    }
}

export default RegisterService