import React, { Fragment } from 'react';
import DataTable from 'react-data-table-component';
import TableHeader from '../../../Components/TableHeader';
import CustomDataTablePagination from '../../../Components/CustomDataTablePagination';
import CustomTableStyle from '../../../utils/CustomTableStyle'
import fetcher from '../../../utils/fetcher'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert'
import { DefaultErrorMessage, EmailRegex, SUCCESS, SuccessfullyDeleted, SuperUser, YES } from '../../../utils/Constant'
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import { webserviceUrl } from '../../../utils/configuration';
import { withRouter } from 'react-router';
import { UserContext } from '../../../Context/UserContext';
import FormInput from '../../../Components/FormInput';
import FormActions from '../../../Components/FormActions';
import ProtectedComponent from '../../../Components//ProtectedComponent';
import PermissionsConstant from '../../../assets/permission.config.json'
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class Administrator extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: "",
            wasValidated: false,
            showDeleteConfirmModel: false,
            id: "",
            errors: {},
            isLoaded: false,
            items: [],
            page: 1,
            pageSize: 10,
            total: 0,
            sortColumn: props.orderBy || "",
            sortDirection: "desc",
            searchTerm: "",
            searchText: ""
        };
    }

    setPage = (page) => this.setState({ page });

    setPageSize = (pageSize) => {
        pageSize = parseInt(pageSize);
        const page = this.preventPageOverflow(pageSize);
        this.setState({ pageSize, page })
    };

    setSortColumn = (column, sortDirection) => this.setState({ sortColumn: column.selector, sortDirection });

    preventPageOverflow = (newPageSize) => {
        const { page, pageSize } = this.state;
        let startItem = (page - 1) * pageSize + 1;
        return Math.ceil(startItem / newPageSize);
    }

    search = (e) => {
        this.setState();
    }

    apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/User/GetUsers?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`)
        }
        if ((sortBy).length == 0)
        {
            apiUrl = "".concat(apiUrl, `&sortBy=userFullName`);
            sortDirection = "asc"
        }

        apiUrl = "".concat(apiUrl, `&organizationId=${this.props.match.params.id}`)
        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        apiUrl = "".concat(apiUrl, `&clientAdminOnly=true`)
        return apiUrl;
    }

    getItems = () => {
        const { page, pageSize, searchTerm, sortColumn, sortDirection } = this.state;
        fetcher(
            this.apiUrl(page, pageSize, searchTerm, sortColumn, sortDirection),
            {},
            (result) => {
                this.props.setLoading(false)
                this._isMounted && this.setState({
                    isLoaded: true,
                    items: result.data.data,
                    total: result.data.total
                });
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    componentDidMount() {
        this._isMounted = true;
        this.getItems();
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    validate() {
        const { email } = this.state;
        const errors = {};
        let isValid = true;
        if (!email) {
            isValid = false
            errors.email = "Email Address is a required field."
        }
        if (email && !EmailRegex.test(email)) {
            isValid = false
            errors.email = "Email Address is not valid."
        }
        this.setState({ errors, wasValidated: true });
        return isValid;
    }

    onSubmit = () => {
        const { email } = this.state;
        if (!this.validate()) return;
        const user = UserContext
        fetcher(
            `${webserviceUrl}/api/User/CreateOrganizationAdmin`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ email: email, organizationId: this.props.match.params.id })
            },
            () => { showSuccessAlert(SUCCESS), this.getItems(), this.setState({ email: "", wasValidated: false }), this.props.updateOrganization(false) },
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    componentDidUpdate(prevProps, prevState) {
        const { page, pageSize, searchTerm, sortColumn, sortDirection } = this.state;

        if (
            prevState.page != page
            || prevState.pageSize != pageSize
            || prevState.searchTerm != searchTerm
            || prevState.sortColumn != sortColumn
            || sortDirection != prevState.sortDirection
            || this.props.shouldReload
        ) {
            this.getItems();
            this.props.setShouldReload && this.props.setShouldReload(false)
        }
    }

    handleInputChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
        this.props.updateOrganization(true);
    }

    resetPassword = (id) => {
        fetcher(
            `${webserviceUrl}/api/User/SendResetPassword?id=${id}`,
            {
                method: 'GET',
                cache: 'no-cache',
                mode: 'cors',
                headers: {
                    'content-type': 'application/json',
                }
            },
            () => showSuccessAlert('Reset Password Email Sent'),
        );
    };

    resendActiveEmail = (id) => {
        fetcher(
            `${webserviceUrl}/api/User/ResendActiveEmail?id=${id}`,
            {
                method: 'GET',
                cache: 'no-cache',
                mode: 'cors',
                headers: {
                    'content-type': 'application/json',
                }
            },
            () => showSuccessAlert('Activation Email Sent'),
        );
    };

    closeDeleteConfirmModel = () => {
        this.setState({ showDeleteConfirmModel: false })
        this.getItems();
    }

    deleteUser = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/User/DeleteUser?userId=${id}&organizaitonId=${this.props.match.params.id}&reason=${reason} `,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, this.closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, this.closeDeleteConfirmModel)
        )
    }

    render() {
        const { items, pageSize, total, page, searchText, email, wasValidated, errors, showDeleteConfirmModel, id } = this.state;
        const columns = [
           
            {
                name: 'Name',
                selector: 'userFullName',
                sortable: true,
                maxWidth: "40%"
            },
            {
                name: 'Email',
                selector: 'email',
                sortable: true,
                maxWidth: "25%"
            },
            {
                name: 'Enabled',
                selector: 'status',
                sortable: true,
                cell: row => row.status,
                maxWidth: "20%"
            },
            {
                name: 'Actions',
                selector: 'edit',
                sortable: false,
                maxWidth: "15%",
                cell: row =>
                    <React.Fragment>
                        <ProtectedComponent action="disable" requirePermissions={[PermissionsConstant.UserModify]}>
                            <div>{!row.passwordNotCreated && <i onClick={() => this.resetPassword(row.id)} className="la la-expeditedssl hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Reset Password</span></i>}
                            {row.passwordNotCreated && <i onClick={() => this.resendActiveEmail(row.id)} className="la la-envelope hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Verify Email</span></i>}</div>
                        </ProtectedComponent>&nbsp;
                        <ProtectedComponent action="disable" requirePermissions={[PermissionsConstant.UserDelete]}>
                            <div><i onClick={() => { this.setState({ showDeleteConfirmModel: true, id: row.id }) }} className="la la-trash hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Delete User</span></i></div>
                        </ProtectedComponent>
                    </React.Fragment>
            },
        ];

        const search = () => this.setState({ searchTerm: this.state.searchText });
        const onSearchInput = (e) => {
            if (!e.target.value) {
                this.setState({ searchText: "", searchTerm: "" })
            }
            this.setState({ searchText: e.target.value })
        }

        const inputClasses = {
            labelClass: "control-label col-md-12",
            colWidthClass: "col-md-12",
            inputClass: "form-control"
          }
          
        const tableHeader = (
            <TableHeader
                searchText={searchText}
                onChangePageSize={this.setPageSize}
                pageSize={pageSize}
            />
        )

        const formActions = [
            {
                text: "Add New Administrator",
                onClick: this.onSubmit,
                className: "btn btn-primary submitButton"
            }]

        const tableSearchElement = (
            <React.Fragment>
                <div>
                    <div className="table-search-input-wrapper">
                        <div className="input-group md-form form-sm form-1 pl-0">
                            <input onChange={onSearchInput} className="form-control my-0 py-1" type="text" placeholder="Search *" aria-label="Search" />
                            <div className="input-group-prepend">
                                <span onClick={search} className="input-group-text color-primary btn-primary lighten-3 btn-search" id="basic-text1">
                                    <i className="la la-search text-white" aria-hidden="true"></i>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )

        return (
            <Fragment>
                <ChangePageTitle pageTitle="Update Organization Administrators" />
                    <div className="card">
                        <div className="card-body">
                            <form action="#" className="form-horizontal">
                                <ProtectedComponent requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                                    <div className="form-group row">
                                        <label className={inputClasses.labelClass}>They will be the administrator of your organization. Your organization service emails, notification and other emails will be sent to them.</label>
                                    </div>
                                    <div className="form-body">
                                        <div className="form-group row">
                                            <FormInput
                                                label="Add Administrator email address"
                                                value={email}                                                        
                                                onChange={this.handleInputChange}
                                                name="email"
                                                autoComplete="email"
                                                type="text"
                                                id="email"
                                                error={errors.email}
                                                wasValidated={wasValidated}
                                                isRequired={true}
                                            />                                                                             
                                        </div>
                                        <div className="card-footer bg-light">                                                                                    
                                            <FormActions actions={formActions} />                                               
                                        </div> 
                                    </div>
                                </ProtectedComponent>
                                <br/>
                                <DataTable
                                    title={tableSearchElement}
                                    actions={tableHeader}
                                    columns={columns}
                                    data={items}
                                    pagination={false}
                                    paginationServer={true}
                                    customStyles={CustomTableStyle}
                                    sortServer={false}
                                    striped={true}
                                    responsive={true} />
                            </form>
                        </div>                         
                    </div>
                    {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={this.deleteUser} closeModel={this.closeDeleteConfirmModel} />}
             
            </Fragment>
        )
    }
}

export default withRouter(WithLoadingBackDrop(Administrator))