import React from "react";
import UserEditor from "../../../Components/UserEditor";
import fetcher from "../../../utils/fetcher";
import { webserviceUrl } from '../../../utils/configuration';
import { showErrorAlert } from '../../../Components/Alert';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class CreateUser extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {}
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/User/CreateUser`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify(data)
            },
            (result) => {
                if (result.message) {
                    showErrorAlert(result.message)
                }
                else {
                    this.props.history.push('/users')
                }
            },
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    render() {
        const title = "Create User";
        const { errors } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}</h5>
            </div>
        );
        return (
            <div>
                <ChangePageTitle pageTitle={title} />
                <UserEditor errors={errors} header={header} submit={this.submit} buttontype="Create" />
            </div>
        )        
    }
}

export default CreateUser