import React from "react";
import RoleEditor from "../../../Components/RoleEditor";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import { DefaultErrorMessage } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class EditRole extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showDeleteConfirmModel: false,
            errors: {},
            name: ""
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Role/UpdateRole`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id })
            },
            () => this.props.history.push('/roles'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if (!result.Data) 
                {
                    showErrorAlert(result.message)
                }
                else {
                    this.setState({ errors: result.Data });
                }
            }
        )
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/Role/GetRole?id=${this.props.match.params.id}`,
            {},
            (result) => {
                const {
                    name,
                    description,
                    permissions,
                    parent
                } = result.data;

                this.apiClientData = {
                    name,
                    description,
                    permissions,
                    parent
                }
                this.setState({ name })
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    render() {
        const title = "Edit Role";
        const { errors, name } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}: {name}</h5>
            </div>
        );
        const subHeader = (
            <div className="divPadTopBottom15">
                <h5 className="subHeaderFontRed">Permissions</h5>
            </div>
        );

        const { isLoading } = this.props;
        return (
            <React.Fragment>                
                <ChangePageTitle pageTitle={title} />
                {!isLoading && <RoleEditor errors={errors} header={header} subHeader={subHeader} submit={this.submit} {...this.apiClientData} isEdit={true} buttontype="Update" />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(EditRole)