import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import UploadFileModel from '../Components/UploadFileModel'
import FormActions from './FormActions';
import PermissionsConstant from '../assets/permission.config.json'
function LinkedAppEditor(props) {
    const [showUploadModel, setshowUploadModel] = useState(false);
    const handleInputChange = (event) => {
        props.changeAppValue(event.target.name, event.target.value);
    }

    const hanndleCheckBoxChange = (event) => {
        props.changeAppValue(event.target.name, event.target.checked);
    }

    const closeUploadFileModel = () => {
        setshowUploadModel(false);
    }

    const onChooseImage = (icon) => {
        props.changeAppValue("icon", icon);
        setshowUploadModel(false);
    }

    const formActions = [
        {
            text: props.buttontype ?? "Create",
            onClick: props.submit,
            className: "btn btn-primary submitButton",
            requirePermissions: [PermissionsConstant.AppsModify]
        },
        {
            text: "Cancel",
            onClick: () => props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const {
        app,
        icon,
        errors,
        wasValidated,
    } = props;

    return (
        <React.Fragment>
             <div className="row">
                <div className="col">
                    <div className="card">
                        <div className="card-body p-b-20">
                            <form action="#" className="form-horizontal">
                                <div className="form-body">
                                    <div className="form-group row">
                                        <FormInput
                                            label="Application Name"
                                            value={app.name}
                                            onChange={handleInputChange}
                                            name="name"
                                            autoComplete="application-name"
                                            type="text"
                                            id="application-name"
                                            error={errors.name}
                                            wasValidated={wasValidated}
                                            isRequired={true}
                                        />
                                    </div>
                                    <div className="form-group row">
                                        <FormInput
                                            label="Description"
                                            onChange={handleInputChange}
                                            name="description"
                                            autoComplete="application-description"
                                            type="textarea"
                                            id="application-description"
                                            rows="3"
                                            value={app.description}
                                            error={errors.description}
                                            wasValidated={wasValidated}
                                            isRequired={true}
                                        />
                                    </div>
                                    <div className="form-group row">
                                        <FormInput
                                            label="Application Url"
                                            value={app.appUrl}
                                            onChange={handleInputChange}
                                            name="appUrl"
                                            autoComplete="application-appUrl"
                                            type="text"
                                            id="application-appUrl"
                                            error={errors.appUrl}
                                            wasValidated={wasValidated}
                                            isRequired={true}
                                        />
                                    </div>
                                    <div className="form-group row">
                                        <label className="control-label col-md-3">Preview</label>
                                        <div className="col-md-8">
                                            <input type="checkbox" id="preview" name="preview" checked={app.preview} onChange={hanndleCheckBoxChange} />
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <label className="control-label col-md-3">Enabled</label>
                                        <div className="col-md-8">
                                            <input type="checkbox" id="enabled" name="enabled" checked={app.enabled} onChange={hanndleCheckBoxChange} />
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <label className="control-label col-md-3">Hide From Home</label>
                                        <div className="col-md-8">
                                            <input type="checkbox" id="hideFromHome" name="hideFromHome" checked={app.hideFromHome} onChange={hanndleCheckBoxChange} />
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <label className="control-label col-md-3">
                                            Icon
                                            <span className="text-danger">*</span>
                                        </label>
                                        <div className="col-md-8">
                                            <button type="button" onClick={() => setshowUploadModel(true)} className="btn btn-primary">
                                                Upload image
                                            </button>
                                            <div className="divTopPad">
                                                <img style={{ display: "block", maxWidth: 100, maxHeight: 100 }} src={app.icon && app.icon.url} />
                                            </div>
                                            {errors && errors.icon && <div className={`invalid-feedback ${wasValidated && errors.icon ? 'd-block' : ''}`}>{errors.icon}</div>}
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <FormInput
                                            label="Document Link"
                                            value={app.documentLink}
                                            onChange={handleInputChange}
                                            name="documentLink"
                                            autoComplete="application-documentLink"
                                            type="text"
                                            id="application-documentLink"
                                        />
                                    </div>
                                    <div className="form-group row">
                                        <FormInput
                                            label="Admin Portal"
                                            value={app.adminPortal}
                                            onChange={handleInputChange}
                                            name="adminPortal"
                                            autoComplete="application-adminPortal"
                                            type="text"
                                            id="application-adminPortal"
                                        />
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="card-footer bg-light">
                            <FormActions actions={formActions} />
                        </div>
                        </div>
                    </div>
                </div>
            {showUploadModel && <UploadFileModel images={icon && [icon]} onChooseImage={onChooseImage} closeModel={closeUploadFileModel} />}
        </React.Fragment>
    )
}

LinkedAppEditor.propTypes = {
}

export default withRouter(LinkedAppEditor)