import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { webserviceUrl } from '../utils/configuration';
import { Link } from 'react-router-dom';
import PermissionsConstant from '../assets/permission.config.json'
import Protected from './ProtectedComponent'
import { showConfirmModel, showErrorAlert } from './Alert'
import fetcher from '../utils/fetcher'
import { UserContext } from '../Context/UserContext';

function AppItem(props) {
    const userContext = useContext(UserContext);
    const description = props.description ? <p className="card-text">{props.description}</p> : null;
    
    const subcribeApp = () => {
        fetcher(`${webserviceUrl}/api/Application/SubcribeApplication`,
            {
                mode: 'cors',
                method: "POST",
                body: JSON.stringify({
                    organizationId: userContext.organizationId,
                    appId: props.id
                })
            },
            () => props.onSubcribeChange(props.id, true),
            (ex) => {
                const result = JSON.parse(ex.message);                
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
            }
        )
    }

    const unSubcribeApp = () => {
        fetcher(`${webserviceUrl}/api/Application/unsubcribeApplication`,
            {
                mode: 'cors',
                method: "POST",
                body: JSON.stringify({
                    organizationId: userContext.organizationId,
                    appId: props.id
                })
            },
            () => props.onSubcribeChange(props.id, false),
            (ex) => {
                const result = JSON.parse(ex.message);                
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
            }
        )
    }

    const showSubcribeWarnning = (e) => {
        e.preventDefault();
        const wrapper = document.createElement('div');
        wrapper.innerHTML = `Confirm subscription to this App? Your organization might be charged a fee upon activation. 
        Contact our customer support to check on pricing.`;
        showConfirmModel(
            {
                content: wrapper,
                icon: "warning"
            },
            (result) => {
                if (result) {
                    subcribeApp()
                }
            }
        );
    }

    const showUnSubcribeWarnning = (e) => {
        e.preventDefault();
        const wrapper = document.createElement('div');
        wrapper.innerHTML = `Confirm unsubscribe to this App? You will lose access to your data immediately.`;
        showConfirmModel(
            {
                content: wrapper,
                icon: "error"
            },
            (result) => {
                if (result) {
                    unSubcribeApp()
                }
            }
        );
    }

    return (
        <div className="col-sm-12 col-md-6 col-xl-3" style={{ marginBottom: "1.875em", minWidth: 255 }} >
            <div className={`card ${props.enabled ? '' : 'disabled'}`} style={{ height: "100%", marginBottom: 30 }}>
                <div className="card-content">
                    <div className="card-body">
                        <img width={52} height={52} src={props.icon.url} />
                        <br/><br/>
                        <h4 className="card-title">{props.name}
                            {
                                !props.readonly &&
                                <Protected requirePermissions={[PermissionsConstant.AppsModify]}>
                                    <Link to={`/AppSubscription/${props.id}`}><i className="icon la la-pencil"></i></Link>
                                </Protected>
                            }
                        </h4>
                        {description}
                        {
                            !props.readonly && (
                                <React.Fragment>
                                    {
                                        <Protected requirePermissions={[PermissionsConstant.AppsSubcribe]}>
                                            {props.subcribed ?
                                                <a href="#" className={!props.enabled ? "disabled": ''} onClick={showUnSubcribeWarnning}>Unsubscribe</a>
                                                : <a href="#" className={!props.enabled ?  "disabled": ''} onClick={showSubcribeWarnning}>Subscribe</a>}
                                        </Protected>
                                    }
                                    <br></br>
                                    <Protected requirePermissions={[PermissionsConstant.AppsSubcribe]}>
                                        <a className={!props.enabled ?  "disabled": ''}  href={props.adminPortal}>Setting</a>
                                    </Protected>
                                </React.Fragment>
                            )
                        }
                        {
                            props.readonly &&
                            <a href={props.appUrl} target="_blank" aria-expanded="false" className="btn btn-primary" style={{ position: "absolute", bottom: 20 }}>
                                Go To App
                            </a>
                        }
                    </div>
                </div>
            </div>
        </div>
    );
}

AppItem.propTypes = {
    icon: PropTypes.object.isRequired,
    description: PropTypes.string.isRequired,
    appUrl: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired
}

export default AppItem