import Authorize from './Components/Authorize'
import SignUp from './Pages/SignUp/SignUp'
import Verify from './Pages/SignUp/Verify'
import TermsAndCondition from './Pages/SignUp/TermsAndCondition'
import AdditionalInformation from './Pages/SignUp/AdditionalInformation'
import SignUpOrganization from './Pages/SignUp/SignUpOrganization'
import React from 'react'
import ErrorPage from './Pages/Error'
import { Switch, Route, BrowserRouter } from "react-router-dom"
import { PageNotFoundMessage, ConnectionProblem, UnauthorizeMessage } from './utils/Constant'
import "core-js/stable";
import "regenerator-runtime/runtime";
import 'bootstrap/dist/css/bootstrap.min.css'
import './assets/css/common/main.bundle.css'
import './assets/css/layouts/vertical/core/main.css'
import './assets/css/layouts/vertical/themes/theme-a.css'
import 'bootstrap/dist/js/bootstrap.min.js'
import './assets/css/icons/line-awesome.min.css'
import './assets/css/icons/dripicons.min.css'
import './assets/css/icons/material-design-iconic-font.min.css'
import './assets/css/layouts/vertical/menu-type/compact.css'
import './assets/css/login.css'
import './assets/css/style.css'
import './assets/css/common/file-upload-with-preview.min.css'
import "react-datetime/css/react-datetime.css";
import 'react-image-crop/dist/ReactCrop.css';
import Home from "./Pages/Home/index"

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/404" component={() => <ErrorPage errorMessage={PageNotFoundMessage} title="404" />} />
        <Route exact path="/ConnectionDown" component={() => <ErrorPage errorMessage={ConnectionProblem} title="Connection Error" />} />
        <Route exact path="/SignUp/:activationId" component={SignUp} />
        <Route exact path="/SignUp" component={SignUp} />
        <Route exact path="/Verify/:activationId" component={Verify} />
        <Route exact path="/TermsAndCondition/:activationId" component={TermsAndCondition} />
        <Route exact path="/AdditionalInformation/:activationId" component={AdditionalInformation} />
        <Route exact path="/SignUpOrganization" component={SignUpOrganization} />
        <Route exact path="/Unauthorized" component={() => <ErrorPage errorMessage={UnauthorizeMessage} title="Unauthorized" />} />
        <Route path="/" component={Home} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
