import React, { useEffect, useState, useContext } from "react";
import AppItem from '../../../Components/AppItem';
import fetcher from '../../../utils/fetcher'
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop'
import { webserviceUrl } from '../../../utils/configuration'
import { UserContext } from '../../../Context/UserContext'
import { showErrorAlert } from '../../../Components/Alert';
import { DefaultErrorMessage } from '../../../utils/Constant';
import Protected from '../../../Components/ProtectedComponent';
import PermissionsConstant from '../../../assets/permission.config.json'
import { Link } from 'react-router-dom';
import ChangePageTitle from "../../../Components/ChangePageTitle";

function AppSubscription(props) {
    const [apps, setApps] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");

    const onSubcribeChange = (id, subcribed) => {
        const app = apps.find(x => x.id == id);
        app.subcribed = subcribed;
        setApps([...apps]);
    }

    const getData = () => {
        const apiUrl = `${webserviceUrl}/api/Application/Applications?page=0&pageSize=0&searchTerm=${encodeURIComponent(searchTerm)}`;
        fetcher(
            apiUrl,
            {},
            (result) => {
                setApps(result.data ?? [])
                props.setLoading(false);
            },
            () => {
                showErrorAlert(DefaultErrorMessage)
                props.setLoading(false);
            }
        )
    }

    useEffect(() => {
        getData();
    }, [])

    const app = apps.map((item, index) => <AppItem key={item.id} {...item}
        onSubcribeChange={onSubcribeChange}
    />)

    const onSearchInput = (e) => {
        if (!e.target.value) {
            setSearchTerm("");
        }
        setSearchTerm(e.target.value)
    }
    const search = () => {
        getData()
    };

    const title = "All Product and Services";
    return (
        <React.Fragment>
            <ChangePageTitle pageTitle={title} />
            <div className="main-content-header">
                <div className="container-fluid">
                    <div className="col-xs-12">
                        <h2>{title}</h2>
                    </div>
                </div>
            </div>
            <div className="content">
                <div className="container-fluid">
                    <div className="main-content row">
                        <div className="col-md-6 divPadTop10Bottom30">
                            <div className="table-search-input-wrapper">
                                <div className="input-group md-form form-sm form-1 pl-0">
                                    <input onChange={onSearchInput} className="form-control my-0 py-1" type="text" placeholder="Application Name" aria-label="Search" />
                                    <div className="input-group-prepend">
                                        <span onClick={search} className="input-group-text color-primary btn-primary lighten-3 btn-search" id="basic-text1">
                                            <i className="la la-search text-white" aria-hidden="true"></i>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 text-sm-right divPadTop10Bottom30">
                            <Protected action="disable" requirePermissions={[PermissionsConstant.AppsModify]}>
                                <Link
                                    to={"/AppSubscription/RegisterService"}
                                    className="btn btn-primary"
                                    aria-haspopup="true" aria-expanded="true"
                                >
                                    Register Service
                                </Link>
                            </Protected>
                        </div>
                    </div>
                </div>
                <div className="container" style={{ minHeight: "calc(85vh - 50px)", display: "flex", alignItems: "center" }}>
                    <div className="row">
                        {app}
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default withLoadingBackDrop(AppSubscription)