import React from "react";
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';
import { WorkInProgress } from "../../utils/Constant";
import ChangePageTitle from "../../Components/ChangePageTitle";

function ErrorPage(props) {
    return (
        <div className="" style={{ justifyContent: "center", display: "flex", width: "100%" }}>            
            <ChangePageTitle pageTitle="Error" />
            <div className="page-wrap d-flex flex-row align-items-center">
                <div className="container">
                    <section className="page-content-new container-fluid">
                        <div className="card">
                            <div className="card-body">
                                <div className="row justify-content-center">
                                    <div className="col-md-12 text-center">
                                        <span className="display-4 d-block">{props.title?? WorkInProgress}</span>
                                        <div className="mb-4 lead">{props.errorMessage} </div>
                                        <Link to="/appLink" aria-expanded="false" className="btn btn-primary">Back to Home</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    )
}

ErrorPage.propTypes = {
    title: PropTypes.string.isRequired,
    errorMessage: PropTypes.string
}

export default ErrorPage