import React from "react";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import Tabs from "../../../Components/Tabs";
import ClientId from './ClientId';
import FormActions from '../../../Components/FormActions';
import PermissionsConstant from '../../../assets/permission.config.json';
import Contacts from './Contacts';
import Personalization from './Personalization';
import Scopes from './Scopes';
import { UserContext } from '../../../Context/UserContext';
import { makeRandomSecureString } from '../../../utils/random';
import { withRouter } from 'react-router-dom';
import Overview from './Overview';

class CreateApiClient extends React.Component {
    static contextType = UserContext;
    constructor(props) {
        super(props);
        this.state = {
            clientApi: {
                clientAppName: "",
                clientId: makeRandomSecureString(26),
                clientSecrets: makeRandomSecureString(26),
                allowedGrantType: "authorization_code",
                redirectUris: "",
                postLogoutRedirectUris: "",
                allowedScopes: "openid",
                allowedCorsOrigins: "",
                logoUri: "",
                backgroundImage: "",
                appHomePage: "",
                privacyPolicyLink: "",
                termOfServiceLink: "",
                supportEmail: "",
                developerEmail: ""
            },
            errors: {},
            completion: {},
            wasValidated: false
        }
    }

    validate = () => {
        const {
            clientAppName,
            redirectUris,
        } = this.state.clientApi;
        const errors = {};
        let isValid = true;
        if (!clientAppName) {
            isValid = false;
            errors.clientAppName = 'Client App Name is a required field.';
        }
        if (!redirectUris) {
            isValid = false;
            errors.redirectUris = 'Redirect URL is a required field.';
        }
        this.setState({ errors, wasValidated: true });
        return isValid;
    }

    handleInputChange = (name, value) => {
        this.setState({ clientApi: { ...this.state.clientApi, [name]: value } });
    };

    submit = () => {
        if (this.validate()) {
            fetcher(
                `${webserviceUrl}/api/ApiClient/CreateApiClient`,
                {
                    method: 'POST',
                    cache: 'no-cache',
                    mode: 'cors',
                    body: JSON.stringify({
                        ...this.state.clientApi,
                        organizationId: this.context.organizationId,
                        logoUri: this.state.clientApi.logoUri.key,
                        backgroundImage: this.state.clientApi.backgroundImage.key
                    }),
                },
                () => this.props.history.push('/apiClients/oauthClientId'),
                (ex) => {
                    const result = JSON.parse(ex.message);
                    if(!result.Data) 
                    { 
                        showErrorAlert(result.message) 
                    }
                    else {
                        this.setState({ errors: result.Data});
                    }
                }
            )
        }
    }

    formActions = [
        {
            text: "Create",
            onClick: this.submit,
            className: "btn btn-primary submitButton",
            requirePermissions: [PermissionsConstant.APICredentialModify]
        },
        {
            text: "Cancel",
            onClick: () => this.props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    render() {
        const submitAction = (
            <div className="card-footer bg-light">
                <FormActions actions={this.formActions} />
            </div>)
        const { clientApi, errors, wasValidated } = this.state;
        return (
            <div className="main-content">            
                <section className="page-content container-fluid">
                    <div className="card">
                        <div className="card-header card-header-flex">
                            <h5 className="card-header-text">New API Credential</h5>
                        </div>
                        {/* <div className="card-header"> */}
                            <Tabs>
                                <div className="card-header-text" label="Overview">
                                    <Overview clientApi={clientApi} />
                                </div>
                                <div label="Client Id">
                                    <ClientId clientApi={clientApi} wasValidated={wasValidated} handleInputChange={this.handleInputChange} errors={errors} />
                                    {submitAction}
                                </div>
                                <div label="Scopes">
                                    <Scopes clientApi={clientApi} wasValidated={wasValidated} handleInputChange={this.handleInputChange} errors={errors} />
                                    {submitAction}
                                </div>
                                <div label="Contacts">
                                    <Contacts clientApi={clientApi} wasValidated={wasValidated} handleInputChange={this.handleInputChange} errors={errors} />
                                    {submitAction}
                                </div>
                                <div label="Personalization">
                                    <Personalization clientApi={clientApi} wasValidated={wasValidated} handleInputChange={this.handleInputChange} errors={errors} />
                                    {submitAction}
                                </div>
                            </Tabs>
                        {/* </div> */}
                    </div>
                </section>
            </div>
        )
    }
}

export default withRouter(CreateApiClient)