import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../../../Components/FormInput';
import FormActions from '../../../Components/FormActions';
import { showConfirmModel } from '../../../Components/Alert';
import Protected from '../../../Components/ProtectedComponent';
import PermissionsConstant from '../../../assets/permission.config.json';
import {
  showErrorAlert,
  showSuccessAlert,
} from '../../../Components/Alert';
import {
  DefaultErrorMessage,
  SUCCESS,
} from '../../../utils/Constant';
import fetcher from '../../../utils/fetcher';
import { webserviceUrl } from '../../../utils/configuration';
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import ChangePageTitle from "../../../Components/ChangePageTitle";

function IdentityProvider(props) {
  const [state, setState] = useState({
    id: props.id ?? "",
    identityProvider: {
      provider: 'Google',
      clientId: props.clientId ?? "",
      clientSecret: props.clientSecret ?? "",
      returnUrl: props.returnUrl ?? "",
    },
    wasValidated: false,
    errors: {},
  });

  const handleInputChange = (event) => {
    const { identityProvider } = state;
    const newIdentityProvider = {
      ...identityProvider,
      [event.target.name]: event.target.value,
    };
    setState(state => ({ ...state, identityProvider: newIdentityProvider }));
  };

  function validate() {
    const { identityProvider, } = state;
    let isValid = true;
    if (!identityProvider.clientId.trim()) {
      isValid = false;
      errors.clientId = 'Client Id is a required field.';
    }
    if (!identityProvider.clientSecret.trim()) {
      isValid = false;
      errors.clientSecret = 'Client Secret is a required field.';
    }
    if (!identityProvider.returnUrl.trim()) {
      isValid = false;
      errors.returnUrl = 'Return Url is a required field.';
    }
    setState(state => ({ ...state, errors, wasValidated: true }));
    return isValid;
  }

  useEffect(() => {
    fetcher(
      `${webserviceUrl}/api/Organization/IdentityProvider?organizationID=${props.match.params.id}`,
      {},
      (result) => {
        const identityProvider = {
          provider: result.data.provider ?? '',
          clientId: result.data.clientId ?? '',
          clientSecret: result.data.clientSecret ?? '',
          returnUrl: result.data.returnUrl ?? '',
        };
        setState(state => ({ ...state, identityProvider: identityProvider }));
        props.setLoading(false);
      },
      () => {
        props.setLoading(false);
        showErrorAlert(DefaultErrorMessage);
      }
    );
  }, []);

  const onSubmit = () => {
    if (!validate()) return;
    fetcher(
      `${webserviceUrl}/api/Organization/SetIdentityProvider`,
      {
        method: 'POST',
        cache: 'no-cache',
        mode: 'cors',
        body: JSON.stringify({
          ...identityProvider,
          organizationId: props.match.params.id,
        }),
      },
      () => {
        showSuccessAlert(SUCCESS), props.updateOrganization(false);
      },
      (ex) => {
        const result = JSON.parse(ex.message);
        if (!result.Data) {
          showErrorAlert(result.message);
        }
        else {
          setState(state => ({ ...state, errors: result.Data }));
        }
      }
    );
  };

  const formActions = [
    {
      text: 'Update',
      onClick: onSubmit,
      className: 'btn btn-primary submitButton',
      requirePermissions: [PermissionsConstant.OrganizationsModify],
    },
    {
      text: 'Cancel',
      onClick: () =>
        showConfirmModel(
          {
            message:
              'Your form has not been submitted. Are you sure you want to exit?',
            confirmBtnText: 'Exit',
          },
          (result) => {
            if (result) {
              props.history.push('/applink');
            }
          }
        ),
      className: 'btn btn-secondary clear-form btn-outline',
    },
  ];

  const providerOptions = [
    {
      name: 'Google',
      value: 'Google',
    },
  ];

  const { identityProvider, errors, wasValidated } = state;
  return (
    <React.Fragment>
      <ChangePageTitle pageTitle="Update Organization Identity Provider" />
      <div className="row">
        <div className="col">
          <div className="card">
            {props.header}
            <div className="card-body p-b-20">
              <form action="#" className="form-horizontal">
                <Protected
                  requirePermissions={[
                    PermissionsConstant.OrganizationsModify,
                  ]}
                >
                  <div className="form-body">
                    <div className="form-group row">
                      <FormInput
                        label="Identity Provider"
                        onChange={handleInputChange}
                        name="provider"
                        type="select"
                        id="provider"
                        value={identityProvider.provider}
                        options={providerOptions}
                        wasValidated={wasValidated}
                        isRequired={true}
                      />
                    </div>
                    <div className="form-group row">
                      <FormInput
                        label="Client Id"
                        value={identityProvider.clientId}
                        onChange={handleInputChange}
                        name="clientId"
                        autoComplete="clientId"
                        type="text"
                        id="clientId"
                        wasValidated={wasValidated}
                        isRequired={true}
                        error={errors.clientId}
                      />
                    </div>
                    <div className="form-group row">
                      <FormInput
                        label="Client Secret"
                        value={identityProvider.clientSecret}
                        onChange={handleInputChange}
                        name="clientSecret"
                        autoComplete="clientSecret"
                        type="text"
                        id="clientSecret"
                        wasValidated={wasValidated}
                        isRequired={true}
                        error={errors.clientSecret}
                      />
                    </div>
                    <div className="form-group row">
                      <FormInput
                        label="Return Url"
                        value={identityProvider.returnUrl}
                        onChange={handleInputChange}
                        name="returnUrl"
                        autoComplete="returnUrl"
                        type="text"
                        id="returnUrl"
                        isRequired={true}
                        wasValidated={wasValidated}
                        error={errors.returnUrl}
                      />
                    </div>
                  </div>
                </Protected>
              </form>
            </div>
            <div className="card-footer bg-light">
              <FormActions actions={formActions} />
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

IdentityProvider.propTypes = {};

export default withRouter(WithLoadingBackDrop(IdentityProvider));
