import React, { useState, useContext, useEffect } from 'react';
import imgplaceholder from '../assets/img/preview-place-holder.png';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import { Active, Inactive, EmailRegex, Activation, DefaultErrorMessage } from '../utils/Constant';
import PropTypes from 'prop-types';
import { UserContext } from '../Context/UserContext';
import ReactSelect from 'react-select';
import fetcher from '../utils/fetcher';
import { webserviceUrl } from '../utils/configuration';
import UploadFileModel from './UploadFileModel';
import { showErrorAlert } from './Alert';

function UserEditor(props) {
    const user = useContext(UserContext);
    const [state, setState] = useState({
        name: props.name ?? "",
        staffId: props.staffId ?? "",
        mobileNumber: props.mobileNumber ?? "",
        loginType: "",
        email: props.email ?? "",
        status: props.isEdit ? props.status : Activation,
        roles: props.roles ?? [],
        wasValidated: false,
        errors: {},
        organizationName: props.organizationName ?? "",
        roleOptions: [],
        showLogoModel: false,
    });

    const [profilePicture, setProfilePicture] = useState(props.profilePicture ?? {});

    const updateProfilePicture = (profilePicture) => {
        setProfilePicture(profilePicture);
        setState(state => ({ ...state, showLogoModel: false }))
    }

    const handleInputChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value }));
    }

    const handleRoleChange = (event) => {
        setState(state => ({ ...state, roles: event.map(x => ({ id: x.value, name: x.label })) }));
    }

    useEffect(async () => {
        const roleOptionsPromise = new Promise(function (resolve, reject) {
            fetcher(`${webserviceUrl}/api/Role/GetManageableRole?organizationId=${user.organizationId}`,
                {
                    method: 'GET',
                    cache: 'no-cache',
                    mode: 'cors'
                },
                (result) => {
                    const roleOptions = result.data.map(x => {
                        return {
                            value: x.id,
                            label: x.name
                        }
                    })
                    resolve(roleOptions)
                },
                () => {
                    reject()
                }
            )
        })

        const loginTypePromise = new Promise(function (resolve, reject) {
            fetcher(
                `${webserviceUrl}/api/Organization/GetOrganization?organizationID=${user.organizationId}`,
                {},
                (result) => {
                    const { loginType } = result.data.organizationPolicy ?? {};
                    resolve(loginType)
                },
                () => {
                    showErrorAlert(DefaultErrorMessage)
                    reject()
                }
            )
        })

        let [roleOptions, loginType] = await Promise.all([roleOptionsPromise, loginTypePromise])
        setState(state => ({ ...state, roleOptions, loginType }))
    }, [])

    useEffect(() => {
        setState(state => ({ ...state, errors: props.errors }));
    }, [props.errors])


    const inputClasses = {
        labelClass: "control-label col-md-3",
        colWidthClass: "col-md-5",
        inputClass: "form-control"
    }

    const inputClasses2 = {
        labelClass: "control-label col-md-4",
        colWidthClass: "col-md-8",
        inputClass: "form-control"
    }

    function validate() {
        const { name, staffId, email, roles, loginType, mobileNumber } = state;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Full Name is a required field."
        }
        if (loginType == "Email" && !email.trim()) {
            isValid = false
            errors.email = "Email Address is a required field."
        }
        if (email && !EmailRegex.test(email)) {
            isValid = false
            errors.email = "Email Address is not valid."
        }
        if (!(roles && roles.length > 0)) {
            isValid = false
            errors.roles = "Roles is a required field."
        }
        if (loginType == "Mobile" && !mobileNumber.trim()) {
            isValid = false
            errors.mobileNumber = "Mobile Number is a required field."
        }
        if (loginType == "Code" && !staffId.trim()) {
            isValid = false
            errors.staffId = "Staff Id is a required field."
        }
        setState(state => ({ ...state, errors, wasValidated: true }));
        return isValid;
    }

    const closeUploadLogoModel = () => {
        setState(state => ({ ...state, showLogoModel: false }))
    }

    const formActions = [
        {
            text: props.buttontype ?? "Update",
            onClick: () => {
                if (!validate()) return;
                const { name, staffId, email, status, roles, mobileNumber } = state;
                props.submit({
                    userFullName: name.trim(),
                    staffId: staffId.trim(),
                    email: email.trim(),
                    status,
                    profilePicture: profilePicture.key,
                    roles: roles.map(x => x.id),
                    organizationId: user.organizationId,
                    mobileNumber,
                    profileUrl: profilePicture.url
                })
            },
            className: "btn btn-primary submitButton"
        },
        {
            text: 'Cancel',
            onClick: () => props.history.goBack(),
            className: 'btn btn-secondary clear-form btn-outline',
        }
    ]

    const statusOptions = [
        {
            name: Active,
            value: Active
        },
        {
            name: Inactive,
            value: Inactive
        }
    ]

    if (state.status == Activation) {
        statusOptions.push({ name: Activation, value: Activation })
    }

    const { name, staffId, email, status, wasValidated, errors, roleOptions, roles, mobileNumber, showLogoModel, loginType } = state;

    return (
        <React.Fragment>

            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                {props.header}
                                <div className="card-body">
                                    <form action="#" className="form-horizontal">
                                        <div className="form-body">
                                            <div className="form-group row">
                                                <label className={inputClasses.labelClass}>Create new user account</label>
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Full Name"
                                                    value={name}
                                                    classes={inputClasses}
                                                    onChange={handleInputChange}
                                                    name="name"
                                                    autoComplete="user-name"
                                                    type="text"
                                                    id="user-name"
                                                    error={errors.userFullName ?? errors.name}
                                                    isRequired={true}
                                                    wasValidated={wasValidated}
                                                />
                                            </div>
                                            <div className="border-form">
                                                <h5>Your organization security policy requires at least one mandatory information for the login purpose:</h5><br />
                                                <div className="form-group row">
                                                    <FormInput
                                                        label="Email Address"
                                                        classes={inputClasses2}
                                                        onChange={handleInputChange}
                                                        name="email"
                                                        autoComplete="user-email"
                                                        type="text"
                                                        id="user-email"
                                                        value={email}
                                                        wasValidated={wasValidated}
                                                        error={errors.email}
                                                        isRequired={loginType == "Email"}
                                                    />
                                                </div>
                                                <div className="form-group row">
                                                    <FormInput
                                                        label="Phone Number"
                                                        classes={inputClasses2}
                                                        onChange={handleInputChange}
                                                        name="mobileNumber"
                                                        autoComplete="user-mobileNumber"
                                                        type="text"
                                                        id="user-mobileNumber"
                                                        value={mobileNumber}
                                                        isRequired={loginType == "Mobile"}
                                                        wasValidated={wasValidated}
                                                        error={errors.mobileNumber}
                                                    />
                                                </div>
                                                <div className="form-group row">
                                                    <FormInput
                                                        label="Staff Id"
                                                        value={staffId}
                                                        classes={inputClasses2}
                                                        onChange={handleInputChange}
                                                        name="staffId"
                                                        autoComplete="user-id"
                                                        type="text"
                                                        id="staff-id"
                                                        wasValidated={wasValidated}
                                                        error={errors.staffId}
                                                        isRequired={loginType == "Code"}
                                                    />
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className={inputClasses.labelClass}>Upload Image</label>
                                                <div className="col-md-5">
                                                    <button type="button" onClick={() => setState({ ...state, showLogoModel: !showLogoModel })} className="btn btn-primary">
                                                        Choose Image
                                                    </button>
                                                    <div className="divTopPad">
                                                        <img style={{ display: "block", height: "150px" }} src={profilePicture?.url ? `${profilePicture && profilePicture?.url}` : imgplaceholder } />
                                                    </div>
                                                    <div className="error-upload-image">{errors.profilePicture}</div>
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Enabled"
                                                    classes={inputClasses}
                                                    onChange={handleInputChange}
                                                    name="status"
                                                    type="select"
                                                    id="user-status"
                                                    value={status}
                                                    options={statusOptions}
                                                    disabled={status == Activation || props.isEditOwnProfile}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <label
                                                    htmlFor="roles"
                                                    className={inputClasses.labelClass}
                                                >
                                                    Roles<span className="text-danger">*</span>
                                                </label>
                                                <div className={inputClasses.colWidthClass}>
                                                    <ReactSelect
                                                        onChange={handleRoleChange}
                                                        options={roleOptions}
                                                        isMulti
                                                        isDisabled={props.isEditOwnProfile}
                                                        value={roles.map(x => ({ label: x.name, value: x.id }))}
                                                        className={`react-select-no-border form-control select2-form-control ${wasValidated
                                                            ? errors.roles
                                                                ? 'is-invalid'
                                                                : 'is-valid'
                                                            : ''
                                                            }`}
                                                    />
                                                    {errors.roles && (
                                                        <div className="invalid-feedback">
                                                            {errors.roles}
                                                        </div>
                                                    )}
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                                <div className="card-footer bg-light">
                                    <FormActions actions={formActions} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
            {showLogoModel && <UploadFileModel images={profilePicture && [profilePicture]} onChooseImage={updateProfilePicture} closeModel={closeUploadLogoModel} />}
        </React.Fragment>
    )
}

UserEditor.propTypes = {
    name: PropTypes.string,
    email: PropTypes.string,
    enabled: PropTypes.string,
    profilePicture: PropTypes.object,
    role: PropTypes.string,
    staffId: PropTypes.string
}

export default withRouter(UserEditor)