import React from 'react'
import logo from '../../assets/img/logobrand-red-192.png'
import { webserviceUrl } from '../../utils/configuration'
import { showConfirmModel, showErrorAlert } from '../../Components/Alert'
import fetcher from '../../utils/fetcher'
import { CancelSignUpMessage, DefaultErrorMessage, SUCCESS } from '../../utils/Constant'
import WithLoadingBackDrop from '../../Components/HOC/WithLoadingBackDrop'
import ChangePageTitle from "../../Components/ChangePageTitle";

class Verify extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			otpCode: "",
			error: {},
		}
	}

	validate = () => {
		const { otpCode } = this.state;
		const error = {};
		let isValid = true;
		if (!otpCode.trim()) {
			isValid = false
			error.otpCode = "Otp Code is a required field."
		}
		this.setState({ ...this.state, error });
		return isValid;
	}

	cancel = () => {
		showConfirmModel({ message: CancelSignUpMessage, confirmBtnText: "Exit" }, () => this.props.history.push('/'))
	}

	componentDidMount() {
		if (this.props.match.params.activationId) {
			fetch(`${webserviceUrl}/api/user/GetUserByActivationId?setPasswordId=${this.props.match.params.activationId}`)
				.then(response => {
					if (!response.ok) {
						throw new Error(response.statusText);
					}
					return response
				})
				.then(result => result.json())
				.then(result => {
					const isValid = result.status == SUCCESS;
					if (!isValid) {
						throw new Error(response.statusText)
					}
					const { status } = result.data;
					switch (status) {
						case 'PendingActivation':
							this.props.history.push(`/SignUp/${this.props.match.params.activationId}`);
							break;
						case 'Activation':
							break;
						case 'AdditionalInformation':
							this.props.history.push(`/AdditionalInformation/${this.props.match.params.activationId}`);
							break;
						case 'Verified':
							this.props.history.push(`/TermsAndCondition/${this.props.match.params.activationId}`);
							break;
						default:
							this.props.history.push(`/`);
							break;
					}
				})
				.catch(ex => {
					this.props.history.push('/404')
				});
		}
		this.props.setLoading(false)
	}

	submit = () => {
		const { otpCode, error } = this.state;
		if (!this.validate()) return;
		fetcher(`${webserviceUrl}/api/user/VerifyUser`,
			{
				mode: 'cors',
				method: "POST",
				body: JSON.stringify({
					otpCode,
					setPasswordId: this.props.match.params.activationId
				})
			},
			(result) => {
				if (result.message) {
					if (result.message == 'AdditionalInformation') {
						this.props.history.push(`/AdditionalInformation/${this.props.match.params.activationId}`)
					} else {
						error.otpCode = result.message;
						this.setState({ error });
					}
				} else {
					this.props.history.push(`/TermsAndCondition/${this.props.match.params.activationId}`)
				}
			},
			(ex) => {
				const result = JSON.parse(ex.message);
				if (!result.Data) {
					showErrorAlert(result.message)
				}
				else {
					this.setState({ errors: result.Data });
				}
			}
		)
	}

	handleInputChange = (event) => {
		this.setState({ [event.target.name]: event.target.value });
	};

	render() {
		const { otpCode, error } = this.state;
		return (
			<div className="container">
				<ChangePageTitle pageTitle="SignUp" />
				<form className="sign-in-form">
					<div className="card">
						<div className="card-body">
							<img src={logo} className="brand text-center d-block m-b-20" />
							<h5 className="sign-in-heading m-b-20">Verify your email address</h5>
							<p className="sign-in-heading m-b-20">Enter the verification code that we sent to your email. If you don't see it, please check your spam folder.</p>
							<div className="form-group">
								<label htmlFor="password" className="sr-only">Otp Code</label>
								<input type="number"
									maxLength="6"
									name="otpCode"
									id="otpCode"
									autoComplete="otpCode"
									className="form-control"
									value={otpCode}
									onChange={this.handleInputChange}
									required=""
									placeholder="Otp Code"
								/>
								{error && error.otpCode && <span className="d-block invalid-feedback">{error.otpCode}</span>}
							</div>
							<div className="divPadLogin">
								<button onClick={this.submit} className="btn btn-primary col-md-5" type="button">Verify</button>&nbsp;&nbsp;&nbsp;
								<button onClick={this.cancel} className="btn btn-secondary col-md-5" type="button">Cancel</button>
							</div>
						</div>
					</div>
				</form>
			</div>
		)
	}
}

export default WithLoadingBackDrop(Verify)