const customTableStyle = {
    table: {
        style: {
            border: "1px solid #ddd",
            borderRightWidth: "0px",
        }
    },
    header: {
        style: {
            padding: 0
        }
    },
    headCells: {
        style: {
            fontWeight: "bold",
            borderRight: '1px solid #ddd',
            fontWeight: 500,
            fontSize: '0.875em',
            color: "#617182",
            width:"100%"
        },
    },
    headRow: {
        style: {
            minHeight: '40px',
            border: 'none'
        }
    },
    cells: {
        style: {
            border: '1px solid #ddd',
            borderLeftWidth: "0px",
            borderBottomWidth: "0px",
        }
    },
    rows: {
        style: {
            '&:not(:last-of-type)': {
                border: 'none'
            },
            minHeight: '30px',
            backgroundColor: "#f3f3f3",
            color: "#617182",
            fontSize: '0.875em'
        },
        stripedStyle: {
            color: "#617182",
            backgroundColor: "#fff",
        }
    },
    pagination: {
        style: {
            border: 'none',
            padding: 0
        }
    }
}

export default customTableStyle