import React, { useState, useRef } from 'react'
import imagePlaceHolder from '../assets/img/preview-place-holder.png'
import CircularProgress from '@material-ui/core/CircularProgress'
import Resizer from "react-image-file-resizer";
import ReactCrop from 'react-image-crop';
import fetcher from '../utils/fetcher'
import { webserviceUrl } from '../utils/configuration';
import { DefaultErrorMessage } from '../utils/Constant';
import toBase64 from '../utils/ToBase64';

function UploadFileModel(props) {
    const [image, setImage] = useState(props.image);
    const [uploading, setUploading] = useState(false);
    const [fileName, setFileName] = useState("");
    const [validationMessage, setValidationMessage] = useState("");
    const [crop, setCrop] = useState(props.crop);
    const imageRef = useRef(null);
    const onFileChanged = function (e) {
        setImage(URL.createObjectURL(e.target.files[0]))
    }

    const getCroppedImg = (image) => {
        let scaleX;
        let scaleY;
        const aspectRatio = image.naturalWidth / image.naturalHeight;

        if (image.naturalWidth > 468) {
            scaleX = image.naturalWidth / 468;
            scaleY = image.naturalHeight / (468 / aspectRatio);
        } else {
            scaleX = image.naturalWidth / image.width;
            scaleY = image.naturalHeight / image.height
        }

        const canvas = document.createElement('canvas');
        const pixelRatio = window.devicePixelRatio;
        const ctx = canvas.getContext('2d');

        canvas.width = crop.width * pixelRatio * scaleX;
        canvas.height = crop.height * pixelRatio * scaleY;

        ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
        ctx.imageSmoothingQuality = 'high';
        ctx.drawImage(
            image,
            crop.x * scaleX,
            crop.y * scaleY,
            crop.width * scaleX,
            crop.height * scaleY,
            0,
            0,
            crop.width * scaleX,
            crop.height * scaleY
        );

        const base64Image = canvas.toDataURL("image/png", 1);
        return base64Image
    }

    const resizeFile = (file, size) =>
        new Promise((resolve) => {
            Resizer.imageFileResizer(
                file,
                size,
                size,
                "PNG",
                100,
                0,
                (uri) => {
                    resolve(uri.replace(/^data:image\/(png);base64,/, ""));
                },
                "base64"
            );
        });

    const onImgLoad = ({ target: img }) => {
        setValidationMessage("")
        if (!props.dimensions) return;
        if (props.dimensions.width != img.naturalWidth || props.dimensions.height != img.naturalHeight) {
            setValidationMessage(`Please upload image according to the preferred size ${props.dimensions.width} x ${props.dimensions.height}`)
        }
    }

    const progressBarStyle = {
        color: "#fff",
        position: "absolute",
        top: "calc(50% - 20px)",
        left: "calc(50% - 20px)",
    }

    const loadingBackdrop = (
        <div className="overlay" style={{ position: "absolute" }}>
            <CircularProgress style={progressBarStyle} color="inherit" />
        </div>
    )

    const uploadImage = async () => {
        let photo;
        if (crop.width == 0 || crop.height == 0) {
            photo = await toBase64(await fetch(imageRef.current.src).then(r => r.blob()))
        } else {
            photo = await getCroppedImg(imageRef.current);
        }
        setFileName(image.name);
        setUploading(true)
        fetcher(
            `${webserviceUrl}/api/File/Upload`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify([
                    {
                        name: image.name,
                        data: photo.split(',').pop()
                    }
                ])
            },
            (data) => {
                setUploading(false);
                props.onChooseImage(data.data[0]);
            },
            () => {
                setUploading(false);
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    return (
        <React.Fragment>
            <div className="modal fade show" tabIndex="-1" role="dialog" style={{ display: "block", opacity: 1, transition: "opacity .15s linear" }}>
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">Image</h5>                            
                            <button onClick={props.closeModel} type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">                            
                            <div className="custom-file-container" data-upload-id="myUniqueUploadId">
                            <label>You can crop your Photo using your mouse by highlighting the area.</label>
                                <label className="custom-file-container__custom-file is-invalid">
                                    <input
                                        type="file"
                                        className="custom-file-container__custom-file__custom-file-input"
                                        accept="image/*"
                                        aria-label="Choose File"
                                        onChange={onFileChanged}
                                    />
                                    <span className="custom-file-container__custom-file__custom-file-control">
                                        {fileName ? fileName : "Choose file..."}
                                        <span className="custom-file-container__custom-file__custom-file-control__button"> Browse </span>
                                    </span>
                                </label>
                                {validationMessage && <div className="invalid-feedback">{validationMessage}</div>}
                                <div className="custom-file-container__image-preview">
                                    {uploading && loadingBackdrop}
                                    {
                                        image ? <ReactCrop src={image} crop={crop} onChange={newCrop => setCrop(newCrop)} />
                                            : <img style={{ width: "auto", height: "auto", display: "block" }} src={imagePlaceHolder} />
                                    }
                                    <img ref={imageRef} style={{ display: 'none' }} src={image} />
                                </div>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" onClick={uploadImage} disabled={validationMessage || !image}>Proceed</button>
                            <button type="button" className="btn btn-secondary" onClick={props.closeModel} data-dismiss="modal">Cancel</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal-backdrop fade show"></div>
        </React.Fragment>
    )
}

export default UploadFileModel