import React from 'react'
import usericon from '../assets/img/usericon.png';
import { Link } from 'react-router-dom';
import { withAuth } from 'oidc-react';
import { UserContext } from '../Context/UserContext'
import { Version } from '../utils/Constant';
import PermissionsConstant from '../assets/permission.config.json'
import Protected from './ProtectedComponent'
import { webserviceUrl } from '../utils/configuration';

class TopNav extends React.Component {
    static contextType = UserContext;
    render() {
        let organizationId = this.context.organizationId
        let userRoles = this.context.userRoles;
        userRoles = [];
        let roleName = userRoles[1] ? `${userRoles[0]}...` : userRoles[0]
        let userProfile = (
            <div className="tooltipLeft">
                <span className="tooltiptextLeft">My Account</span>
                <Link to={`/Profile`}>
                    <table>
                        <tbody>
                            <tr>
                                <td><img className="align-self-center mr-3 w-25 rounded-circle" height="60" width="60" src={this.context.profilePicture?.url ? `${this.context.profilePicture?.url}` : usericon} alt='?' /></td>
                                <td>
                                    <h5 className="mt-0 mb-0">{this.context.userFullName}</h5>
                                    <small>{roleName}</small>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </Link>
            </div>
        )

        const toggleSidebar = () => {
            if (document.body.classList.contains('aside-left-open')) {
                document.body.classList.remove('aside-left-open');
            } else {
                document.body.classList.add('aside-left-open');
            }
        }

        const changePassword = (
            <React.Fragment>
                <Link className="dropdown-item" to="/userManagerment/changePassword">
                    <i className="icon la la-key"></i>Change Password
                </Link>
            </React.Fragment>
        );

        return (
            <nav className="top-toolbar navbar navbar-desktop flex-nowrap">
                <ul className="navbar-nav mobilenav">
                    <li className="nav-item">
                        <a href="#" onClick={toggleSidebar} data-toggle="collapse" data-toggle-state="aside-left-open">
                            <i className="icon dripicons-align-left"></i>
                        </a>
                    </li>
                </ul>

                <div className="centerHeaderAppCenter">
                    <ul className="nav nav-tabs float-left border-0">
                        <li className="nav-item">
                            <h6 className="nav-link-header">{Version}</h6>
                        </li>
                    </ul>
                    <ul className="nav nav-tabs float-right border-0">
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle border-0" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">
                                {this.context.organizationName}
                            </a>
                            <Protected requirePermissions={[PermissionsConstant.OrganizationsModify, PermissionsConstant.SwitchOrganization]}>
                                <div className="dropdown-menu dropdown-menu-right">
                                    <Protected requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                                        <Link className="dropdown-item" to={`/organizations/edit/${organizationId}`}>
                                            <i className="icon zmdi zmdi-group"></i> Manage
                                        </Link>
                                    </Protected>
                                    <Protected requirePermissions={[PermissionsConstant.SwitchOrganization]}>
                                        <Link onClick={() => this.props.showOrganizationSwitcher()} className="dropdown-item" to="/applink">
                                            <i className="icon dripicons-media-shuffle"></i> Switch organizations
                                        </Link>
                                    </Protected>
                                </div>
                            </Protected>
                        </li>
                    </ul>
                </div>

                <ul className="navbar-nav nav-right">
                    <li className="nav-item dropdown">
                        <a className="nav-link nav-pill user-avatar" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">
                            <img src={this.context.profilePicture?.url ? `${this.context.profilePicture?.url}` : usericon} className="w-35 rounded-circle" alt={this.context.userFullName} />
                        </a>
                        <div className="dropdown-menu dropdown-menu-right dropdown-menu-accout">
                            <div className="dropdown-header pb-3">
                                <div className="media d-user">
                                    {userProfile}
                                </div>
                            </div>
                            {changePassword}
                            <button type="button" className="dropdown-item" onClick={() => this.props.signOutRedirect()}><i className="icon la la-sign-out"></i> Sign Out</button>
                        </div>
                    </li>
                </ul>
            </nav>
        );
    }
}
export default withAuth(TopNav)