import React from "react";
import { withRouter } from "react-router-dom";
import OrganizationEditor from "../../../Components/OrganizationEditor";
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop'
import fetcher from "../../../utils/fetcher";
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert'
import { SUCCESS } from '../../../utils/Constant';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class UpdateOrganization extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {}
        }
      }
      
    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/Organization/GetOrganization?organizationID=${this.props.match.params.id}`,
            {},
            (result) => {
                const { id, organizationName, organizationCode, description} = result.data;
                this.organizationData = {
                    id,
                    name: organizationName,
                    code: organizationCode,
                    description
                }
                this.props.setLoading(false)
            },
            () => this.props.setLoading(false)
        )
    }
    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Organization/UpdateOrganization`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id })
            },
            () => {showSuccessAlert(SUCCESS), this.props.updateOrganization(false)},
            (ex) =>
            {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    render() {
        const { errors } = this.state;
        const { isLoading } = this.props;
        return (
            <React.Fragment>
                <ChangePageTitle pageTitle="Update Organization Profile" />
                {!isLoading && <OrganizationEditor errors={errors} submit={this.submit} {...this.organizationData} {...this.update} updateOrganization={this.props.updateOrganization} applications={this.applications} isEdit={true} buttontype="Update" />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(withRouter(UpdateOrganization))