import React from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration'
import { SuccessfullyDeleted } from '../../../utils/Constant'
import fetcher from '../../../utils/fetcher';
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel'
import Protected from '../../../Components/ProtectedComponent'
import PermissionsConstant from '../../../assets/permission.config.json'

export default function linkedApps() {
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);

    const columns = [
        {
            name: 'App Name',
            selector: 'name',
            sortable: true,
            maxWidth: "45%"
        },
        {
            name: 'Description',
            selector: 'description',
            sortable: true,
            maxWidth: "45%"
        },
        {
            name: 'Edit',
            selector: 'edit',
            sortable: false,
            maxWidth: "10%",
            cell: row =>
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.AppsModify]}>
                        <Link to={`/linkedApps/edit/${row.id}`}><i className="la la-edit hover-pointer tooltipTop"><span className="tooltiptextTop">Edit App</span></i></Link>
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.AppsDelete]}>
                        <i onClick={() => { setShowDeleteConfirmModel(true), setId(row.id) }} className="la la-trash hover-pointer tooltipTop"><span className="tooltiptextTop">Delete App</span></i>
                    </Protected>
                </React.Fragment>
        },
    ];

    const title = "Apps Management";

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/Application/GetApplications?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`)
        }
        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteLinkedApps = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/Application/DeleteApplication?id=${id}&reason=${reason}`,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel)
        )
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.AppsModify]}>
            <Link
                to={"/linkedApps/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                New App
            </Link>
        </Protected>
    )

    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.AppsModify]}>
            <Link to={"/linkedApps/create"} className="dropdown-item">
                New App
            </Link>
        </Protected>
    )

    return (
        <React.Fragment>
            <ManagermentTable shouldReload={shouldReload} setShouldReload={setshouldReload} columns={columns} title={title} apiUrl={apiUrl} createButton={createButton} createButtonMobile={createButtonMobile} searchType="Search App Name:" />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteLinkedApps} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}