import React from 'react';
import ChangePageTitle from "../../../Components/ChangePageTitle";


function Overview(props) {
    const { clientApi } = props;

    const notCompleted = "notCompleted"
    const completed = "completed"

    const getStatus = (requiredField, optionalField) => {
        const allField = optionalField.concat(requiredField);
        if (!allField.some(x => clientApi[x])) {
            return notCompleted;
        }

        if (optionalField.some(x => !clientApi[x])) {
            return notCompleted;
        }

        if (optionalField.includes("logoUri") || optionalField.includes("backgroundImage")) {
            if (!clientApi.logoUri.key || !clientApi.backgroundImage.key) return notCompleted;
        }
        return completed;
    }

    const clientIdRequiredField = ["clientId", "redirectUris"];
    const clientIdOptionalField = ["postLogoutRedirectUris", "allowedCorsOrigins"];
    const scopesOptionalField = ["allowedScopes"];
    const contactOptionalField = ["supportEmail", "developerEmail"];
    const personalizationOptionalField = ["logoUri", "backgroundImage", "appHomePage", "privacyPolicyLink", "termOfServiceLink"];
    const allOptionalField = [...clientIdOptionalField, ...scopesOptionalField, ...contactOptionalField, ...personalizationOptionalField];
    const allRequiredField = [...clientIdRequiredField];
    const clientIdStatus = getStatus(clientIdRequiredField, clientIdOptionalField);
    const scopesStatus = getStatus([], scopesOptionalField);
    const contactStatus = getStatus([], contactOptionalField);
    const personalizationStatus = getStatus([], personalizationOptionalField)

    const renderStatus = (status) => {
        switch (status) {
            case notCompleted:
                return <strong className="text-danger">Not Completed</strong>
            default:
                return <strong className="text-success">Completed</strong>
        }
    }

    const overviewStatusMessage = () => {
        if (allRequiredField.some(x => !clientApi[x])) {
            return <p>Your API Credential setup is not completed yet.</p>
        }
        if (allOptionalField.some(x => !clientApi[x])) {
            return <p>Your API Credential setup is partially completed.</p>
        }
        return <p>Your API Credential setup is completed.</p>
    }

    return (
        <React.Fragment>
            <ChangePageTitle pageTitle="API Credential Overview" />
            <div className="row">
                <div className="col">
                    {props.header}
                    <div className="card">
                        <div className="card-body">
                            <div className="container-fluid">
                                {overviewStatusMessage()}
                                <p>When your application requires Ascentis Single Sign On for your user authorization, you need to register
                                    your application as client application here. This is also to allow your user to access Ascentis product APIs.
                                </p>
                            </div>
                            <ul className="list-group list-group-flush">
                                <li className="list-group-item">
                                    <div className="media">
                                        <span className={`api-credential-overview-item align-self-center mr-3 h-40 w-40 rounded-circle ${clientIdStatus == completed ? "completed" : ""}`}>1</span>
                                        <div className="media-body">
                                            <p className="mb-0">Setup<strong className="text-danger">Client Id</strong> for each of your application instance</p>
                                            {renderStatus(clientIdStatus)}
                                        </div>
                                    </div>
                                </li>
                                <li className="list-group-item">
                                    <div className="media">
                                        <span className={`api-credential-overview-item align-self-center mr-3 h-40 w-40 rounded-circle ${scopesStatus == completed ? "completed" : ""}`}>2</span>
                                        <div className="media-body">
                                            <p className="mb-0">Grant<strong className="text-danger">Scopes</strong> to allow your application to access Ascentis Apis.</p>
                                            {renderStatus(scopesStatus)}
                                        </div>
                                    </div>
                                </li>
                                <li className="list-group-item">
                                    <div className="media">
                                        <span className={`api-credential-overview-item align-self-center mr-3 h-40 w-40 rounded-circle ${contactStatus == completed ? "completed" : ""}`}>3</span>
                                        <div className="media-body">
                                            <p className="mb-0">Provide<strong className="text-danger">Contacts</strong> information for your users to reach out to you in case they have questions.</p>
                                            {renderStatus(contactStatus)}
                                        </div>
                                    </div>
                                </li>
                                <li className="list-group-item">
                                    <div className="media">
                                        <span className={`api-credential-overview-item align-self-center mr-3 h-40 w-40 rounded-circle ${personalizationStatus == completed ? "completed" : ""}`}>4</span>
                                        <div className="media-body">
                                            <p className="mb-0"><strong className="ml-0 text-danger">Personalize</strong> your login form</p>
                                            {renderStatus(personalizationStatus)}
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

Overview.propTypes = {

};

export default Overview;
