import React from 'react';
import logo from '../../assets/img/logobrand-red-192.png';
import { webserviceUrl } from '../../utils/configuration';
import WithLoadingBackDrop from '../../Components/HOC/WithLoadingBackDrop';
import { SUCCESS, DefaultErrorMessage, EmailRegex, ExistedEmail } from '../../utils/Constant';
import { showConfirmModel, showErrorAlert, showSuccessAlert } from '../../Components/Alert';
import fetcher from '../../utils/fetcher';
import ChangePageTitle from "../../Components/ChangePageTitle";

class SignUp extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			userFullName: "",
			email: "",
			password: "",
			confirmPassword: "",
			errors: {},
			emailDisable: false,
		}
	}

	componentDidMount() {
		if (this.props.match.params.activationId) {
			fetch(`${webserviceUrl}/api/user/GetUserByActivationId?setPasswordId=${this.props.match.params.activationId}`)
				.then(response => {
					if (!response.ok) {
						throw new Error(response.statusText);
					}
					return response
				})
				.then(result => result.json())
				.then(result => {
					const isValid = result.status == SUCCESS;
					if (!isValid) {
						throw new Error(response.statusText)
					}
					const { userFullName, email, status } = result.data;
					switch (status) {
						case 'PendingActivation':
							this.setState({ isValid: true, userFullName, email, emailDisable: true })
							break;
						case 'Activation':
							this.props.history.push(`/verify/${this.props.match.params.activationId}`);
							break;
						case 'AdditionalInformation':
							this.props.history.push(`/AdditionalInformation/${this.props.match.params.activationId}`);
							break;
						case 'Verified':
							this.props.history.push(`/TermsAndCondition/${this.props.match.params.activationId}`);
							break;
						default:
							this.props.history.push(`/`);
							break;
					}
				})
				.catch(ex => {
					this.props.history.push('/404')
				});
		}
		this.props.setLoading(false)
	}

	validate = () => {
		const { userFullName, email, password, confirmPassword } = this.state;
		const errors = {};
		let isValid = true;
		if (!userFullName.trim()) {
			isValid = false
			errors.userFullName = "Full Name is a required field."
		}
		if (!email.trim()) {
			isValid = false
			errors.email = "Email Address is a required field."
		}
		if (email && !EmailRegex.test(email)) {
			isValid = false
			errors.email = "Email Address is not valid."
		}
		if (!(password.trim())) {
			isValid = false
			errors.password = "Password is a required field."
		}
		if (!(confirmPassword.trim())) {
			isValid = false
			errors.confirmPassword = "Confirm Password is a required field."
		}
		if (password != confirmPassword) {
			isValid = false
			errors.confirmPassword = "Confirm password must match with Password."
		}
		this.setState({ ...this.state, errors });
		return isValid;
	}

	submit = () => {
		const { userFullName, email, password, confirmPassword, errors } = this.state;
		if (!this.validate()) return;
		fetcher(`${webserviceUrl}/api/user/SignUpUser`,
			{
				mode: 'cors',
				method: "POST",
				body: JSON.stringify({
					userFullName,
					email,
					password,
					confirmPassword,
					setPasswordId: this.props.match.params.activationId
				})
			},
			(result) => {
				if (result.message) {
					if (result.message == ExistedEmail) {
						errors.email = result.message;
					} else {
						errors.password = result.message;
					}
					this.setState({ ...this.state, errors });
				} else {
					this.props.history.push(`/verify/${result.data}`)
				}
			},
			(ex) => {
				const result = JSON.parse(ex.message);
				if (!result.Data) {
					showErrorAlert(result.message)
				}
				else {
					this.setState({ ...this.state, errors: result.Data });
				}
			}
		)
	}

	cancel = () => {
		showConfirmModel({ message: CancelSignUpMessage, confirmBtnText: "Exit" }, () => this.props.history.push('/'))
	}

	handleInputChange = (event) => {
		this.setState({ [event.target.name]: event.target.value });
	};

	render() {
		const { isLoading } = this.props;
		const { userFullName, email, password, confirmPassword, errors, emailDisable } = this.state;
		if (isLoading) {
			return <ChangePageTitle pageTitle="SignUp" />
		}
		return (
			<div className="container">
				<ChangePageTitle pageTitle="SignUp" />
				<form className="sign-in-form">
					<div className="card">
						<div className="card-body">
							<img src={logo} className="brand text-center d-block m-b-20" />
							<h5 className="sign-in-heading m-b-20">Create your Ascentis Account</h5>
							<div className="form-group">
								<label htmlFor="userFullName" className="sr-only">Full Name</label>
								<input type="text"
									name="userFullName"
									id="userFullName"
									autoComplete="userFullName"
									className="form-control"
									value={userFullName}
									onChange={this.handleInputChange}
									required=""
									placeholder="Full Name"
								/>
								{errors && errors.userFullName && <span className="d-block invalid-feedback">{errors.userFullName}</span>}
							</div>
							<div className="form-group">
								<label htmlFor="email" className="sr-only">Your Email Address</label>
								<input type="email"
									id="email"
									name="email"
									autoComplete="email"
									className="form-control"
									value={email}
									onChange={this.handleInputChange}
									disabled={emailDisable}
									required=""
									placeholder="Email Address"
								/>
								{errors && errors.email && <span className="d-block invalid-feedback">{errors.email}</span>}
							</div>
							<div className="form-group">
								<label htmlFor="password" className="sr-only">Password</label>
								<input type="password"
									id="password"
									name="password"
									autoComplete="password"
									className="form-control"
									value={password}
									onChange={this.handleInputChange}
									required=""
									placeholder="Password"
								/>
								{errors && errors.password != null && <span className="d-block invalid-feedback">{errors.password}</span>}
							</div>
							<div className="form-group">
								<label htmlFor="confirmPassword" className="sr-only">Confirm new password</label>
								<input
									type="password"
									id="confirmPassword"
									name="confirmPassword"
									autoComplete="new-password"
									className="form-control"
									value={confirmPassword}
									onChange={this.handleInputChange}
									required=""
									placeholder="Confirm Password"
								/>
								{errors && errors.confirmPassword != null && <span className="d-block invalid-feedback">{errors.confirmPassword}</span>}
							</div>
							<div className="divPadLogin">
								<button onClick={this.submit} className="btn btn-primary col-md-5" type="button">Submit</button>&nbsp;&nbsp;&nbsp;
								<button onClick={this.cancel} className="btn btn-secondary col-md-5" type="button">Cancel</button>
							</div>
						</div>
					</div>
				</form>
			</div>
		)
	}
}

export default WithLoadingBackDrop(SignUp)