import React from "react";
import LinkedAppEditor from "../../../Components/LinkedAppEditor";
import DefaultUserGroup from "../../../Components/DefaultUserGroup";
import fetcher from '../../../utils/fetcher'
import { showErrorAlert } from '../../../Components/Alert'
import { webserviceUrl } from '../../../utils/configuration'
import { DefaultErrorMessage } from '../../../utils/Constant'
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop'
import Tabs from '../../../Components/Tabs'
import PermissionsConstant from '../../../assets/permission.config.json'
import Protected from "../../../Components/ProtectedComponent";
import ChangePageTitle from "../../../Components/ChangePageTitle";

class ManageApp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {},
            app: {
                name: "",
                description: "",
                appUrl: "",
                icon: "",
                preview: false,
                enabled: false,
                hideFromHome: false,
                adminPortal: "",
                documentLink: "",
            },
            wasValidated: false
        }
    }

    validate = () => {
        const { name, description, appUrl, icon } = this.state.app;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Application Name is a required field."
        }
        if (!description || !description.trim()) {
            isValid = false
            errors.description = "Description is a required field."
        }
        if (!appUrl || !appUrl.trim()) {
            isValid = false
            errors.appUrl = "Application Url is a required field."
        }
        if (!icon || !icon.key?.trim()) {
            isValid = false
            errors.icon = "Icon is a required field."
        }
        this.setState({ errors, wasValidated: true })
        return isValid;
    }

    submit = () => {
        if (!this.validate()) return;
        fetcher(
            `${webserviceUrl}/api/Application/UpdateApplication`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...this.state.app, id: this.props.match.params.id })
            },
            () => this.props.history.push('/AppSubscription/ViewAll'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    changeAppValue = (name, value) => {
        const app = { ...this.state.app };
        app[name] = value;
        this.setState({ app });
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/Application/GetApplication?id=${this.props.match.params.id}`,
            {},
            (result) => {
                const {
                    name,
                    description,
                    appUrl,
                    icon,
                    preview,
                    enabled,
                    hideFromHome,
                    documentLink,
                    adminPortal
                } = result.data;
                const app = {
                    name,
                    description,
                    appUrl,
                    icon,
                    preview,
                    enabled,
                    hideFromHome,
                    documentLink,
                    adminPortal
                }
                this.setState({ app });
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    render() {
        const title = "Update Application";
        const { errors, wasValidated, app } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}: {app.name}</h5>
            </div>
        );
        const { isLoading } = this.props;
        return (
            !isLoading && 
            <div className="main-content">
                <ChangePageTitle pageTitle={title} />
                <section className="page-content container-fluid">               
                    <div className="card">
                        <div className="card-header card-header-flex">
                            <h5 className="card-header-text">{title}: {app.name}</h5>
                        </div>          
                            <Tabs update={this.state.updateOrganization} updateOrganization={this.setUpdateOrganization} >
                                <div label="Basic Setting">
                                    <LinkedAppEditor submit={this.submit} changeAppValue={this.changeAppValue} app={this.state.app} errors={errors} wasValidated={wasValidated} buttontype="Update" />
                                </div>
                                <Protected label="User Groups" requirePermissions={[PermissionsConstant.ApplicationGroupModifyDefault]}>
                                    <div label="User Groups">
                                        <DefaultUserGroup applicationId={this.props.match.params.id} wasValidated={wasValidated} />
                                    </div>
                                </Protected>
                            </Tabs>                                
                    </div>
                </section>
            </div>
        )
    }
}

export default withLoadingBackDrop(ManageApp)