import React from "react";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import { DefaultErrorMessage, SuccessfullyDeleted } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import UserSelector from '../../../Components/UserSelector';
import FormActions from "../../../Components/FormActions";
import DeleteConfirmModel from "../../../Components/ConfirmDeleteModel";
import { UserContext } from '../../../Context/UserContext';
import DataTable from "react-data-table-component";
import CustomTableStyle from '../../../utils/CustomTableStyle'
import ProtectedComponent from "../../../Components/ProtectedComponent";
import PermissionsConstant from '../../../assets/permission.config.json';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class AssignUserToGroup extends React.Component {
    static contextType = UserContext
    constructor(props) {
        super(props);
        this.state = {
            errors: {},
            userName: {},
            users: [],
            group: {},
            user: {
                id: "",
                userName: ""
            },
            showDeleteConfirmModel: false
        }
    }

    columns = [
        {
            name: 'User Name',
            selector: 'userFullName',
            sortable: true,
            maxWidth: "20%"
        },
        {
            name: 'Email',
            selector: 'email',
            sortable: true,
            maxWidth: "40%"
        },
        {
            name: 'Staff Id',
            selector: 'userId',
            sortable: true,
            maxWidth: "20%"
        },
        {
            name: 'Actions',
            sortable: false,
            maxWidth: "20%",
            cell: row =>
                <React.Fragment>
                    <ProtectedComponent action="disable" requirePermissions={[PermissionsConstant.UserDelete]}>
                        <div><i onClick={() => { this.setState({ showDeleteConfirmModel: true, id: row.id }) }} className="la la-trash hover-pointer tooltipTop icon-big">
                            <span className="tooltiptextTop">Remove User</span>
                        </i></div>
                    </ProtectedComponent>
                </React.Fragment>
        },
    ];

    closeDeleteConfirmModel = () => {
        this.setState({ showDeleteConfirmModel: false })
        this.getUsersInGroup();
    }

    submit = () => {
        const { user } = this.state;
        if (!user || !user.id) return
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/AddUserToApplicationGroup`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({
                    userId: this.state.user.id,
                    applicationGroupId: this.state.group.id
                })
            },
            () => {
                this.getUsersInGroup();
                this.setState({ user: null })
            },
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    removeUser = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/RemoveUserFromGroup?userId=${id}&applicationGroupId=${this.state.group.id}&reason=${reason} `,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, this.closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, this.closeDeleteConfirmModel)
        )
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/GetApplicationGroup?id=${this.props.match.params.id}`,
            {},
            (result) => {
                const group = { ...result.data };
                this.setState({ group })
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
        this.getUsersInGroup();
    }

    getUsersInGroup() {
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/GetUsersFromGroup?applicationId=${this.props.match.params.id}&organizationId=${this.context.organizationId}`,
            {},
            (result) => {
                const users = result.data;
                this.setState({ users })
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    onUserChange = (user) => {
        this.setState({ user })
    }

    formActions = [
        {
            text: "Select",
            onClick: this.submit,
            className: "btn btn-primary submitButton",
            requirePermissions: [PermissionsConstant.ApplicationGroupAssignUser]
        },
        {
            text: "Cancel",
            onClick: () => this.props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    render() {
        const title = "Group Name";
        const { user, showDeleteConfirmModel, users, id } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}: {this.state.group.name}</h5>
            </div>
        );
        const { isLoading } = this.props;
        return (
            !isLoading && <React.Fragment>
                <ChangePageTitle pageTitle={title} />
                <div className="main-content">
                    <section className="page-content container-fluid">
                        <div className="row">
                            <div className="col">
                                <div className="card">
                                    {header}
                                    <div className="card-body p-b-20">
                                        <form action="#" className="form-horizontal">
                                            <div className="form-body">
                                                <div className="form-group row">
                                                    <label htmlFor="application" className="control-label col-md-3"> User </label>
                                                    <div className="col-md-5">
                                                        <UserSelector removeOptions={users.map(x => x.id)} onChange={this.onUserChange} value={user ? { value: user.id, label: user.name } : null} />
                                                    </div>
                                                </div>
                                                <div className="card-footer bg-light">
                                                    <FormActions actions={this.formActions} />
                                                </div>
                                            </div>
                                        </form>
                                        <br/>
                                        <DataTable
                                            noHeader={true}
                                            columns={this.columns}
                                            data={users}
                                            pagination={false}
                                            customStyles={CustomTableStyle}
                                            sortServer={false}
                                            striped={true}
                                            responsive={true}
                                         />
                                      

                                    </div>                     
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
                {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={this.removeUser} closeModel={this.closeDeleteConfirmModel} />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(AssignUserToGroup)