import React from 'react';
import FormInput from '../../../Components/FormInput';
import UploadFileModel from '../../../Components/UploadFileModel';
import ChangePageTitle from "../../../Components/ChangePageTitle";
import Popover from "../../../Components/Popover";

function Personalization(props) {
  const [state, setState] = React.useState({
    showImageModel: false,
    showLogoModel: false
  });

  const handleInputChange = props.handleInputChange;
  const onChooseLogo = (logoUri) => {
    handleInputChange("logoUri", logoUri);
    closeUploadLogoModel();
  }

  const closeUploadImageModel = () => {
    setState(state => ({ ...state, showImageModel: false }))
  }

  const onChooseImage = (backgroundImage) => {
    handleInputChange("backgroundImage", backgroundImage);
    closeUploadImageModel();
  }

  const closeUploadLogoModel = () => {
    setState(state => ({ ...state, showLogoModel: false }))
  }

  const { clientApi, errors, wasValidated } = props;
  const { showLogoModel, showImageModel } = state;
  return (
    <React.Fragment>
      <ChangePageTitle pageTitle="API Credential Personalization" />
      <div className="row">
        <div className="col">
          {props.header}
          <div className="card">
            <div className="card-body">
              <form
                noValidate
                action="#"
                className="form-horizontal"
              >
                <div className="form-body">
                  <div className="form-group row">
                    <label className="control-label col-md-3">
                      App Logo
                    </label>
                    <div className="col-md-8">
                      <button type="button" onClick={() => setState(state => ({ ...state, showLogoModel: !showLogoModel }))} className="btn btn-primary">
                        Upload Image
                      </button>
                      <Popover
                        id="tooltiphoverAppLogo"
                        content="Logo to display on the Ascentis SSO Login page."
                        placement="right"
                        maxWidth="350px"
                        iconClass="la la-question-circle la-2x"
                      />
                      {errors.logoUri && <span className="d-block invalid-feedback">{errors.logoUri}</span>}
                      <div className="divTopPad">
                        <img style={{ height: "40px" }} src={clientApi.logoUri && clientApi.logoUri.url} />
                      </div>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label className="control-label col-md-3">
                      Background image
                    </label>
                    <div className="col-md-8">
                      <button type="button" onClick={() => setState(state => ({ ...state, showImageModel: !showImageModel }))} className="btn btn-primary">
                        Upload Image
                      </button>
                      <Popover
                        id="tooltiphoverBackgroundImage"
                        content="Background image to display on the Ascentis SSO Login page."
                        placement="right"
                        maxWidth="350px"
                        iconClass="la la-question-circle la-2x"
                      />
                      {errors.backgroundImage && <span className="d-block invalid-feedback">{errors.backgroundImage}</span>}
                      <div className="divTopPad">
                        <img style={{ height: "40px" }} src={clientApi.backgroundImage && clientApi.backgroundImage.url} />
                      </div>
                    </div>
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="App Home Page"
                      value={clientApi.appHomePage}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      errors={errors.appHomePage}
                      name="appHomePage"
                      autoComplete="appHomePage"
                      type="text"
                      id="appHomePage"
                      error={errors.appHomePage}
                      wasValidated={wasValidated}
                    />
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="Privacy Policy Link"
                      value={clientApi.privacyPolicyLink}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      errors={errors.privacyPolicyLink}
                      name="privacyPolicyLink"
                      autoComplete="privacyPolicyLink"
                      type="text"
                      id="privacyPolicyLink"
                      error={errors.privacyPolicyLink}
                      wasValidated={wasValidated}
                    />
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="Terms of Service link"
                      value={clientApi.termOfServiceLink}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      errors={errors.termOfServiceLink}
                      name="termOfServiceLink"
                      autoComplete="termOfServiceLink"
                      type="text"
                      id="termOfServiceLink"
                      error={errors.termOfServiceLink}
                      wasValidated={wasValidated}
                    />
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      {showLogoModel && <UploadFileModel images={clientApi.logoUri && [clientApi.logoUri]} onChooseImage={onChooseLogo} closeModel={closeUploadLogoModel} />}
      {showImageModel && <UploadFileModel images={clientApi.backgroundImage && [clientApi.backgroundImage]} onChooseImage={onChooseImage} closeModel={closeUploadImageModel} />}
    </React.Fragment>
  );
}

Personalization.propTypes = {

};

export default Personalization;
