import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import PropTypes from 'prop-types';
import CreatableSelect from 'react-select/creatable';
import fetcher from '../utils/fetcher';
import { webserviceUrl } from '../utils/configuration';

function PermissionEditor(props) {
    const [state, setState] = useState({
        name: props.name ?? "",
        description: props.description ?? "",
        displayName: props.displayName ?? "",
        group: props.group ?? "",
        groupOptions: [],
        errors: {}
    });

    useEffect(() => {
        fetcher(`${webserviceUrl}/api/Permission/GetAllGroup`,
            { method: 'GET', },
            (result) => {
                const groupOptions = result.data.map(x => {
                    return {
                        value: x,
                        label: x
                    }
                })
                setState(state => ({ ...state, groupOptions }));
            }
        );
    }, [])

    const handleInputChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value }));
    }

    const handleGroupChange = (event) => {
        setState(state => ({ ...state, group: event.value }));
    }

    function validate() {
        const { name, group, displayName } = state;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Permission Name is a required field."
        }
        if (!group.trim()) {
            isValid = false
            errors.group = "Group is a required field."
        }
        if (!displayName.trim()) {
            isValid = false
            errors.displayName = "Display Name is a required field."
        }
        setState(state => ({ ...state, errors, wasValidated: true }));
        return isValid;
    }

    const onSubmit = () => {
        const {
            name,
            description,
            group,
            displayName
        } = state;
        if (!validate()) return;

        props.submit({
            name: name.trim(),
            description: description.trim(),
            group: group.trim(),
            displayName: displayName.trim()
        })
    }

    useEffect(() => {
        setState(state => ({ ...state, errors: props.errors }));
    }, [props.errors])

    const formActions = [
        {
            text: props.buttontype ?? "Update",
            onClick: onSubmit,
            className: "btn btn-primary submitButton"
        },
        {
            text: "Cancel",
            onClick: () => props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const {
        name,
        description,
        errors,
        group,
        groupOptions,
        wasValidated,
        displayName
    } = state;

    return (
        <React.Fragment>
            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                {props.header}
                                <div className="card-body p-b-20">
                                    <form action="#" className="form-horizontal">
                                        <div className="form-body">
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Permission Name"
                                                    value={name}
                                                    onChange={handleInputChange}
                                                    name="name"
                                                    autoComplete="permission-name"
                                                    type="text"
                                                    id="permission-name"
                                                    error={errors.name}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Display Name"
                                                    value={displayName}
                                                    onChange={handleInputChange}
                                                    name="displayName"
                                                    autoComplete="permission-display-name"
                                                    type="text"
                                                    id="permission-display-name"
                                                    error={errors.displayName}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Description"
                                                    onChange={handleInputChange}
                                                    name="description"
                                                    autoComplete="role-description"
                                                    type="textarea"
                                                    id="role-description"
                                                    rows="3"
                                                    value={description}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <label
                                                    htmlFor="scopes"
                                                    className="control-label col-md-3"
                                                >
                                                    Permission Group<span className="text-danger">*</span>
                                                </label>
                                                <div className="col-md-8">
                                                    <CreatableSelect
                                                        isClearable
                                                        onChange={handleGroupChange}
                                                        options={groupOptions}
                                                        value={{
                                                            value: group,
                                                            label: group
                                                        }}
                                                        className={`react-select-no-border form-control select2-form-control ${wasValidated
                                                            ? errors.group
                                                                ? 'is-invalid'
                                                                : 'is-valid'
                                                            : ''
                                                            }`}
                                                    />
                                                    {errors.group && (
                                                        <div className="invalid-feedback">
                                                            {errors.group}
                                                        </div>
                                                    )}
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                                <div className="card-footer bg-light">
                                    <FormActions actions={formActions} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </React.Fragment >
    )
}

PermissionEditor.propTypes = {
    name: PropTypes.string,
    description: PropTypes.string,
    group: PropTypes.string
}

export default withRouter(PermissionEditor)