import React, { useState, useContext, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../../../Components/FormInput';
import FormActions from '../../../Components/FormActions';
import { UserContext } from '../../../Context/UserContext';
import fetcher from '../../../utils/fetcher';
import { webserviceUrl } from '../../../utils/configuration';
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert'
import ChangePageTitle from "../../../Components/ChangePageTitle";

class ResetUserPassword extends React.Component {
    static contextType = UserContext;
    constructor(props) {
        super(props);
        this.state = {
            showDeleteConfirmModel: false,
            id: "",
            name: "",
            email: "",
            password: "",
            confirmPassword: "",
            remarks: "",
            errors: {}
        }
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/User/GetUser?userId=${this.props.match.params.id}&organizationId=${this.context.organizationId}`,
            {},
            (result) => {
                const { id, userFullName, email } = result.data;
                this.setState({ id, name: userFullName, email })
                this.props.setLoading(false)
            },
            () => {
                showErrorAlert(DefaultErrorMessage)
                this.props.setLoading(false)
            }
        )
    }
    submit = (data) => {
        if (!data.email) {
            fetcher(
                `${webserviceUrl}/api/User/ResetUserPassword`,
                {
                    method: 'POST',
                    cache: 'no-cache',
                    mode: 'cors',
                    body: JSON.stringify({ ...data, id: this.props.match.params.id }),
                },
                () => this.props.history.push('/users'),
                (ex) => {
                    const result = JSON.parse(ex.message);
                    if(!result.Data) 
                    { 
                        showErrorAlert(result.message) 
                    }
                    else {
                        this.setState({ errors: result.Data});
                    }
                }
            )
        }
        else {
            fetcher(
                `${webserviceUrl}/api/User/SendResetPassword?id=${data.id}`,
                {
                    method: 'GET',
                    cache: 'no-cache',
                    mode: 'cors',
                    headers: {
                        'content-type': 'application/json',
                    }
                },
                () => this.props.history.push('/users'),
            );
        }
    }

    validate = () => {
        const { email, password, confirmPassword } = this.state;
        const errors = {};
        let isValid = true;
        if (!email && !password.trim()) {
            isValid = false
            errors.password = "Password is a required field."
        }
        if (!email && !confirmPassword.trim()) {
            isValid = false
            errors.confirmPassword = "Confirm Password is a required field."
        }
        if (!email && password != confirmPassword) {
            isValid = false
            errors.confirmPassword = "Please input correct confirm password."
        }
        this.setState({ errors });
        return isValid;
    }

    render() {
        const { name, email, password, confirmPassword, remarks, errors } = this.state;

        const handleInputChange = (event) => {
            this.setState({ [event.target.name]: event.target.value });
        }

        const formActions = [
            {
                text: "Submit",
                onClick: () => {
                    if (!this.validate()) return;
                    const { id, name, email, password, confirmPassword } = this.state;
                    this.submit({
                        id,
                        userFullName: name.trim(),
                        email,
                        password: password,
                        confirmPassword: confirmPassword,
                        organizationId: this.context.organizationId,
                    })
                },
                className: "btn btn-primary submitButton"
            },
            {
                text: 'Cancel',
                onClick: () => this.props.history.goBack(),
                className: 'btn btn-secondary clear-form btn-outline',
            }
        ]

        return (
            <React.Fragment>
                <ChangePageTitle pageTitle="Reset Password" />
                <div className="main-content">
                    <section className="page-content container-fluid">
                        <div className="row">
                            <div className="col">
                                <div className="card">
                                    <div className="card-header card-header-flex">
                                        <h5 className="card-header-text">Reset Password: {name}</h5>
                                    </div>
                                    <div className="card-body">
                                        {email && <p>You can reset your user's password here. An instruction to reset password will be sent to your user registered email address.</p>}
                                        {!email && <div>
                                            <p>You can reset your user's password here. A temporary password will be generated so that you can inform the staff on the new password.</p>
                                            <p>The staff will be required to change password on their first login.</p>
                                        </div>}
                                        <form action="#" className="form-horizontal">
                                            <div className="form-body">
                                                <div className="form-group row">
                                                    <FormInput
                                                        label="Staff Name"
                                                        value={name}
                                                        name="name"
                                                        autoComplete="user-name"
                                                        type="text"
                                                        id="user-name"
                                                        disabled={true}
                                                    />
                                                </div>
                                                {email && <div className="form-group row">
                                                    <FormInput
                                                        label="Email Address"
                                                        value={email}
                                                        name="email"
                                                        autoComplete="email"
                                                        type="text"
                                                        id="email"
                                                        disabled={true}
                                                    />
                                                </div>}
                                                {!email && <div className="form-group row">
                                                    <FormInput
                                                        label="New Password"
                                                        value={password}
                                                        onChange={handleInputChange}
                                                        name="password"
                                                        autoComplete="password"
                                                        type="password"
                                                        id="password"
                                                        wasValidated={errors.password}
                                                        error={errors.password}
                                                        isRequired={true}
                                                    />
                                                </div>}
                                                {!email && <div className="form-group row">
                                                    <FormInput
                                                        label="Confirm Password"
                                                        onChange={handleInputChange}
                                                        name="confirmPassword"
                                                        autoComplete="confirmPassword"
                                                        type="password"
                                                        id="confirmPassword"
                                                        value={confirmPassword}
                                                        wasValidated={errors.confirmPassword}
                                                        error={errors.confirmPassword}
                                                        isRequired={true}
                                                    />
                                                </div>}
                                                <div className="form-group row">
                                                    <FormInput
                                                        label="Remarks"
                                                        onChange={handleInputChange}
                                                        name="remarks"
                                                        autoComplete="remarks"
                                                        type="textarea"
                                                        id="remarks"
                                                        rows="3"
                                                        value={remarks}
                                                    />
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                    <div className="card-footer bg-light">
                                        <FormActions actions={formActions} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </React.Fragment>
        )
    }
}



export default WithLoadingBackDrop(withRouter(ResetUserPassword))