import React, { useContext } from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration';
import swal from 'sweetalert';
import ReactDOM from 'react-dom';
import fetcher from '../../../utils/fetcher';
import { SuccessfullyDeleted } from '../../../utils/Constant';
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel';
import { UserContext } from '../../../Context/UserContext';
import Protected from '../../../Components/ProtectedComponent';
import PermissionsConstant from '../../../assets/permission.config.json';
import ChangePageTitle from "../../../Components/ChangePageTitle";

const copy = function (clientSecret, tooltipElement) {
    navigator.clipboard.writeText(clientSecret);
    tooltipElement.style.visibility = 'visible';
    setTimeout(function () {
        tooltipElement.style.visibility = 'hidden';
    }, 1000);
};

const secretKeyElement = (clientSecrets) => {
    let wrapper = document.createElement('div');

    const element = (
        <React.Fragment>
            <div className="swal-text mt-0 mb-0">{clientSecrets}
                <i style={{ fontSize: "25px", cursor: "pointer" }} className="la la-copy copy tooltip pl-3">
                    <span className="tooltiptext">Copy</span>
                    <span className="tooltiptext-copied">Copied</span>
                </i>
            </div>
        </React.Fragment>
    )
    ReactDOM.render(element, wrapper);
    const tooltipElement = wrapper.getElementsByClassName('tooltiptext-copied').item(0);
    wrapper.getElementsByClassName('copy').item(0).addEventListener('click', () => copy(clientSecrets, tooltipElement))
    return wrapper.firstChild
}

export default function ApiClientManagermentTable() {
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);
    const user = useContext(UserContext);
    const columns = [
        {
            name: 'Client App Name',
            selector: 'clientAppName',
            sortable: true,
            maxWidth: "15%"
        },
        {
            name: 'Client Id',
            selector: 'clientId',
            sortable: true,
            maxWidth: "15%"
        },
        {
            name: 'Client Secret',
            selector: 'clientSecrets',
            sortable: false,
            maxWidth: "20%",
            cell: row => (
                <div>
                    <span>*******************************&nbsp;</span>
                    <i className="ml-auto la la-key tooltipTop" onClick={() => {
                        swal({
                            title: "Client Secret",
                            content: {
                                element: secretKeyElement(row.clientSecrets)
                            },
                            closeOnClickOutside: false,
                            button: {
                                className: "btn btn-primary"
                            }
                        })
                    }}>
                        <span className="tooltiptextTop">Show Client Secret</span>
                    </i>
                </div>
            )
        },
        {
            name: 'Scope',
            selector: 'allowedScopes',
            sortable: false,
            maxWidth: "25%",
            cell: row => (
                <div>{row.allowedScopes}
                </div>
            )
        },
        {
            name: 'Creation Date',
            selector: 'createdDate',
            sortable: true,
            maxWidth: "15%",
            cell: row => (
                <div>{row.createdDate}</div>
            )
        },
        {
            name: 'Action',
            sortable: false,
            maxWidth: "10%",
            cell: row => (
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.APICredentialModify]}>
                        {row.isComplete && <Link to={`/apiClients/oauthClientId/edit/${row.id}`} >
                            <div><i className="la la-exclamation-triangle tooltipTop icon-big"><span className="tooltiptextTop">Incomplete</span></i></div>
                        </Link>}
                        <Link to={`/apiClients/oauthClientId/edit/${row.id}`} >
                            <div><i className="la la-edit tooltipTop icon-big"><span className="tooltiptextTop">Edit Credential</span></i></div>
                        </Link>
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.APICredentialDelete]}>
                        <div><i onClick={() => { setShowDeleteConfirmModel(true), setId(row.id) }} className="la la-trash hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Delete Credential</span></i></div>
                    </Protected>
                </React.Fragment>
            )
        }
    ];

    const title = "Manage API Credential";

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/ApiClient/GetApiClients?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`)
        }
        if ((sortBy).length == 0) {
            apiUrl = "".concat(apiUrl, `&sortBy=clientId`);
            sortDirection = "asc"
        }
        apiUrl = "".concat(apiUrl, `&organizationId=${user.organizationId}`)
        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteClient = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/ApiClient/DeleteApiClient?id=${id}&reason=${reason}`,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel)
        )
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.APICredentialModify]}>
            <Link
                to={"/apiClients/oauthClientId/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                Create New
            </Link>
        </Protected>
    )

    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.APICredentialModify]}>
            <Link to={"/apiClients/oauthClientId/create"} className="dropdown-item">
                Create New
            </Link>
        </Protected>
    )


    return (
        <React.Fragment>
            <ChangePageTitle pageTitle={title} />
            <ManagermentTable shouldReload={shouldReload} setShouldReload={setshouldReload} columns={columns} title={title} apiUrl={apiUrl} createButton={createButton} createButtonMobile={createButtonMobile} searchType="Client Id or Scope" />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteClient} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}
