import React, {setState} from 'react';
import PropTypes from 'prop-types';
import { webserviceUrl } from '../../../utils/configuration';
import UploadFileModel from '../../../Components/UploadFileModel';
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import { withRouter } from "react-router-dom";
import { showErrorAlert } from '../../../Components/Alert';
import fetcher from '../../../utils/fetcher';
import { UserContext } from '../../../Context/UserContext';
import ProtectedComponent from '../../../Components//ProtectedComponent';
import PermissionsConstant from '../../../assets/permission.config.json';
import ChangePageTitle from "../../../Components/ChangePageTitle";
import imgplaceholder from '..//../../assets/img/preview-place-holder.png';

class Personalization extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showLogoModel: false,
            dimensions: { width: 200, height: 200}
        }
    }
    
    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/Organization/GetOrganization?organizationID=${this.props.match.params.id}`,
            {},
            (result) => {
                const { logo } = result.data;
                this.setState({logo})
                this.props.setLoading(false)
            },
            () => this.props.setLoading(false)
        )
    }

    closeUploadLogoModel = () => {
        this.setState({showLogoModel: false })
    }

    showUpload = () => {
        this.setState({showLogoModel: true})
    }

    onChooseLogo = (logo) => {
        fetcher(
            `${webserviceUrl}/api/Organization/UpdateOrganizationLogo`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ logo, id: this.props.match.params.id })
            },
            () => { this.setState({ showLogoModel: false, logo }) },
            (ex) => showErrorAlert(JSON.parse(ex.message).message)
        )
    }

    render() {
        const {logo, isLoading, showLogoModel} = this.state;
        const user = UserContext;
        return (
            <React.Fragment>
                <ChangePageTitle pageTitle="Update Organization Personalization" />
                        {!isLoading && <div className="row">
                            <div className="col">
                                <div className="card">
                                    <div className="card-body p-b-20">
                                        <form action="#" className="form-horizontal">
                                            <div className="form-body">
                                                <div className="form-group row">
                                                    <div className="col-md-3"/>
                                                    <div className="col-md-6 text-center">
                                                        <img style={{ height: "150px" }} src={logo?.url ? `${logo && logo?.url}` : imgplaceholder } />
                                                        <p>Picture type: JPG or PNG</p>
                                                        <p>Resolution: 200 px x 200 px</p>
                                                        <ProtectedComponent requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                                                            <button type="button" onClick={this.showUpload} className="btn btn-primary">
                                                                Upload
                                                            </button>
                                                        </ProtectedComponent>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                        }
                {showLogoModel && <UploadFileModel images={logo && [logo]} dimensions={this.state.dimensions} onChooseImage={this.onChooseLogo} closeModel={this.closeUploadLogoModel} />}
            </React.Fragment>
        )
    }
}

Personalization.propTypes = {
    logo: PropTypes.string
}

export default WithLoadingBackDrop(withRouter(Personalization))