import React from 'react';
import Pagination from '@material-ui/lab/Pagination';
import PaginationItem from '@material-ui/lab/PaginationItem';
import PropTypes from 'prop-types';


function TablePaginationActions({ count, currentPage, rowsPerPage, onChangePage }) {
    const numberOfPages = Math.ceil(count / rowsPerPage);
    const from = (currentPage - 1) * rowsPerPage + 1;
    const to = currentPage == numberOfPages ? count : currentPage * rowsPerPage;
    return (
        <div className="row">
            <div className="col-md-6">
                <div
                    className="dataTables_info"
                    id="recent-transaction-table_info"
                    role="status"
                    aria-live="polite"
                >
                   <label>{`Showing ${from} to ${to} of ${count} entries`}</label>
                </div>
            </div>
            <div className="col-md-6">
                <Pagination
                    count={numberOfPages}
                    color="secondary"
                    renderItem={(item) => <PaginationItem disableRipple={true} {...item} />}
                    onChange={onChangePage}
                    page={currentPage}
                />
            </div>
        </div>
    );
}

function CustomMaterialPagination({ rowsPerPage, rowCount, onChangePage, onChangeRowsPerPage, currentPage }) {
    return (
        <TablePaginationActions
            component="nav"
            count={rowCount}
            rowsPerPage={rowsPerPage}
            currentPage={currentPage}
            onChangePage={(evt, page) => { onChangePage(page) }}
            onChangeRowsPerPage={({ target }) => onChangeRowsPerPage(Number(target.value))}
        />
    );
}

CustomMaterialPagination.propTypes = {
    rowsPerPage: PropTypes.number.isRequired,
    rowCount: PropTypes.number.isRequired,
    onChangePage: PropTypes.func.isRequired,
    onChangeRowsPerPage: PropTypes.func.isRequired,
    currentPage: PropTypes.number.isRequired
}

export default CustomMaterialPagination;