import React, { useContext } from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import fetcher from '../../../utils/fetcher';
import { SuccessfullyDeleted } from '../../../utils/Constant'
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel'
import Protected from '../../../Components/ProtectedComponent'
import PermissionsConstant from '../../../assets/permission.config.json'
import { UserContext } from '../../../Context/UserContext';
import ChangePageTitle from "../../../Components/ChangePageTitle";

export default function PermissionsManagermentTable() {
    const userContext = useContext(UserContext);
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);
    const title = "Manage API Scopes";
    const columns = [
        {
            name: 'App Name',
            selector: 'appName',
            sortable: true,
            maxWidth: "25%"
        },
        {
            name: 'Scope Name',
            selector: 'name',
            sortable: true,
            maxWidth: "45%"
        },
        {
            name: 'Display Name',
            selector: 'displayName',
            maxWidth: "25%"
        },
        {
            name: 'Description',
            selector: 'description',
            maxWidth: "25%"
        },
        {
            name: 'Enabled',
            selector: 'status',
            maxWidth: "5%"
        },    
        {
            name: 'Actions',
            selector: 'edit',
            sortable: false,
            maxWidth: "10%",
            cell: row => (
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.ScopeModify]}>
                        <Link to={`/AppSubscription/apiscopes/edit/${row.id}`}><i className="la la-edit tooltipTop icon-big"><span className="tooltiptextTop">Edit Scope</span></i></Link>
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.ScopeDelete]}>
                        <div><i onClick={() => { setShowDeleteConfirmModel(true), setId(row.id) }} className="la la-trash hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Delete Scope</span></i></div>
                    </Protected>
                </React.Fragment>
            )
        },
    ];

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/scope/GetScopes?page=${page}&pageSize=${pageSize}&organizationId=${userContext.organizationId}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`)
        }
        if ((sortBy).length == 0)
        {
            apiUrl = "".concat(apiUrl, `&sortBy=name`);
            sortDirection = "asc"
        }

        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteScope = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/scope/DeleteSCope?id=${id}&reason=${reason}`,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel)
        )
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.ScopeModify]}>
            <Link
                to={"/AppSubscription/apiscopes/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                Create New
            </Link>
        </Protected>
    )

    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.ScopeModify]}>
            <Link to={"/AppSubscription/apiscopes/create"} className="dropdown-item">
                Create New
            </Link>
        </Protected>
    )

    return (
        <React.Fragment>
            <ChangePageTitle pageTitle="Manage API Scopes" />
            <ManagermentTable
                shouldReload={shouldReload}
                setShouldReload={setshouldReload}
                columns={columns}
                title={title}
                apiUrl={apiUrl}
                createButton={createButton}
                createButtonMobile={createButtonMobile}
                searchType="Scope Name"
            />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteScope} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}