import React from 'react';
import PropTypes from 'prop-types';

function TableHeader(props) {
    return (
        <div className="table-search-wrapper">
            <div>
                <label className="control-label text-right table-search-label">Show</label>
                <select
                    onChange={(e) => props.onChangePageSize(e.target.value)}
                    value={props.pageSize}
                    name="recent-transaction-table_length"
                    aria-controls="recent-transaction-table"
                    className="table-search-input form-control form-control-sm"
                >
                    <option value="5">5</option>
                    <option value="10">10</option>
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                </select>
                <label className="control-label text-right table-search-label result-per-page">results per page</label>
            </div>
        </div>
    )
}

TableHeader.propTypes = {
    onChangePageSize: PropTypes.func.isRequired,
    pageSize: PropTypes.number.isRequired,
}

export default TableHeader