import React, { useContext, useState } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../../Components/FormInput';
import FormActions from '../../Components/FormActions';
import fetcher from '../../utils/fetcher';
import { webserviceUrl } from '../../utils/configuration';
import { UserContext } from '../../Context/UserContext';
import ChangePageTitle from "../../Components/ChangePageTitle";

function ChangePassword(props) {
  const userContext = useContext(UserContext);
  const [state, setState] = useState({
    currentPassword: props.currentPassword ?? '',
    newPassword: props.newPassword ?? '',
    confirmNewPassword: props.confirmNewPassword ?? '',
    wasValidated: false,
    errors: {}
  });

  const handleInputChange = (event) => {
    setState(state => ({ ...state, [event.target.name]: event.target.value }));
  };

  function validate() {
    const { currentPassword, newPassword, confirmNewPassword } = state;
    const errors = {};
    let isValid = true;
    if (!currentPassword) {
      isValid = false
      errors.currentPassword = "Current Password is a required field."
    }
    if (!newPassword) {
      isValid = false
      errors.newPassword = "New Password is a required field."
    }
    if (state.newPassword != state.confirmNewPassword) {
      isValid = false
      errors.confirmNewPassword = "Please input the correct confirm password!";
    }
    if (!confirmNewPassword) {
      isValid = false
      errors.confirmNewPassword = "Confirm New Password is not valid."
    }
    if (isValid) {
      errors.currentPassword = undefined;
      errors.newPassword = undefined;
      errors.confirmNewPassword = undefined;
    }
    setState(state => ({ ...state, errors, wasValidated: true }));
    return isValid;
  }

  const submit = (data) => {
    //TODO move url to setting or constant
    const { errors } = state;
    fetcher(
      `${webserviceUrl}/api/User/ChangePassword`,
      {
        method: 'POST',
        cache: 'no-cache',
        mode: 'cors',
        headers: {
          'content-type': 'application/json',
        },
        body: JSON.stringify({ ...data, id: userContext.id }),
      },
      () => swal({
        title: 'Change Password Successful',
        icon: 'success',
        closeOnClickOutside: false,
        button: {
          className: 'btn btn-primary',
        },
      }).then(() => {
        props.history.goBack();
      }),
      (ex) => {
        const result = JSON.parse(ex.message);
        if (result.message.includes('old')) {
          errors.currentPassword = result.message;
        } else {
          errors.newPassword = result.message;
        }
        if (result.data) {
          errors = result.data
        }
        setState(state => ({ ...state, errors, wasValidated: true }));
      }
    );
  };

  const formActions = [
    {
      text: 'Update',
      onClick: () => {
        if (!validate()) return;
        submit({ ...state });
      },
      className: "btn btn-primary submitButton"
    },
    {
      text: 'Cancel',
      onClick: () => props.history.goBack(),
      className: 'btn btn-secondary clear-form btn-outline',
    },
  ];

  const { currentPassword, newPassword, confirmNewPassword, errors, wasValidated } = state;
  const titles = "Change Password"
  return (
    <React.Fragment>
      <ChangePageTitle pageTitle={titles} />
      <div className="main-content">
        <section className="page-content container-fluid">
          <div className="row">
            <div className="col">
              <div className="card">
                {props.header}
                <div className="card-header card-header-flex">
                  <h5 className="card-header-text">{titles}</h5>
                </div>
                <div className="card-body">
                  <form action="#" className="form-horizontal">
                    <div className="form-body">
                      <div className="form-group row">
                        <FormInput
                          label="Current Password"
                          value={currentPassword}
                          onChange={handleInputChange}
                          name="currentPassword"
                          type="password"
                          id="currentPassword"
                          isRequired={true}
                          error={errors.currentPassword}
                          wasValidated={wasValidated}
                        />
                      </div>
                      <div className="form-group row">
                        <FormInput
                          label="New Password"
                          value={newPassword}
                          onChange={handleInputChange}
                          name="newPassword"
                          type="password"
                          id="newPassword"
                          isRequired={true}
                          error={errors.newPassword}
                          wasValidated={wasValidated}
                        />
                      </div>
                      <div className="form-group row">
                        <FormInput
                          label="Confirm New Password"
                          value={confirmNewPassword}
                          onChange={handleInputChange}
                          name="confirmNewPassword"
                          type="password"
                          id="confirmNewPassword"
                          isRequired={true}
                          error={errors.confirmNewPassword}
                          wasValidated={wasValidated}
                        />
                      </div>
                    </div>
                  </form>
                </div>
                <div className="card-footer bg-light">
                  <FormActions actions={formActions} />
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </React.Fragment>
  );
}

export default withRouter(ChangePassword);
