import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import Datetime from 'react-datetime';
import { CKEditor } from '@ckeditor/ckeditor5-react';
import CustomEditor from './ckeditor/ckeditor';
import CkeditorConfig from '../utils/CkeditorConfig';

function TermsAndConditionsEditor(props) {
    const [state, setState] = useState({
        termAndConditions: props.termAndConditions ?? {
            type: "Terms and Conditions",
            version: "",
            content: "",
            enabled: "",
            publishOn: ""
        },
        wasValidated: false,
        errors: {}
    });

    const handleInputChange = (name, value) => {
        let { termAndConditions } = state;
        termAndConditions = { ...termAndConditions, [name]: value }
        setState(state => ({ ...state, termAndConditions }));
    }

    function validate() {
        const errors = {};
        const { type, version, content } = state.termAndConditions;
        let isValid = true;
        if (!type.trim()) {
            isValid = false
            errors.type = "Type is a required field."
        }

        if (!version.trim()) {
            isValid = false
            errors.version = "Version is a required field."
        }

        if (!content) {
            isValid = false
            errors.content = "Content is a required field."
        }
        setState(state => ({ ...state, errors, wasValidated: true }));
        return isValid;
    }

    const onSubmit = () => {
        const {
            termAndConditions
        } = state;
        if (!validate()) return;

        props.submit(termAndConditions)
    }

    const formActions = [
        {
            text: "Submit",
            onClick: onSubmit,
            className: "btn btn-primary submitButton"
        },
        {
            text: "Cancel",
            onClick: () => props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const typeOptions = [
        {
            name: "Terms and Conditions",
            value: "Terms and Conditions"
        },
        {
            name: "Privacy Policy",
            value: "Privacy Policy"
        },
        {
            name: "Cookies",
            value: "Cookies"
        },
    ]

    const editorConfig = {
        toolbar: ['underline']
    }

    const {
        termAndConditions,
        wasValidated,
        errors
    } = state;

    return (
        <React.Fragment>
            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                {props.header}
                                <div className="card-body p-b-20">
                                    <form action="#" className="form-horizontal">
                                        <div className="form-body">
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Type"
                                                    onChange={handleInputChange}
                                                    name="type"
                                                    type="select"
                                                    id="type"
                                                    value={termAndConditions.type}
                                                    options={typeOptions}
                                                    error={errors.type}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Version"
                                                    value={termAndConditions.version}
                                                    onChange={(e) => handleInputChange(e.target.name, e.target.value)}
                                                    name="version"
                                                    autoComplete="scope-name"
                                                    type="text"
                                                    id="scope-name"
                                                    error={errors.version}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <label className="control-label col-md-3">Content<span className="text-danger">*</span></label>
                                                <div className="col-md-5">
                                                    <CKEditor
                                                        config={CkeditorConfig}
                                                        editor={CustomEditor}
                                                        data={termAndConditions.content}
                                                        onChange={(event, editor) => handleInputChange("content", editor.getData())}
                                                    />
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="control-label col-md-3">Enabled</label>
                                                <div className="col-md-5">
                                                    <input type="checkbox" id="preview"
                                                        name="enabled"
                                                        checked={termAndConditions.enabled}
                                                        onChange={(e) => handleInputChange("enabled", e.target.checked)} />
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <label className="control-label col-md-3">Publish on<span className="text-danger">*</span></label>
                                                <div className="col-md-5">
                                                    <Datetime
                                                        id="publishOn"
                                                        value={termAndConditions.publishOn}
                                                        onChange={(value) => handleInputChange("publishOn", value)}
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                                <div className="card-footer bg-light">
                                    <FormActions actions={formActions} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </React.Fragment >
    )
}

export default withRouter(TermsAndConditionsEditor)