import React from "react";
import OrganizationEditor from "../../../Components/OrganizationEditor"
import fetcher from '../../../utils/fetcher'
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert } from '../../../Components/Alert'
import ChangePageTitle from "../../../Components/ChangePageTitle";

class CreateOrganization extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
          errors: {}
      }
    }
    submit = async data => {
        const body = JSON.stringify(data);
        fetcher(
            `${webserviceUrl}/api/Organization/CreateOrganization`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: body
            },
            () => { this.props.history.push('/organizations') },
            (ex) =>
            {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }
    
    render() {
        const {errors} = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">Create Organization</h5>
            </div>
        );

        return (
            <div className="main-content">
                <section className="page-content container-fluid">
            <React.Fragment>
                <ChangePageTitle pageTitle="Create Organizations" />
                <OrganizationEditor errors={errors} header={header} submit={this.submit} buttontype="Create" />
            </React.Fragment>
                </section>
            </div>
        )
    }
}

export default CreateOrganization