import React, { useState } from 'react'
import Backdrop from '@material-ui/core/Backdrop'
import { makeStyles } from '@material-ui/core/styles'
import CircularProgress from '@material-ui/core/CircularProgress'
import { useEffect } from 'react'

const useStyles = makeStyles((theme) => ({
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));

function WithLoadingBackDrop(WrappedComponent) {
    return (props) => {
        const classes = useStyles();
        let mounted = true;
        useEffect(() => {
            mounted = true;
            return () => mounted = false;
        }, [])
        const [isLoading, setLoading] = useState(true);
        const changeLoading = (isLoading) => {
            if(!mounted) return;
            setLoading(isLoading)
        }
        const backDropComponent = (
            <Backdrop className={classes.backdrop} open={true}>
                <CircularProgress color="inherit" />
            </Backdrop>
        )

        return (
            <React.Fragment>
                {isLoading && backDropComponent}
                <WrappedComponent {...props} isLoading={isLoading} setLoading={(isLoading) => changeLoading(isLoading)} />
            </React.Fragment>
        )
    }
}

export default WithLoadingBackDrop