import React, { useState, useContext, useEffect } from 'react';
import Creatable from 'react-select/creatable';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import PropTypes from 'prop-types';
import { UserContext } from '../Context/UserContext';
import ProtectedComponent from './ProtectedComponent';
import PermissionsConstant from '../assets/permission.config.json'
import Popover from '../Components/Popover';


function OrganizationEditor(props) {
    const [state, setState] = useState({
        organizationCode: props.organizationCode ?? "",
        loginType: props.loginType ?? "",
        enableTwoFactorAuthentication: props.enableTwoFactorAuthentication ?? false,
        twoFactorAuthenticationType: props.twoFactorAuthenticationType ?? "",
        whiteListEmailDomain: props.whiteListEmailDomain ?? "",
        noOfInvalidOtpAttempts: props.noOfInvalidOtpAttempts ?? "",
        enablePasswordPolicy: props.enablePasswordPolicy ?? false,
        passwordMinLength: props.passwordMinLength ?? "",
        passwordMaxLength: props.passwordMaxLength ?? "",
        passwordRequireLowerCase: props.passwordRequireLowerCase ?? false,
        passwordRequireUpperCase: props.passwordRequireUpperCase ?? false,
        passwordRequireNumericCase: props.passwordRequireNumericCase ?? false,
        passwordRequireSpecialCase: props.passwordRequireSpecialCase ?? false,
        passwordMatchEmailCase: props.passwordMatchEmailCase ?? false,
        invalidRetires: props.invalidRetires ?? "",
        passwordExpiry: props.passwordExpiry ?? "",
        passwordHistory: props.passwordHistory ?? "",
        accountLockDuration: props.accountLockDuration ?? "",
        idleTimeOut: props.idleTimeOut ?? "",
        allowConcurrentSession: props.allowConcurrentSession ?? false,
        enableRecaptcha: props.enableRecaptcha ?? false,
        errors: props.errors ?? {}
    });

    const user = useContext(UserContext);
    const handleInputChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value }));
        props.updateOrganization(true);
    }

    const handleCheckboxChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.checked }));
        props.updateOrganization(true);
    }

    const handleLoginTypeChange = (login) => {
        const loginType = state.loginType;
        if (loginType == login) {
            login = ""
        }
        setState(state => ({ ...state, loginType: login }));
        props.updateOrganization(true);
    }

    const handleNumberChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value?.length > 2 ? event.target.value.substring(0, 2) : event.target.value }));
        props.updateOrganization(true);
    }
    const onKeyDown = (event) => {
        if (event.target.value?.length > 1 && isFinite(event.key) || event.key == '+' || event.key == '-') event.preventDefault();
    }

    useEffect(() => {
        setState(state => ({ ...state, errors: props.errors }));
    }, [props.errors])

    const onSubmit = () => {
        const {
            loginType,
            enableTwoFactorAuthentication,
            twoFactorAuthenticationType,
            whiteListEmailDomain,
            noOfInvalidOtpAttempts,
            enablePasswordPolicy,
            passwordMinLength,
            passwordMaxLength,
            passwordRequireLowerCase,
            passwordRequireUpperCase,
            passwordRequireNumericCase,
            passwordRequireSpecialCase,
            passwordMatchEmailCase,
            invalidRetires,
            passwordExpiry,
            passwordHistory,
            accountLockDuration,
            idleTimeOut,
            allowConcurrentSession,
            enableRecaptcha
        } = state;
        props.submit({
            loginType,
            enableTwoFactorAuthentication,
            twoFactorAuthenticationType,
            whiteListEmailDomain: whiteListEmailDomain ? whiteListEmailDomain.map(x => x.value).join(", ") : '',
            noOfInvalidOtpAttempts,
            enablePasswordPolicy,
            passwordMinLength,
            passwordMaxLength,
            passwordRequireLowerCase,
            passwordRequireUpperCase,
            passwordRequireNumericCase,
            passwordRequireSpecialCase,
            passwordMatchEmailCase,
            invalidRetires,
            passwordExpiry,
            passwordHistory,
            accountLockDuration,
            idleTimeOut,
            allowConcurrentSession,
            enableRecaptcha
        })
    }

    const handleWhiteListChange = (event) => {
        setState(state => ({ ...state, whiteListEmailDomain: event }));
    }

    const twoFactorAuthenticationTypeOptions = [
        {
            name: "None",
            value: ""
        },
        {
            name: "Email OTP",
            value: "Email"
        }
        // {
        //     name: "Timebased OTP",
        //     value: "TOTP"
        // },      
        // {
        //     name: "SMS OTP",
        //     value: "SMS",
        //     disable: true
        // }
    ]

    const formActions = [
        {
            text: "Update",
            onClick: onSubmit,
            className: "btn btn-primary submitButton"
        },
        {
            text: "Cancel",
            onClick: () => props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const inputClasses3 = {
        labelClass: "control-label col-md-3",
        colWidthClass: "col-sm-2",
        inputClass: "form-control"
    }

    const inputClasses4 = {
        colWidthClass: "col-sm-2",
        inputClass: "form-control"
    }

    const inputClasses5 = {
        labelClass: "control-label col-md-12",
        colWidthClass: "col-md-12",
        inputClass: "form-control"
    }

    const {
        organizationCode,
        loginType,
        enableTwoFactorAuthentication,
        twoFactorAuthenticationType,
        whiteListEmailDomain,
        noOfInvalidOtpAttempts,
        enablePasswordPolicy,
        passwordMinLength,
        passwordMaxLength,
        passwordRequireLowerCase,
        passwordRequireUpperCase,
        passwordRequireNumericCase,
        passwordRequireSpecialCase,
        passwordMatchEmailCase,
        invalidRetires,
        passwordExpiry,
        passwordHistory,
        accountLockDuration,
        idleTimeOut,
        allowConcurrentSession,
        enableRecaptcha,
        errors,
    } = state;
    return (
        <React.Fragment>
            <div className="row">
                <div className="col">
                    <ProtectedComponent requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                        <div className="card">
                            <div className="card-body p-b-20">
                                <form action="#" className="form-horizontal">
                                    <div className="form-body">
                                        <div className="form-group row">
                                            <div className="col-md-3"></div>
                                            <div className="col-md-12">
                                                <label className={inputClasses5.labelClass}>These policies governs how your users should create their account and password, as well as login.</label>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="form-body">
                                        <div className="form-group row">
                                            <label className="col-md-3">Login using</label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="loginTypeEmail" name="loginTypeEmail"
                                                    checked={loginType == "Email"} type="checkbox" onChange={() => handleLoginTypeChange("Email")} />
                                                <label className="tgl-btn" htmlFor="loginTypeEmail"></label>
                                            </div><label className="padding-mobile-spaces1">email address</label>
                                        </div>
                                        <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="loginTypeMobile" name="loginTypeMobile"
                                                    checked={loginType == "Mobile"} type="checkbox" onChange={() => handleLoginTypeChange("Mobile")} />
                                                <label className="tgl-btn" htmlFor="loginTypeMobile"></label>
                                            </div><label className="padding-mobile-spaces1">mobile Number</label>
                                        </div>
                                        <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="loginTypeCode" name="loginTypeCode"
                                                    checked={loginType == "Code"} type="checkbox" onChange={() => handleLoginTypeChange("Code")} />
                                                <label className="tgl-btn" htmlFor="loginTypeCode"></label>
                                            </div><label className="padding-mobile-spaces1">staff id, with organization code: {organizationCode}. <br />e.g. {organizationCode}\staffId</label>
                                        </div>
                                        {errors.loginType && (
                                            <div className="form-group row">
                                                <div className="control-label col-md-3"></div>
                                                <div className="col-sm-2 error-upload-image">{errors.loginType}</div>
                                            </div>)}
                                        <hr className="solid"></hr>
                                        <div className="form-group row">
                                            <label htmlFor="whiteListEmailDomain" className={inputClasses3.labelClass}>White List Email Domain</label>
                                            <div className={inputClasses3.colWidthClass}>
                                                <Creatable
                                                    onChange={handleWhiteListChange}
                                                    isMulti
                                                    value={whiteListEmailDomain}
                                                    className={`react-select-no-border form-control select2-form-control ${errors.whiteListEmailDomain ? errors.whiteListEmailDomain
                                                        ? 'is-invalid' : 'is-valid' : ''}`} />
                                                {errors.whiteListEmailDomain && (<div className="invalid-feedback">{errors.whiteListEmailDomain}</div>)}
                                            </div>
                                            <Popover
                                                id="tooltiphoverWhiteListChange"
                                                content="<center>Enable this if you want to enforce that all your new users must be from this email domain. The existing users will not be validated against this policy. You should review the list of your users.<center>"
                                                placement="right"
                                                maxWidth="350px"
                                                iconClass="la la-question-circle la-2x"
                                            />
                                        </div>
                                        <hr className="solid"></hr>
                                        <div className="form-group row">
                                            <label className="col-md-3">Two Factor Authentication</label>
                                            <div className="col-md-5">
                                                <input className="tgl tgl-light tgl-primary" id="enableTwoFactorAuthentication" name="enableTwoFactorAuthentication"
                                                    checked={enableTwoFactorAuthentication} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="enableTwoFactorAuthentication"></label>
                                            </div>
                                        </div>
                                        {enableTwoFactorAuthentication && <div className="form-group row">
                                            <FormInput
                                                label="using"
                                                onChange={handleInputChange}
                                                classes={inputClasses3}
                                                name="twoFactorAuthenticationType"
                                                type="select"
                                                id="twoFactorAuthenticationType"
                                                value={twoFactorAuthenticationType}
                                                options={twoFactorAuthenticationTypeOptions}
                                                error={errors.twoFactorAuthenticationType}
                                                wasValidated={errors.twoFactorAuthenticationType}
                                            />
                                        </div>}
                                        {enableTwoFactorAuthentication && <div className="form-group row">
                                            <FormInput
                                                label="allow"
                                                value={noOfInvalidOtpAttempts}
                                                classes={inputClasses3}
                                                onChange={handleInputChange}
                                                name="noOfInvalidOtpAttempts"
                                                autoComplete="noOfInvalidOtpAttempts"
                                                onKeyDown={onKeyDown}
                                                type="number"
                                                error={errors.noOfInvalidOtpAttempts}
                                                wasValidated={errors.noOfInvalidOtpAttempts}
                                                id="noOfInvalidOtpAttempts"
                                            /><label className="padding-mobile-spaces2">invalid attempts</label>
                                        </div>}
                                        <hr className="solid"></hr>
                                        <div className="form-group row">
                                            <label className="col-md-3">Password</label>
                                            <div className="col-md-5">
                                                <input className="tgl tgl-light tgl-primary" id="enablePasswordPolicy" name="enablePasswordPolicy"
                                                    checked={enablePasswordPolicy} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="enablePasswordPolicy"></label>
                                            </div>
                                        </div>
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <FormInput
                                                label="length between "
                                                value={passwordMinLength}
                                                classes={inputClasses3}
                                                onChange={handleNumberChange}
                                                onKeyDown={onKeyDown}
                                                name="passwordMinLength"
                                                autoComplete="passwordMinLength"
                                                type="number"
                                                id="passwordMinLength"
                                                error={errors.passwordMinLength}
                                                wasValidated={errors.passwordMinLength}
                                                maxLength="2"
                                                min={8}
                                                max={80}
                                            /><label className="padding-mobile-spaces2">and</label>
                                            <FormInput
                                                label=""
                                                value={passwordMaxLength}
                                                classes={inputClasses4}
                                                onChange={handleNumberChange}
                                                name="passwordMaxLength"
                                                autoComplete="passwordMaxLength"
                                                type="number"
                                                onKeyDown={onKeyDown}
                                                id="passwordMaxLength"
                                                error={errors.passwordMaxLength}
                                                wasValidated={errors.passwordMaxLength}
                                                min={8}
                                                max={80}
                                                maxLength="2"
                                            />
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="passwordRequireLowerCase" name="passwordRequireLowerCase"
                                                    checked={passwordRequireLowerCase} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="passwordRequireLowerCase"></label>
                                            </div><label className="padding-mobile-spaces1">include Lowercase</label>
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="passwordRequireUpperCase" name="passwordRequireUpperCase"
                                                    checked={passwordRequireUpperCase} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="passwordRequireUpperCase"></label>
                                            </div><label className="padding-mobile-spaces1">include Uppercase</label>
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="passwordRequireNumericCase" name="passwordRequireNumericCase"
                                                    checked={passwordRequireNumericCase} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="passwordRequireNumericCase"></label>
                                            </div><label className="padding-mobile-spaces1">include Numeric</label>
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="passwordRequireSpecialCase" name="passwordRequireSpecialCase"
                                                    checked={passwordRequireSpecialCase} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="passwordRequireSpecialCase"></label>
                                            </div><label className="padding-mobile-spaces1">include Specials</label>
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <FormInput
                                                label="allow"
                                                value={invalidRetires}
                                                classes={inputClasses3}
                                                onChange={handleNumberChange}
                                                name="invalidRetires"
                                                autoComplete="invalidRetires"
                                                onKeyDown={onKeyDown}
                                                type="number"
                                                error={errors.invalidRetires}
                                                wasValidated={errors.invalidRetires}
                                                id="invalidRetires"
                                            /><label className="padding-mobile-spaces2">invalid attempts</label>
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <FormInput
                                                label="expries in"
                                                value={passwordExpiry}
                                                classes={inputClasses3}
                                                onChange={handleNumberChange}
                                                name="passwordExpiry"
                                                autoComplete="passwordExpiry"
                                                onKeyDown={onKeyDown}
                                                error={errors.passwordExpiry}
                                                wasValidated={errors.passwordExpiry}
                                                type="number"
                                                id="passwordExpiry"
                                            /><label className="padding-mobile-spaces2">day(s)</label>
                                        </div>}
                                        {enablePasswordPolicy && <div className="form-group row">
                                            <FormInput
                                                label="can't reuse the last"
                                                value={passwordHistory}
                                                classes={inputClasses3}
                                                onChange={handleNumberChange}
                                                name="passwordHistory"
                                                autoComplete="passwordHistory"
                                                onKeyDown={onKeyDown}
                                                type="number"
                                                error={errors.passwordHistory}
                                                wasValidated={errors.passwordHistory}
                                                id="passwordHistory"
                                            /><label className="padding-mobile-spaces2">password(s)</label>
                                        </div>}
                                        <hr className="solid"></hr>
                                        <div className="form-group row">
                                            <FormInput
                                                label="Account Lock Duration"
                                                value={accountLockDuration}
                                                classes={inputClasses3}
                                                onChange={handleNumberChange}
                                                name="accountLockDuration"
                                                autoComplete="accountLockDuration"
                                                onKeyDown={onKeyDown}
                                                type="number"
                                                id="accountLockDuration"
                                                error={errors.accountLockDuration}
                                                wasValidated={errors.accountLockDuration}
                                                disabled={true}
                                            /><label className="padding-mobile-spaces2">minute(s)</label>
                                        </div>
                                        <div className="form-group row">
                                            <FormInput
                                                label="Idle time out"
                                                value={idleTimeOut}
                                                classes={inputClasses3}
                                                onChange={handleNumberChange}
                                                name="idleTimeOut"
                                                autoComplete="idleTimeOut"
                                                onKeyDown={onKeyDown}
                                                type="number"
                                                error={errors.idleTimeOut}
                                                wasValidated={errors.idleTimeOut}
                                                id="idleTimeOut"
                                                disabled={true}
                                            /><label className="padding-mobile-spaces2">minute(s)</label>
                                        </div>
                                        <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="allowConcurrentSession" name="allowConcurrentSession"
                                                    checked={allowConcurrentSession} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="allowConcurrentSession"></label>
                                            </div><label className="padding-mobile-spaces1">Allow concurrent sessions</label>
                                        </div>
                                        <div className="form-group row">
                                            <label className="col-md-3"></label>
                                            <div className="col-md-small">
                                                <input className="tgl tgl-light tgl-primary" id="enableRecaptcha" name="enableRecaptcha"
                                                    checked={enableRecaptcha} type="checkbox" onChange={handleCheckboxChange} />
                                                <label className="tgl-btn" htmlFor="enableRecaptcha"></label>
                                            </div><label className="padding-mobile-spaces1">Enable Captcha</label>
                                        </div>
                                    </div>
                                </form>
                            </div>
                            <div className="card-footer bg-light">
                                <FormActions actions={formActions} />
                            </div>
                        </div>
                    </ProtectedComponent>
                </div>
            </div>
        </React.Fragment>
    )
}

OrganizationEditor.propTypes = {
    organizationCode: PropTypes.string,
    loginType: PropTypes.string,
    enableTwoFactorAuthentication: PropTypes.bool,
    twoFactorAuthenticationType: PropTypes.string,
    whiteListEmailDomain: PropTypes.string,
    noOfInvalidOtpAttempts: PropTypes.number,
    enablePasswordPolicy: PropTypes.bool,
    passwordMinLength: PropTypes.number,
    passwordMaxLength: PropTypes.number,
    passwordRequireLowerCase: PropTypes.bool,
    passwordRequireUpperCase: PropTypes.bool,
    passwordRequireNumericCase: PropTypes.bool,
    passwordRequireSpecialCase: PropTypes.bool,
    passwordMatchEmailCase: PropTypes.bool,
    invalidRetires: PropTypes.number,
    passwordExpiry: PropTypes.number,
    passwordHistory: PropTypes.number,
    accountLockDuration: PropTypes.number,
    idleTimeOut: PropTypes.number,
    allowConcurrentSession: PropTypes.bool,
    enableRecaptcha: PropTypes.bool
}

export default withRouter(OrganizationEditor)