import React from "react";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import { DefaultErrorMessage } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import FormActions from '../../../Components/FormActions';
import renderHTML from 'react-render-html'

class ViewTermsAndConditions extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            termAndConditions: {
                content: ""
            }
        }
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/TermsAndConditions/Get?id=${this.props.match.params.id}`,
            {},
            (result) => {
                this.setState({ termAndConditions: result.data });
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    formActions = [
        {
            text: "Go Back",
            onClick: () => this.props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    render() {
        const { termAndConditions } = this.state;
        return (
            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                <div className="card-header card-header-flex">
                                    <h5 className="card-header-text">{`${termAndConditions.type} ${termAndConditions.version}`}</h5>
                                </div>
                                <div className="card-body p-b-20">
                                    {/* <ReactMarkdown>{termAndConditions.content}</ReactMarkdown> */}
                                    {renderHTML(termAndConditions.content)}
                                </div>
                                <div className="card-footer bg-light">
                                    <FormActions actions={this.formActions} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
export default withLoadingBackDrop(ViewTermsAndConditions);