import React from "react";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import { DefaultErrorMessage, SUCCESS } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import OrganizationPolicyEditor from "../../../Components/OrganizationPolicyEditor";
import { withRouter } from "react-router";
import ChangePageTitle from "../../../Components/ChangePageTitle";

class EditOrganizationPolicy extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showDeleteConfirmModel: false,
            errors: {}
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Organization/UpdateOrganizationPolicy`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id })
            },
            () => { showSuccessAlert(SUCCESS), this.props.updateOrganization(false), this.setState({ errors: {} }) },
            (ex) => {
                const result = JSON.parse(ex.message);
                if (!result.Data) {
                    showErrorAlert(result.message)
                }
                else {
                    this.setState({ errors: result.Data });
                }
            }
        )
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/Organization/GetOrganization?organizationID=${this.props.match.params.id}`,
            {},
            (result) => {
                const {
                    loginType,
                    enableTwoFactorAuthentication,
                    twoFactorAuthenticationType,
                    whiteListEmailDomain,
                    noOfInvalidOtpAttempts,
                    enablePasswordPolicy,
                    passwordMinLength,
                    passwordMaxLength,
                    passwordRequireLowerCase,
                    passwordRequireUpperCase,
                    passwordRequireNumericCase,
                    passwordRequireSpecialCase,
                    passwordMatchEmailCase,
                    invalidRetires,
                    passwordExpiry,
                    passwordHistory,
                    accountLockDuration,
                    idleTimeOut,
                    allowConcurrentSession,
                    enableRecaptcha
                } = result.data.organizationPolicy ?? {};
                const { organizationCode } = result.data;
                this.organizationPolicy = {
                    organizationCode,
                    loginType,
                    enableTwoFactorAuthentication,
                    twoFactorAuthenticationType,                   
                    whiteListEmailDomain : whiteListEmailDomain ? whiteListEmailDomain.trim().split(",").map(x => ({ label: x, value: x })) : '',
                    noOfInvalidOtpAttempts,
                    enablePasswordPolicy,
                    passwordMinLength,
                    passwordMaxLength,
                    passwordRequireLowerCase,
                    passwordRequireUpperCase,
                    passwordRequireNumericCase,
                    passwordRequireSpecialCase,
                    passwordMatchEmailCase,
                    invalidRetires,
                    passwordExpiry,
                    passwordHistory,
                    accountLockDuration,
                    idleTimeOut,
                    allowConcurrentSession,
                    enableRecaptcha
                }
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    render() {
        const { errors } = this.state;
        const { isLoading } = this.props;
        return (
            <React.Fragment>
                <ChangePageTitle pageTitle="Update Organization Security Policy" />
                {!isLoading && <OrganizationPolicyEditor errors={errors} submit={this.submit} {...this.organizationPolicy} {...this.update} updateOrganization={this.props.updateOrganization} isEdit={true} />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(withRouter(EditOrganizationPolicy))