import React from "react";
import TermsAndConditionsEditor from "../../../Components/TermsAndConditionsEditor";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import { DefaultErrorMessage } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';

class EditTermsAndConditions extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showDeleteConfirmModel: false,
            errors: {}
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/TermsAndConditions/Update`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id })
            },
            () => this.props.history.push('/platformadmin/TermsAndConditions'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/TermsAndConditions/Get?id=${this.props.match.params.id}`,
            {},
            (result) => {
                this.setState({ termAndConditions: result.data });
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    render() {
        const { errors, termAndConditions } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">Edit Terms and Conditions</h5>
            </div>
        );

        const { isLoading } = this.props;
        return (
            <React.Fragment>
                {!isLoading && <TermsAndConditionsEditor termAndConditions={termAndConditions}
                    errors={errors} header={header} submit={this.submit} isEdit={true} />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(EditTermsAndConditions)