import React from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import fetcher from '../../../utils/fetcher';
import { SUCCESS, SuccessfullyDeleted } from '../../../utils/Constant'
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel'
import Protected from '../../../Components/ProtectedComponent'
import PermissionsConstant from '../../../assets/permission.config.json'

export default function PermissionsManagermentTable() {
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);
    const title = "Manage Terms and Conditions";
    const columns = [
        {
            name: 'Version',
            selector: 'version',
            sortable: true,
            maxWidth: "15%"
        },
        {
            name: 'Type',
            selector: 'type',
            sortable: true,
            maxWidth: "20%"
        },
        {
            name: 'Created Date',
            selector: 'createdDate',
            sortable: true,
            maxWidth: "20%"
        },
        {
            name: 'Published Date',
            selector: 'publishOn',
            sortable: true,
            maxWidth: "20%"
        },
        {
            name: 'Enabled',
            sortable: true,
            maxWidth: "10%",
            cell: row => row.enabled ? "True" : "False"
        },
        {
            name: 'Actions',
            sortable: false,
            maxWidth: "15%",
            cell: row => (
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.TermAndConditionModify]}>
                        <Link to={`/platformadmin/termsandconditions/edit/${row.id}`}><i className="la la-edit tooltipTop">
                            <span className="tooltiptextTop">Edit</span></i>
                        </Link>
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.TermAndConditionRead]}>
                        <Link to={`/platformadmin/termsandconditions/view/${row.id}`}><i className="la la-eye tooltipTop">
                            <span className="tooltiptextTop">View</span></i>
                        </Link>
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.TermAndConditionModify]}>
                        <i onClick={() => { clone(row.id); }} className="la la-copy hover-pointer tooltipTop">
                            <span className="tooltiptextTop">Clone</span>
                        </i>
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.TermAndConditionDelete]}>
                        <i onClick={() => { setShowDeleteConfirmModel(true); setId(row.id) }} className="la la-trash hover-pointer tooltipTop">
                            <span className="tooltiptextTop">Delete</span>
                        </i>
                    </Protected>
                </React.Fragment>
            )
        },
    ];

    const clone = (id) => {
        fetcher(
            `${webserviceUrl}/api/TermsAndConditions/Clone?id=${id}`,
            {
                method: 'GET',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SUCCESS, () => setshouldReload(true)),
            (ex) => showErrorAlert(JSON.parse(ex.message).message)
        )
    }

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/TermsAndConditions/Filter?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}&isDesc=${sortDirection == "desc"}`)
        }
        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteScope = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/TermsAndConditions/Delete?id=${id}&reason=${reason}`,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel)
        )
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.TermAndConditionModify]}>
            <Link
                to={"/platformadmin/termsandconditions/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                Create New
            </Link>
        </Protected>
    )

    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.TermAndConditionModify]}>
            <Link to={"/platformadmin/termsandconditions/create"} className="dropdown-item">
                Create New
            </Link>
        </Protected>
    )

    return (
        <React.Fragment>
            <ManagermentTable
                shouldReload={shouldReload}
                setShouldReload={setshouldReload}
                columns={columns}
                title={title}
                apiUrl={apiUrl}
                createButton={createButton}
                createButtonMobile={createButtonMobile}
                searchType="Search Type"
            />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteScope} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}