import React from 'react';
import FormInput from '../../../Components/FormInput';
import ChangePageTitle from "../../../Components/ChangePageTitle";

function Contacts(props) {
  const handleInputChange = props.handleInputChange;

  const { clientApi, errors } = props;

  const inputClasses = {
    labelClass: "control-label col-md-12",
    colWidthClass: "col-md-12",
    inputClass: "form-control"
  }

  return (
    <React.Fragment>      
      <ChangePageTitle pageTitle="API Credential Contacts" />
      <div className="row">
        <div className="col">
          {props.header}
          <div className="card">
          <div className="card-body">
            <div className="form-group row">
                <label className={inputClasses.labelClass}>These settings allow your user to contact you with questions about their login.</label>
            </div>
            <form
              noValidate
              action="#"
              className="form-horizontal"
            >
              <div className="form-body">
                <div className="form-group row">
                  <FormInput
                    label="Support Email"
                    value={clientApi.supportEmail}
                    onChange={(e) =>
                      handleInputChange(
                        e.target.name,
                        e.target.value
                      )
                    }
                    name="supportEmail"
                    autoComplete="supportEmail"
                    type="text"
                    id="supportEmail"
                    error={errors.supportEmail}
                    wasValidated={errors.supportEmail}
                  />
                  <div className="col-md-3"></div>
                  <div className="col-md-8">
                    <p>This will be displayed on the login form.</p>
                  </div>
                </div>
              </div>
              <div className="form-body">
                <div className="form-group row">
                  <FormInput
                    label="Developer Email"
                    value={clientApi.developerEmail}
                    onChange={(e) =>
                      handleInputChange(
                        e.target.name,
                        e.target.value
                      )
                    }
                    error={errors.developerEmail}
                    wasValidated={errors.developerEmail}
                    name="developerEmail"
                    autoComplete="developerEmail"
                    type="text"
                    id="developerEmail"
                  />
                  <div className="col-md-3"></div>
                  <div className="col-md-8">
                    <p>This will be used by Ascentis to contact you regarding to your app.</p>
                  </div>
                </div>
              </div>
            </form>
          </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

Contacts.propTypes = {

};

export default Contacts;
