import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import PropTypes from 'prop-types';
import { Active, Disabled } from '../utils/Constant';
import ApplicationSelector from '../Components/ApplicationSelector';
import Popover from '../Components/Popover';

function ScopeEditor(props) {
    const [state, setState] = useState({
        name: props.name ?? "",
        description: props.description ?? "",
        displayName: props.displayName ?? "",
        application: props.application ?? {},
        status: props.status ?? Active,
        errors: {}
    });

    const handleInputChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value }));
    }

    const handleChangeApplication = (event) => {
        setState(state => ({ ...state, application: { id: event.id, name: event.name } }));
    }

    useEffect(() => {
        setState(state => ({ ...state, errors: props.errors }));
    }, [props.errors])

    function validate() {
        const { name, displayName, application } = state;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Scope Name is a required field."
        }

        if (!displayName.trim()) {
            isValid = false
            errors.displayName = "Scope Display Name is a required field."
        }

        if (!application.id) {
            isValid = false
            errors.application = "Application Name is a required field."
        }

        setState(state => ({ ...state, errors, wasValidated: true }));
        return isValid;
    }

    const onSubmit = () => {
        const {
            name,
            description,
            displayName,
            application,
            status,
        } = state;
        if (!validate()) return;

        props.submit({
            name: name.trim(),
            description: description.trim(),
            displayName: displayName.trim(),
            application,
            status
        })
    }

    const formActions = [
        {
            text: props.buttontype ?? "Update",
            onClick: onSubmit,
            className: "btn btn-primary submitButton"
        },
        {
            text: "Cancel",
            onClick: () => props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const getValidityClass = (propName) => {
        return wasValidated ? errors[propName] ? 'is-invalid' : 'is-valid' : ''
    }

    const enabledOptions = [
        {
            name: "Active",
            value: Active
        },
        {
            name: "Disabled",
            value: Disabled
        }
    ]

    const {
        name,
        description,
        displayName,
        application,
        status,
        errors,
        wasValidated
    } = state;

    const inputClasses = {
        labelClass: "control-label col-md-12",
        colWidthClass: "col-md-12",
        inputClass: "form-control"
    }

    return (
        <React.Fragment>
            <div className="main-content">
                <section className="page-content container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="card">
                                {props.header}
                                <div className="card-body p-b-20">
                                    <form action="#" className="form-horizontal">
                                        <div className="form-body">
                                            <div className="form-group row">
                                                <label className={inputClasses.labelClass}>An API Credential can be allowed permission to access Ascentis Products and Services by using this API scope.</label>
                                            </div>
                                            <div className="form-group row">
                                                <label htmlFor="application" className="control-label col-md-3">Application Name <span className="text-danger">*</span></label>
                                                <div className="col-md-8">
                                                    <ApplicationSelector onChange={handleChangeApplication}
                                                        className={`react-select-no-border form-control select2-form-control  ${getValidityClass('application')}`}
                                                        value={application ? { value: application.id, label: application.name } : null}
                                                    />
                                                    {errors.application && <span className="d-block invalid-feedback">{errors.application}</span>}
                                                </div>
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Scope Name"
                                                    value={name}
                                                    onChange={handleInputChange}
                                                    name="name"
                                                    autoComplete="scope-name"
                                                    type="text"
                                                    id="scope-name"
                                                    error={errors.name}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                                <Popover
                                                    id="tooltiphoverScopeName"
                                                    content="No space nor special characters, lower case.<br/>Recommended to use the following naming convention:<br/><br/>*<b>Product Name</b>*_*<b>Function</b>*_[*<b>Access Mode</b>*]<br/><br/><b>Product:</b> e.g.: CRM, OMP, MMS, POS, CX, BI<br/><br/><b>Function:</b> Module name, e.g.: Members, Voucher, Product.<br/><br/><b> Access Mode (Optional):</b> By default full access, e.g. “Read” for read access.<br/><br/><b> Max. length:</b> 20 chars."
                                                    placement="top"
                                                    maxWidth="350px"
                                                    iconClass="la la-question-circle la-2x"
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Display Name"
                                                    value={displayName}
                                                    onChange={handleInputChange}
                                                    name="displayName"
                                                    autoComplete="scope-display-name"
                                                    type="text"
                                                    id="scope-display-name"
                                                    error={errors.displayName}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Enabled"
                                                    onChange={handleInputChange}
                                                    name="status"
                                                    type="select"
                                                    id="scope-status"
                                                    value={status}
                                                    options={enabledOptions}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    label="Description"
                                                    onChange={handleInputChange}
                                                    name="description"
                                                    autoComplete="scope-description"
                                                    type="textarea"
                                                    id="scope-description"
                                                    rows="3"
                                                    value={description}
                                                    error={errors.description}
                                                    wasValidated={wasValidated}
                                                    isRequired={true}
                                                />
                                            </div>
                                            <div className="form-group row">
                                                <FormInput
                                                    type="text"
                                                    label="Api Definition link"
                                                    onChange={handleInputChange}
                                                    wasValidated={wasValidated}
                                                />
                                                <Popover
                                                    id="tooltiphoverApiDefinitionLink"
                                                    content="<center>Opens the API definition page that this scope will grant.<center>"
                                                    placement="top"
                                                    maxWidth="350px"
                                                    iconClass="la la-question-circle la-2x"
                                                />
                                            </div>
                                        </div>
                                    </form>
                                </div>
                                <div className="card-footer bg-light">
                                    <FormActions actions={formActions} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </React.Fragment >
    )
}

ScopeEditor.propTypes = {
    name: PropTypes.string,
    description: PropTypes.string,
    group: PropTypes.string
}

export default withRouter(ScopeEditor)