import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import Tabs from '../../../Components/Tabs';
import UpdateOrganization from './UpdateOrganization';
import Personalization from './Personalization';
import EditOrganizationPolicy from '../OrganizationPolicy/EditOrganizationPolicy';
import Administrator from './Administrator';
import IdentityProvider from './IdentityProvider';
import fetcher from '../../../utils/fetcher';
import { webserviceUrl } from '../../../utils/configuration';
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class EditOrganization extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      updateOrganization: false,
      organizationName: ""
    };
  }
  
  setUpdateOrganization = (update) => {
    this.setState({ updateOrganization: update });
  };

  componentDidMount() {
    fetcher(
      `${webserviceUrl}/api/Organization/GetOrganization?organizationID=${this.props.match.params.id}`,
      {},
      (result) => {
        const { organizationName } = result.data;
        this.setState({ organizationName });
        this.props.setLoading(false)
      },
      () => this.props.setLoading(false)
    )
  }
  
  render() {
    const { organizationName } = this.state
    return (
      <div className="main-content">
        <ChangePageTitle pageTitle="Update Organization" />
        <section className="page-content container-fluid">
          <div className="card">
            <div className="card-header card-header-flex">
              <h5 className="card-header-text">
                Update Organization: {organizationName}
              </h5>
            </div>
              <Tabs update={this.state.updateOrganization} updateOrganization={this.setUpdateOrganization} >
                  <div label="Profile">
                    <UpdateOrganization
                      update={this.state.updateOrganization}
                      updateOrganization={this.setUpdateOrganization}
                    />
                  </div>
                  <div label="Administrators">
                    <Administrator
                      update={this.state.updateOrganization}
                      updateOrganization={this.setUpdateOrganization}
                    />
                  </div>
                  <div label="Security Policy">
                    <EditOrganizationPolicy
                      update={this.state.updateOrganization}
                      updateOrganization={this.setUpdateOrganization}
                    />
                  </div>
                  <div label="Identity Provider">
                    <IdentityProvider
                      update={this.state.updateOrganization}
                      updateOrganization={this.setUpdateOrganization}
                    />
                  </div>
                  <div label="Personalization">
                    <Personalization
                      update={this.state.updateOrganization}
                      updateOrganization={this.setUpdateOrganization}
                    />
                  </div>
              </Tabs>            
          </div>
        </section>
      </div>
    );
  }
}
export default WithLoadingBackDrop(withRouter(EditOrganization));
