import React, { Fragment } from 'react';
import DataTable from 'react-data-table-component';
import TableHeader from '../Components/TableHeader';
import CustomDataTablePagination from '../Components/CustomDataTablePagination';
import CustomTableStyle from '../utils/CustomTableStyle'
import PropTypes from 'prop-types';
import fetcher from '../utils/fetcher'
import withLoadingBackDrop from './HOC/WithLoadingBackDrop'
import { showErrorAlert } from './Alert'
import { DefaultErrorMessage } from '../utils/Constant'

class ManagermentTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            page: 1,
            pageSize: 10,
            total: 0,
            sortColumn: props.orderBy || "",
            sortDirection: "desc",
            searchTerm: "",
            searchText: ""
        };
    }

    setPage = (page) => this.setState({ page });

    setPageSize = (pageSize) => {
        pageSize = parseInt(pageSize);
        const page = this.preventPageOverflow(pageSize);
        this.setState({ pageSize, page })
    };

    setSortColumn = (column, sortDirection) => this.setState({ sortColumn: column.selector, sortDirection });

    preventPageOverflow = (newPageSize) => {
        const { page, pageSize } = this.state;
        let startItem = (page - 1) * pageSize + 1;
        return Math.ceil(startItem / newPageSize);
    }

    search = (e) => {
        this.setState();
    }

    getItems = () => {
        this.props.setLoading(true);
        const { page, pageSize, searchTerm, sortColumn, sortDirection } = this.state;
        const { apiUrl } = this.props;
        if (this.props.isOfflineData) {
            this.filterData();
            return;
        }
        fetcher(
            apiUrl(page, pageSize, searchTerm, sortColumn, sortDirection),
            {},
            (result) => {
                this.props.setLoading(false)
                this._isMounted && this.setState({
                    isLoaded: true,
                    items: this.props.transformData ? this.props.transformData(result.data.data) : result.data.data,
                    total: result.data.total
                });
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    filterData = () => {
        const { page, pageSize, searchTerm, sortColumn } = this.state;
        let items = this.props.items;
        if (searchTerm) {
            items = items.filter(x => x[sortColumn].includes(searchTerm));
        }
        items = items.slice(pageSize * (page - 1), pageSize * (page - 1) + pageSize);
        this.setState({
            isLoaded: true,
            items: items,
            total: this.props.items.length
        });
    }

    componentDidMount() {
        this._isMounted = true;
        this.getItems();
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    componentDidUpdate(prevProps, prevState) {
        const { page, pageSize, searchTerm, sortColumn, sortDirection } = this.state;
        if (
            prevState.page != page
            || prevState.pageSize != pageSize
            || prevState.searchTerm != searchTerm
            || prevState.sortColumn != sortColumn
            || sortDirection != prevState.sortDirection
            || this.props.shouldReload
        ) {
            this.getItems();
            this.props.setShouldReload && this.props.setShouldReload(false)
        }
    }

    render() {
        const { items, pageSize, total, page, searchText } = this.state;
        const { columns, title, searchType, isOfflineData } = this.props;
        const search = () => this.setState({ searchTerm: this.state.searchText });
        const onSearchInput = (e) => {
            if (!e.target.value) {
                this.setState({ searchText: "", searchTerm: "" })
            }
            this.setState({ searchText: e.target.value })
        }
        const tableHeader = (
            <TableHeader
                searchText={searchText}
                onChangePageSize={this.setPageSize}
                pageSize={pageSize}
            />
        )

        const tableSearchElement = (
            <React.Fragment>
                <div>
                    <div className="table-search-input-wrapper">
                        <div className="input-group md-form form-sm form-1 pl-0 pb-2 col-xs-12">
                            <input onChange={onSearchInput} className="form-control my-0 py-1" type="text" placeholder={searchType} aria-label="Search" />
                            <div className="input-group-prepend">
                                <span onClick={search} className="input-group-text color-primary btn-primary lighten-3 btn-search" id="basic-text1">
                                    <i className="la la-search text-white" aria-hidden="true"></i>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
        return (
            <Fragment>
                <div className="main-content-header">
                    <div className="container-fluid">
                        <div className="col-xs-12">
                            <h2>{title}</h2>
                            <div className="main-content-actions">
                                <div className="desktop-action">
                                    {this.props.createButton}
                                </div>
                                <div className="mobile-action">
                                    <a className="btn btn-fab" data-toggle="dropdown">
                                        <i className="icon dripicons-dots-3 rotate-90"></i>
                                    </a>
                                    <div className="dropdown-menu dropdown-icon-menu dropdown-menu-right" x-placement="bottom-end">
                                        {this.props.createButtonMobile}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="content page-aside-left">
                    <div className="main-content">
                        <section className="page-content container-fluid">
                            <div className="row">
                                <div className="col-lg-12 col-xl-12">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="container-fluid">
                                                <DataTable
                                                    title={tableSearchElement}
                                                    actions={tableHeader}
                                                    columns={columns}
                                                    data={items}
                                                    pagination={true}
                                                    paginationServer={true}
                                                    customStyles={CustomTableStyle}
                                                    paginationTotalRows={total}
                                                    onChangePage={this.setPage}
                                                    sortServer={!isOfflineData}
                                                    striped={true}
                                                    onSort={this.setSortColumn}
                                                    responsive={true}
                                                    paginationComponent={(props) => <CustomDataTablePagination {...props} currentPage={page} rowsPerPage={pageSize} />}                                                    
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </div>
                </div>
            </Fragment>
        )
    }
}

ManagermentTable.propTypes = {
    columns: PropTypes.array.isRequired,
    title: PropTypes.string.isRequired,
    apiUrl: PropTypes.func.isRequired,
    transformData: PropTypes.func,
    createButton: PropTypes.element,
    createButtonMobile: PropTypes.element
}

export default withLoadingBackDrop(ManagermentTable)