import React from 'react';
import logo from '../../assets/img/logobrand-red-192.png';
import { webserviceUrl } from '../../utils/configuration';
import { showErrorAlert, showSuccessAlert } from '../../Components/Alert';
import fetcher from '../../utils/fetcher';
import ChangePageTitle from "../../Components/ChangePageTitle";

class SignUpOrganization extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			name: "",
			code: "",
			description: "",
			errors: {},
		}
	}

	validate = () => {
		const { name, code, description } = this.state;
        const errors = {};
		let isValid = true;
		if (!name.trim()) {
			isValid = false
			errors.organizationName = "Organization Name is a required field."
		}
		if (!code.trim()) {
			isValid = false
			errors.organizationCode = "Organization Code is a required field."
		}
		if (!description.trim()) {
			isValid = false
			errors.description = "Organization Description is a required field."
		}
		this.setState({ ...this.state, errors });
		return isValid;
	}

	submit = () => {
		const { name, code, description, errors } = this.state;
		if (!this.validate()) return;
		fetcher(`${webserviceUrl}/api/Organization/SignUpOrganization`,
			{
				mode: 'cors',
				method: "POST",
				body: JSON.stringify({
					OrganizationName: name,
					OrganizationCode: code,
					description
				})
			},
			(result) => {
				this.props.setOrganization(result.data.id, result.data.organizationName, result.data.userRoles)
			},
			(ex) => {
				const result = JSON.parse(ex.message);
				if (!result.Data) 
				{
					showErrorAlert(result.message)
				}
				else {
					this.setState({ ...this.state, errors: result.Data });
				}
			}
		)
	}

	handleInputChange = (event) => {
		this.setState({ [event.target.name]: event.target.value });
	};

	render() {
		const { isLoading } = this.props;
		const { name, code, description, errors } = this.state;
		if (isLoading) {
			return <ChangePageTitle pageTitle="SignUp" />
		}
		return (
			<div className="content-wrapper">
				<ChangePageTitle pageTitle="SignUp" />
				<div className="container">
					<form className="sign-in-form">
						<div className="card">
							<div className="card-body">
								<img src={logo} className="brand text-center d-block m-b-20" />
								<h5 className="sign-in-heading m-b-20">Create your Organization</h5>
								<div className="form-group">
									<label htmlFor="name" className="sr-only">Name</label>
									<input type="text"
										name="name"
										id="name"
										autoComplete="name"
										className="form-control"
										value={name}
										onChange={this.handleInputChange}
										required=""
										placeholder="Organization Name"
									/>
									{errors && errors.organizationName && <span className="d-block invalid-feedback">{errors.organizationName}</span>} 
								</div>
								<div className="form-group">
									<label htmlFor="code" className="sr-only">Code</label>
									<input type="code"
										id="code"
										name="code"
										autoComplete="code"
										className="form-control"
										value={code}
										onChange={this.handleInputChange}
										required=""
										placeholder="Organization Code"
									/>
									{errors && errors.organizationCode && <span className="d-block invalid-feedback">{errors.organizationCode}</span>} 
								</div>
								<div className="form-group">
									<label htmlFor="description" className="sr-only">Description</label>
									<input type="textbox"
										id="description"
										name="description"
										autoComplete="description"
										className="form-control"
										value={description}
										onChange={this.handleInputChange}
										required=""
										placeholder="Organization Description"
									/>
									{errors && errors.description != null && <span className="d-block invalid-feedback">{errors.description}</span>}
								</div>
								<div className="divPadLogin">
									<button onClick={this.submit} className="btn btn-primary col-md-10" type="button">Create Organization</button>
								</div>
							</div>
						</div>
					</form>
				</div>
			</div>
		)
	}
}

export default SignUpOrganization