import React from "react";
import UserEditor from "../../../Components/UserEditor";
import { Active, DefaultErrorMessage, Inactive } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop'
import fetcher from "../../../utils/fetcher";
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert } from '../../../Components/Alert'
import { UserContext } from '../../../Context/UserContext'
import ChangePageTitle from "../../../Components/ChangePageTitle";

class EditUser extends React.Component {
    static contextType = UserContext;
    constructor(props) {
        super(props);
        this.state = {
            showDeleteConfirmModel: false,
            errors: {},
            userFullName: "",
        }
    }

    componentDidMount() {
        let url;
        if(this.props.isEditOwnProfile){
            url = `${webserviceUrl}/api/User/GetOwnProfile?organizationId=${this.context.organizationId}`;
        }else{
            url = `${webserviceUrl}/api/User/GetUser?userId=${this.props.match.params.id}&organizationId=${this.context.organizationId}`
        }

        fetcher(
            url,
            {},
            (result) => {
                const { userFullName, staffId, email, status, roles, mobileNumber, profilePicture } = result.data;
                this.userData = {
                    name: userFullName,
                    staffId,
                    email,
                    status,
                    roles,
                    mobileNumber,
                    profilePicture
                }
                this.setState({ userFullName });
                this.props.setLoading(false)
            },
            () => {
                showErrorAlert(DefaultErrorMessage)
                this.props.setLoading(false)
            }
        )
    }

    submit = (data) => {
        let url;
        if(this.props.isEditOwnProfile){
            url = `${webserviceUrl}/api/User/UpdateOwnProfile`;
        }else{
            url = `${webserviceUrl}/api/User/UpdateUser`;
        }
        
        fetcher(
            url,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id }),
            },
            (result) => {
                if (result.message) {
                    showErrorAlert(result.message)
                }
                else {
                    if(this.context.id == this.props.match.params.id){
                        this.context.setUserImage({key: data.profilePicture, url: data.profileUrl});
                    }
                    
                    if(this.props.isEditOwnProfile){
                        this.context.setUserImage({key: data.profilePicture, url: data.profileUrl});
                        this.props.history.goBack();
                    }
                    this.props.history.push('/users')
                }
            },
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    render() {
        const title = "Update User";
        const { errors, userFullName } = this.state;
        const header = () => (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}: {userFullName}</h5>
            </div>
        );

        const { isLoading } = this.props;
        return (
            <React.Fragment>
                <ChangePageTitle pageTitle={title} />
                {!isLoading && <UserEditor isEditOwnProfile={this.props.isEditOwnProfile} errors={errors} header={header()} submit={this.submit} {...this.userData} applications={this.applications} isEdit={true} />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(EditUser)