import React, { Fragment } from 'react';
import DataTable from 'react-data-table-component';
import CustomTableStyle from '../../../utils/CustomTableStyle'
import fetcher from '../../../utils/fetcher';
import { webserviceUrl } from '../../../utils/configuration';
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import { showErrorAlert } from '../../../Components/Alert'
import { UserContext } from '../../../Context/UserContext';
import { DefaultErrorMessage } from '../../../utils/Constant';
import ChangePageTitle from "../../../Components/ChangePageTitle";
import TableHeader from '../../../Components/TableHeader';

class Scopes extends React.Component {
    static contextType = UserContext;
    constructor(props) {
        super(props);
        this.state = {
            error: {},
            isLoaded: false,
            items: [],
            page: 1,
            pageSize: 10,
            total: 0,
            sortColumn: props.orderBy || "",
            sortDirection: "desc",
            searchTerm: "",
            searchText: "",
            scopes: props.clientApi.allowedScopes.split(',').filter(x => x),
            scopesStr: props.clientApi.allowedScopes,
        };
    }

    static contextType = UserContext;

    setPage = (page) => this.setState({ page });

    setPageSize = (pageSize) => {
        pageSize = parseInt(pageSize);
        const page = this.preventPageOverflow(pageSize);
        this.setState({ pageSize, page })
    };

    setSortColumn = (column, sortDirection) => this.setState({ sortColumn: column.selector, sortDirection });

    preventPageOverflow = (newPageSize) => {
        const { page, pageSize } = this.state;
        let startItem = (page - 1) * pageSize + 1;
        return Math.ceil(startItem / newPageSize);
    }

    getItems = () => {
        const { searchTerm } = this.state;
        let url = `${webserviceUrl}/api/Scope/GetAllAvailable?organizationId=${this.context.organizationId}`
        if (searchTerm) {
            url = "".concat(url, `&searchTerm=${searchTerm}`)
        }

        fetcher(
            url,
            {},
            (result) => {
                this.props.setLoading(false)
                this._isMounted && this.setState({
                    isLoaded: true,
                    items: result.data
                });
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    componentDidMount() {
        this._isMounted = true;
        this.getItems();
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    static getDerivedStateFromProps(props, currentState) {
        if (props.clientApi.allowedScopes != currentState.scopesStr) {
            return {
                scopes: props.clientApi.allowedScopes.split(',').filter(x => x),
                scopesStr: props.clientApi.allowedScopes
            }
        }
        return null
    }

    componentDidUpdate(prevProps, prevState) {
        const { page, pageSize, searchTerm } = this.state;

        if (
            prevState.page != page
            || prevState.pageSize != pageSize
            || prevState.searchTerm != searchTerm
            || this.props.shouldReload
        ) {
            this.getItems();
        }
    }

    handleInputChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    onChangeScope = (event) => {
        const currentScopes = this.state.scopes;
        if (event.target.checked) {
            currentScopes.push(event.target.value)
            this.props.handleInputChange("allowedScopes", currentScopes.join(","));
            return;
        }

        const index = currentScopes.indexOf(event.target.value);
        if (index > -1) {
            currentScopes.splice(index, 1);
            this.props.handleInputChange("allowedScopes", currentScopes.join(","));
        }
    }

    search = () => this.setState({ searchTerm: this.state.searchText });

    onSearchInput = (e) => {
        if (!e.target.value) {
            this.setState({ searchText: "", searchTerm: "" })
        }
        this.setState({ searchText: e.target.value })
    }

    render() {
        const { items, pageSize, searchText, scopes } = this.state;

        const tableSearchElement = (
            <React.Fragment>
                <div>
                    <div className="table-search-input-wrapper">
                        <div className="input-group md-form form-sm form-1 pl-0">
                            <input onChange={this.onSearchInput} className="form-control my-0 py-1" type="text" placeholder="Search *" aria-label="Search" />
                            <div className="input-group-prepend">
                                <span onClick={this.search} className="input-group-text color-primary btn-primary lighten-3 btn-search" id="basic-text1">
                                    <i className="la la-search text-white" aria-hidden="true"></i>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )

        const tableHeader = (
            <TableHeader
                searchText={searchText}
                onChangePageSize={this.setPageSize}
                pageSize={pageSize}
            />
        )
        const columns = [
            {
                name: '',
                sortable: true,
                maxWidth: "3%",
                minWidth: "70px",
                cell: row => <input type="checkbox" value={row.name} checked={scopes.includes(row.name)} onChange={this.onChangeScope} disabled={row.name == "openid"} />
            },
            {
                name: 'App Name',
                selector: 'appName',
                sortable: true,
                maxWidth: "20%"
            },
            {
                name: 'Scope',
                selector: 'name',
                sortable: true,
                maxWidth: "20%"
            },
            // {
            //     name: 'Scope Display Name',
            //     selector: 'displayName',
            //     sortable: true,
            //     maxWidth: "20%"
            // },
            {
                name: 'Description',
                selector: 'description',
                sortable: true,
                maxWidth: "67%"
            }
        ];

        const inputClasses = {
            labelClass: "control-label col-md-12",
            colWidthClass: "col-md-12",
            inputClass: "form-control"
        }

        return (
            <Fragment>
                <ChangePageTitle pageTitle="API Credential Scopes" />
                {/* <section className="page-content container-fluid"> */}
                <div className="row">
                    <div className="col-lg-12 col-xl-12">
                        <div className="card">
                            <div className="card-body">
                                <div className="form-group row">
                                    <label className={inputClasses.labelClass}>These scopes are default scopes available for Ascentis APIs. Choose the scopes to be allowed for this client application.</label>
                                </div>
                                <div className="container-fluid">
                                    <DataTable
                                        title={tableSearchElement}
                                        actions={tableHeader}
                                        columns={columns}
                                        data={items}
                                        pagination={false}
                                        customStyles={CustomTableStyle}
                                        sortServer={false}
                                        striped={true}
                                        responsive={true}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* </section> */}
            </Fragment>
        )
    }
}

export default WithLoadingBackDrop(Scopes)