import React from 'react';
import logo from '../../assets/img/logobrand-red-192.png';
import { webserviceUrl } from '../../utils/configuration';
import WithLoadingBackDrop from '../../Components/HOC/WithLoadingBackDrop';
import { SUCCESS, DefaultErrorMessage, CancelSignUpMessage } from '../../utils/Constant';
import { showConfirmModel, showErrorAlert, showSuccessAlert } from '../../Components/Alert';
import fetcher from '../../utils/fetcher';
import ChangePageTitle from "../../Components/ChangePageTitle";
import Popover from "../../Components/Popover";

class AdditionalInformation extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			mobileNumber: "",
			staffId: "",
			errors: {},
		}
	}

	cancel = () => {
		showConfirmModel({ message: CancelSignUpMessage, confirmBtnText: "Exit" }, () => this.props.history.push('/'))
	}

	componentDidMount() {
		fetch(`${webserviceUrl}/api/user/GetUserByActivationId?setPasswordId=${this.props.match.params.activationId}`)
			.then(response => {
				if (!response.ok) {
					throw new Error(response.statusText);
				}
				return response
			})
			.then(result => result.json())
			.then(result => {
				const isValid = result.status == SUCCESS;
				if (!isValid) {
					throw new Error(response.statusText)
				}
				const { mobileNumber, staffId, status } = result.data;
				switch (status) {
					case 'PendingActivation':
						this.props.history.push(`/SignUp/${this.props.match.params.activationId}`);
						break;
					case 'Activation':
						this.props.history.push(`/verify/${this.props.match.params.activationId}`);
						break;
					case 'AdditionalInformation':
						this.setState({ isValid: true, mobileNumber, staffId })
						break;
					case 'Verified':
						this.props.history.push(`/TermsAndCondition/${this.props.match.params.activationId}`);
						break;
					default:
						this.props.history.push(`/`);
						break;
				}
			})
			.catch(ex => {
				this.props.history.push('/404')
			});
		this.props.setLoading(false)
	}

	submit = () => {
		const { mobileNumber, staffId } = this.state;

		fetcher(`${webserviceUrl}/api/user/AddAdditionalInfo`,
			{
				mode: 'cors',
				method: "POST",
				body: JSON.stringify({
					mobileNumber, staffId, setPasswordId: this.props.match.params.activationId
				})
			},
			() => this.props.history.push(`/TermsAndCondition/${this.props.match.params.activationId}`),
			(ex) => {
				const result = JSON.parse(ex.message);
				if (!result.Data) {
					showErrorAlert(result.message)
				}
				else {
					this.setState({ errors: result.Data });
				}
			}
		)
	}

	handleInputChange = (event) => {
		this.setState({ [event.target.name]: event.target.value });
	};

	render() {
		const { mobileNumber, staffId, errors } = this.state;
		return (
			<div className="container">
				<ChangePageTitle pageTitle="SignUp" />
				<form className="sign-in-form">
					<div className="card">
						<div className="card-body">
							<img src={logo} className="brand text-center d-block m-b-20" />
							<h5 className="sign-in-heading m-b-20">Additional Information </h5>
							<p className="sign-in-heading m-b-20">These informations are optional, but highly recommended.
								It will be used to keep your account secure and may improve your experience using our services.</p>
							<div className="form-group">
								<label htmlFor="mobileNumber" className="sr-only">Mobile Number</label>
								<input type="text"
									id="mobileNumber"
									name="mobileNumber"
									autoComplete="mobileNumber"
									className="form-control"
									value={mobileNumber}
									onChange={this.handleInputChange}
									required=""
									placeholder="Mobile Number"
									maxLength="20"
								/>
								<Popover
									id="tooltiphoverMobileNumber"
									content="<center>Why do we need this? This is security policy enabled by your Administrator.</center>"
									placement="bottom"
									maxWidth="350px"
									iconClass="la la-question-circle"
								/>
								<span><small>Example: +658389XXXX</small></span>
								{errors && errors.mobileNumber && <span className="d-block invalid-feedback">{errors.mobileNumber}</span>}
							</div>
							<div className="form-group">
								<label htmlFor="staffId" className="sr-only">Staff ID</label>
								<input
									type="text"
									id="staffId"
									name="staffId"
									autoComplete="staffId"
									className="form-control"
									value={staffId}
									onChange={this.handleInputChange}
									required=""
									placeholder="Staff Id"
								/>
								<Popover
									id="tooltiphoverStaffId"
									content="<center>Why do we need this? This is alternative login mechanism enabled by your Administrator.</center>"
									placement="bottom"
									maxWidth="350px"
									iconClass="la la-question-circle"
								/>
								<span><small>Example: organization_code\<b>johndoe</b></small></span>
								{errors && errors.staffId && <span className="d-block invalid-feedback">{errors.staffId}</span>}
							</div>
							<div className="divPadLogin">
								<button onClick={this.submit} className="btn btn-primary col-md-5" type="button">Next</button>&nbsp;&nbsp;&nbsp;
								<button onClick={this.cancel} className="btn btn-secondary col-md-5" type="button">Cancel</button>
							</div>
						</div>
					</div>
				</form>
			</div>
		)
	}
}

export default WithLoadingBackDrop(AdditionalInformation)