import React, { Fragment } from 'react';
import DataTable from 'react-data-table-component';
import CustomTableStyle from '../utils/CustomTableStyle'
import fetcher from '../utils/fetcher'
import { showErrorAlert, showSuccessAlert } from './Alert'
import { DefaultErrorMessage, EmailRegex, SUCCESS, SuccessfullyDeleted, SuperUser, YES } from '../utils/Constant'
import WithLoadingBackDrop from './HOC/WithLoadingBackDrop';
import { webserviceUrl } from '../utils/configuration';
import { withRouter } from 'react-router';
import ProtectedComponent from './ProtectedComponent';
import PermissionsConstant from '../assets/permission.config.json'
import DeleteConfirmModel from './ConfirmDeleteModel';
import FormInput from './FormInput';
import FormActions from './FormActions';

class DefaultUserGroups extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            wasValidated: false,
            showDeleteConfirmModel: false,
            errors: {},
            items: [],
            group: {
                name: "",
                description: ""
            }
        };
    }

    onGroupInputChange = (e) => {
        const { group } = this.state;
        this.setState({ group: { ...group, [e.target.name]: e.target.value } });
    }

    validate = () => {
        const { name, description } = this.state.group;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.name = "Application Name is a required field."
        }
        if (!description || !description.trim()) {
            isValid = false
            errors.description = "Description is a required field."
        }
        this.setState({ errors, wasValidated: true })
        return isValid;
    }


    getItems = () => {
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/GetAllDefaultApplicationGroup?applicationId=${this.props.applicationId}`,
            {},
            (result) => {
                this.props.setLoading(false)
                this._isMounted && this.setState({
                    isLoaded: true,
                    items: result.data
                });
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }
    
    formActions = [
        {            
            text: "Add New Group",
            onClick: () => this.onSubmit(),
            className: "btn btn-primary submitButton",            
            requirePermissions: [PermissionsConstant.ApplicationGroupModifyDefault]
        },
        {
            text: "Cancel",
            onClick: () => this.props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"      
        }
    ]

    componentDidMount() {
        this._isMounted = true;
        this.getItems();
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    onSubmit = () => {
        if (!this.validate()) return;
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/AddDefaultApplicationGroup`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...this.state.group, applicationId: this.props.applicationId })
            },
            () => {
                showSuccessAlert(SUCCESS)
                this.getItems();
                this.setState({
                    group: {
                        name: "",
                        description: ""
                    }
                });
            },
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
        this.setState({ wasValidated: false })
    }

    handleInputChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
        this.props.updateOrganization(true);
    }

    closeDeleteConfirmModel = () => {
        this.setState({ showDeleteConfirmModel: false })
        this.getItems();
    }

    deleteGroup = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/ApplicationGroup/DeleteApplicationGroup?id=${id}&reason=${reason} `,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, this.closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, this.closeDeleteConfirmModel)
        )
    }

    render() {
        const { items, showDeleteConfirmModel, id, group, errors, wasValidated } = this.state;
        const columns = [
            {
                name: 'Group Name',
                selector: 'name',
                sortable: true,
                maxWidth: "30%"
            },
            {
                name: 'Description',
                selector: 'description',
                sortable: true,
                maxWidth: "60%"
            },
            {
                name: 'Actions',
                sortable: false,
                maxWidth: "10%",
                cell: row =>
                    <React.Fragment>
                        <ProtectedComponent action="disable" requirePermissions={[PermissionsConstant.ApplicationGroupDeleteDefault]}>
                            <i onClick={() => { this.setState({ showDeleteConfirmModel: true, id: row.id }) }} className="la la-trash hover-pointer tooltipTop">
                                <span className="tooltiptextTop">Delete Group</span>
                            </i>
                        </ProtectedComponent>
                    </React.Fragment>
            },
        ];

        return (
            <Fragment>
                 <div className="card">
                        <div className="card-body">
                            <form action="#" className="form-horizontal">
                                <div className="form-body">
                                    <div className="form-group row">
                                        <FormInput
                                            label="Group Name"
                                            value={group.name}
                                            classes={this.inputClasses}
                                            onChange={this.onGroupInputChange}
                                            name="name"
                                            autoComplete="name"
                                            type="text"
                                            id="name"
                                            error={errors.name}
                                            wasValidated={wasValidated}
                                            isRequired={true}
                                        />                            
                                    </div>
                                    <div className="form-group row">
                                        <FormInput
                                            label="Group Description"
                                            value={group.description}
                                            classes={this.inputClasses}
                                            onChange={this.onGroupInputChange}
                                            name="description"
                                            autoComplete="description"
                                            type="text"
                                            id="description"
                                            error={errors.description}
                                            wasValidated={wasValidated}
                                            isRequired={true}
                                        />
                                    </div>
                                    <div className="card-footer bg-light">
                                        <FormActions actions={this.formActions}/>
                                    </div>
                                </div>
                                <DataTable
                                    columns={columns}
                                    data={items}
                                    pagination={false}
                                    customStyles={CustomTableStyle}
                                    sortServer={false}
                                    striped={true}
                                    responsive={true}
                                />
                            </form>                              
                        </div>
                     </div>
                {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={this.deleteGroup} closeModel={this.closeDeleteConfirmModel} />}
            </Fragment>
        )
    }
}

export default withRouter(WithLoadingBackDrop(DefaultUserGroups))