import React, { Fragment } from "react";
import PropTypes from 'prop-types';

function FormInput(props) {
    const {
        id,
        value,
        type,
        onChange,
        validate,
        placeholder,
        name,
        classes = {
            labelClass: "control-label col-md-3",
            colWidthClass: "col-md-8",
            inputClass: "form-control"
        },
        autoComplete,
        rows,
        options,
        error,
        label = "",
        wasValidated = false,
        isRequired,
        min = 0,
        onKeyDown,
        max = 99,
        maxLength,
        disabled,
    } = props;

    const validityClass = error ? "is-invalid" : "is-valid";
    const { labelClass, inputClass, colWidthClass } = classes;

    let inputComponent;
    switch (type) {
        case "textarea":
            inputComponent = (
                <textarea
                    autoComplete={autoComplete}
                    value={value}
                    rows={rows}
                    placeholder={placeholder}
                    validate={validate}
                    className={`${inputClass} ${wasValidated ? validityClass : ""}`}
                    name={name}
                    id={id}
                    disabled={disabled}
                    onChange={onChange}
                >
                </textarea>
            )
            break;
        case "select":
            inputComponent = (
                <select disabled={disabled} className={`${inputClass} ${wasValidated ? validityClass : ""}`} id={id} name={name} onChange={onChange} value={value}>
                    {options.map(item => {
                        return <option disabled={item.disabled} key={item.value} value={item.value}>{item.name}</option>
                    })}
                </select>
            )
            break;
        default:
            inputComponent = (
                <input
                    id={id}
                    type={type}
                    value={value}
                    className={`${inputClass} ${wasValidated ? validityClass : ""}`}
                    onChange={onChange}
                    disabled={disabled}
                    placeholder={placeholder}
                    autoComplete={autoComplete}
                    name={name}
                    min={min}
                    max={max}
                    onKeyDown={onKeyDown}
                    maxLength={maxLength}
                />
            )
            break;
    }

    return (
        <Fragment>
            <label htmlFor={name} className={labelClass}>
                {label}
                {isRequired && <span className="text-danger">*</span>}
            </label>
            <div className={`${colWidthClass}`}>
                {inputComponent}
                {error && <div className="invalid-feedback">{error}</div>}
            </div>
        </Fragment>
    );
};

FormInput.propTypes = {
    id: PropTypes.string,
    value: PropTypes.any,
    type: PropTypes.string.isRequired,
    onChange: PropTypes.func,
    validate: PropTypes.any,
    placeholder: PropTypes.string,
    name: PropTypes.string,
    classes: PropTypes.object,
    autoComplete: PropTypes.string,
    rows: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    options: PropTypes.array,
    error: PropTypes.string,
    onToggle: PropTypes.func,
    label: PropTypes.string,
    clearValueOnDisable: PropTypes.bool,
    wasValidated: PropTypes.bool,
    isRequired: PropTypes.bool,
    disabled: PropTypes.bool
}


export default FormInput;