import swal from 'sweetalert'

export const showSuccessAlert = (message, callback) => swal({
    title: message,
    icon: "success",
    closeOnClickOutside: false,
    button: {
        className: "btn btn-primary",
    }
}).then(callback)

export const showErrorAlert = (message, callback = () => { }) => swal({
    title: 'Alert Message',
    text: message,
    icon: "error",
    closeOnClickOutside: false,
    button: {
        className: "btn btn-primary",
    }
}).then(callback)


export const showConfirmModel = (options, callback = () => { }) => swal({
    title: options.message,
    icon: options.icon ?? "error",
    closeOnClickOutside: true,
    content: options.content,
    html: options.html,
    buttons: {
        submit: {
            text: options.confirmBtnText ?? "Confirm",
            value: true,
            visible: true,
            className: "btn btn-primary submitButton",
            closeModal: true,
        },
        cancel: {
            text: "Cancel",
            value: false,
            visible: true,
            className: "btn btn-secondary clear-form btn-outline",
            closeModal: true,   
        }
    }
}).then((value) => {
    switch (value) {
        case true:
            callback(value);
            break;
        case false:
            break;
    }
})