import React from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import fetcher from '../../../utils/fetcher';
import { SuccessfullyDeleted } from '../../../utils/Constant';
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel';
import PermissionsConstant from '../../../assets/permission.config.json';
import Protected from '../../../Components/ProtectedComponent';
import ChangePageTitle from "../../../Components/ChangePageTitle";

export default function RolesManagermentTable() {
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);
    const title = "Manage Roles";
    const columns = [
        {
            name: 'Role Name',
            selector: 'name',
            sortable: true,
            maxWidth: "25%"
        },
        {
            name: 'Description',
            selector: 'description',
            maxWidth: "50%"
        },
        {
            name: 'Actions',
            selector: 'edit',
            sortable: false,
            maxWidth: "25%",
            cell: row => (
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.RolesModify]}>
                        <Link to={`/roles/edit/${row.id}`}><i className="la la-edit tooltipTop icon-big"><span className="tooltiptextTop">Edit Role</span></i></Link>&nbsp;
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.RolesDelete]}>
                        <div><i onClick={() => { setShowDeleteConfirmModel(true), setId(row.id) }} className="la la-trash hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Delete Role</span></i></div>
                    </Protected>
                </React.Fragment>
            )
        },
    ];

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/Role/GetRoles?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`)
        }
        if ((sortBy).length == 0)
        {
            apiUrl = "".concat(apiUrl, `&sortBy=name`);
            sortDirection = "asc"
        }

        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteRole = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/Role/DeleteRole?id=${id}&reason=${reason}`,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel)
        )
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.RolesModify]}>
            <Link
                to={"/roles/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                Create New
            </Link>
        </Protected>
    )

    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.RolesModify]}>
            <Link to={"/roles/create"} className="dropdown-item">
                Create New
            </Link>
        </Protected>
    )

    return (
        <React.Fragment>            
            <ChangePageTitle pageTitle={title} />
            <ManagermentTable shouldReload={shouldReload} setShouldReload={setshouldReload} columns={columns} title={title} apiUrl={apiUrl} createButton={createButton} createButtonMobile={createButtonMobile} searchType="Role Name" />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteRole} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}