import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../Components/FormInput';
import FormActions from '../Components/FormActions';
import { showConfirmModel } from '../Components/Alert';
import PropTypes from 'prop-types';
import Protected from './ProtectedComponent';
import PermissionsConstant from '../assets/permission.config.json'
import Popover from '../Components/Popover';

function OrganizationEditor(props) {
    const [state, setState] = useState({
        id: props.id ?? "",
        name: props.name ?? "",
        code: props.code ?? "",
        description: props.description ?? "",
        wasValidated: false,
        errors: {}
    });

    const handleInputChange = (event) => {
        setState(state => ({ ...state, [event.target.name]: event.target.value }));
        if ("updateOrganization" in props) {
            props.updateOrganization(true);
        }
    }

    function validate() {
        const { name, code, description } = state;
        const errors = {};
        let isValid = true;
        if (!name.trim()) {
            isValid = false
            errors.organizationName = "Organization Name is a required field."
        }
        if (!code.trim()) {
            isValid = false
            errors.organizationCode = "Organization Code is a required field."
        }
        if (!description.trim()) {
            isValid = false
            errors.description = "Description is a required field."
        }
        setState(state => ({ ...state, errors, wasValidated: true }));
        return isValid;
    }

    useEffect(() => {
        setState(state => ({ ...state, errors: props.errors }));
    }, [props.errors])

    const inputClasses = {
        labelClass: "control-label col-md-12",
        colWidthClass: "col-md-12",
        inputClass: "form-control"
    }

    const onSubmit = () => {
        const {
            name,
            code,
            description,
        } = state;
        if (!validate()) return;
        props.submit({
            organizationName: name.trim(),
            organizationCode: code.trim(),
            description: description.trim()
        })
    }

    const formActions = [
        {
            text: props.buttontype,
            onClick: onSubmit,
            className: "btn btn-primary submitButton",
            requirePermissions: [PermissionsConstant.OrganizationsModify]
        },
        {
            text: "Cancel",
            onClick: () => showConfirmModel({
                message: "Your form has not been submitted. Are you sure you want to exit?",
                confirmBtnText: "Exit"
            },
                (result) => {
                    if (result) {
                        props.history.push('/applink')
                    }
                }),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    const {
        name,
        code,
        id,
        description,
        errors,
        wasValidated
    } = state;
    return (
        <React.Fragment>
            <div className="row">
                <div className="col">
                    <div className="card">
                        {props.header}
                        <div className="card-body p-b-20">
                            <form action="#" className="form-horizontal">
                                <Protected requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                                    <div className="form-body">
                                        {!id && <div className="form-group row">
                                            <label className={inputClasses.labelClass}>Create your organization profile. You will be able to manage the settings once your organization has been created.</label>
                                        </div>
                                        }
                                        <div className="form-group row">
                                            <FormInput
                                                label="Name"
                                                value={name}
                                                onChange={handleInputChange}
                                                name="name"
                                                autoComplete="name"
                                                type="text"
                                                id="name"
                                                error={errors.organizationName}
                                                wasValidated={wasValidated}
                                                isRequired={true}
                                            />
                                        </div>
                                        <div className="form-group row">
                                            <FormInput
                                                label="Code"
                                                value={code}
                                                onChange={handleInputChange}
                                                name="code"
                                                autoComplete="code"
                                                type="text"
                                                id="code"
                                                error={errors.organizationCode}
                                                wasValidated={wasValidated}
                                                isRequired={true}
                                            />
                                            <Popover
                                                id="tooltiphoverCreateOrganizationCode"
                                                content="<center>This code will be used for sign-in using staff id. This need to be unique globally.<br/><br/><b>Recommendation:</b> use your Active Directory or corporate domain name.<center>"
                                                placement="top"
                                                maxWidth="350px"
                                                iconClass="la la-question-circle la-2x"
                                            />
                                        </div>
                                        {id && <div className="form-group row">
                                            <FormInput
                                                disabled={true}
                                                label="Account No"
                                                value={id}
                                                onChange={handleInputChange}
                                                name="id"
                                                autoComplete="id"
                                                type="text"
                                                id="id"
                                            />
                                            <Popover
                                                id="tooltiphoverAccountNo"
                                                content="<center>System Assigned Unique ID<center>"
                                                placement="top"
                                                maxWidth="350px"
                                                iconClass="la la-question-circle la-2x"
                                            />
                                        </div>}
                                        <div className="form-group row">
                                            <FormInput
                                                label="Description"
                                                onChange={handleInputChange}
                                                name="description"
                                                autoComplete="description"
                                                type="textarea"
                                                id="description"
                                                rows="3"
                                                value={description}
                                                error={errors.description}
                                                wasValidated={wasValidated}
                                                isRequired={true}
                                            />
                                        </div>
                                    </div>
                                </Protected>
                            </form>
                        </div>
                        <div className="card-footer bg-light">
                            <FormActions actions={formActions} />
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

OrganizationEditor.propTypes = {
    id: PropTypes.string,
    organizationName: PropTypes.string,
    organizationCode: PropTypes.string,
    description: PropTypes.string,
}

export default withRouter(OrganizationEditor)