import React from "react";
import TermsAndConditionsEditor from "../../../Components/TermsAndConditionsEditor";
import fetcher from '../../../utils/fetcher'
import { showErrorAlert } from '../../../Components/Alert'
import { webserviceUrl } from '../../../utils/configuration'

class CreateTermsAndConditions extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {}
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/TermsAndConditions/Create`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify(data),
            },
            () => this.props.history.push('/platformadmin/termsandconditions'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    render() {
        const { errors } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">Create new Terms and Condition document</h5>
            </div>
        );

        return <TermsAndConditionsEditor errors={errors} header={header} submit={this.submit} />
    }
}

export default CreateTermsAndConditions