import React from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration'
import { SuccessfullyDeleted } from '../../../utils/Constant'
import fetcher from '../../../utils/fetcher';
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel'
import Protected from '../../../Components/ProtectedComponent'
import PermissionsConstant from '../../../assets/permission.config.json'
import ChangePageTitle from "../../../Components/ChangePageTitle";

export default function OrganizationManagermentTable() {
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);
    const columns = [
        {
            name: 'Organization Name',
            selector: 'organizationName',
            sortable: true,
            maxWidth: "50%"
        },
        {
            name: 'Primary Admin',
            sortable: false,
            maxWidth: "20%",
            cell: row => {
                return (
                    <div>
                        <ul className="table-list square">
                            {row.clientAdmins.map(item => <li key={item}>{item}</li>)}
                        </ul>
                    </div>
                )
            }
        },
        {
            name: 'Subscriptions',
            sortable: false,
            maxWidth: "15%",
            cell: row => {
                return (
                    <div>
                        <ul className="table-list square">
                            {row.applications.map(item => <li key={item}>{item}</li>)}
                        </ul>
                    </div>
                )
            }
        },
        {
            name: 'Actions',
            selector: 'edit',
            maxWidth: "15%",
            cell: row =>
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.OrganizationsModify]}>
                        <Link to={`/organizations/edit/${row.id}`}><i className="la la-edit icon-big"><span className="tooltiptextTop">Edit Organization</span></i></Link>&nbsp;
                    </Protected>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.OrganizationsDelete]}>
                        <div><i onClick={() => { setShowDeleteConfirmModel(true), setId(row.id) }} className="la la-trash icon-big"><span className="tooltiptextTop">Delete Organization</span></i></div>
                    </Protected>
                </React.Fragment>
        }
    ];

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/Organization/GetOrganizations?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`)
        }
        if ((sortBy).length == 0) {
            apiUrl = "".concat(apiUrl, `&sortBy=organizationName`);
            sortDirection = "asc"
        }

        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteOrganization = (id, name, reason) => {
        fetcher(
            `${webserviceUrl}/api/Organization/DeleteOrganization?id=${id}&name=${name}&reason=${reason}`,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => (showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel))
        )
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.OrganizationsModify]}>
            <Link
                to={"/organizations/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                Create New
            </Link>
        </Protected>
    )
    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.OrganizationsModify]}>
            <Link to={"/organizations/create"} className="dropdown-item">
                Create New
            </Link>
        </Protected>
    )

    const title = "Search Organizations";
    return (
        <React.Fragment>
            <ChangePageTitle pageTitle={title} />
            <ManagermentTable shouldReload={shouldReload}
                setShouldReload={setshouldReload}
                columns={columns}
                title={title}
                apiUrl={apiUrl}
                createButton={createButton}
                createButtonMobile={createButtonMobile}
                searchType="Organization Name"
            />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteOrganization} requiredName={true} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}