import React, { Component } from 'react';
import { AsyncPaginate } from "react-select-async-paginate";
import fetcher from '../utils/fetcher';
import { webserviceUrl } from '../utils/configuration'
import { UserContext } from '../Context/UserContext';

const apiUrl = (page, pageSize, searchTerm, organizationId) => {
    let apiUrl = `${webserviceUrl}/api/User/GetUsers?page=${page}&pageSize=${pageSize}&organizationId=${organizationId}`;
    if (searchTerm) {
        apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
    }
    return apiUrl
}

export default class UserSelector extends Component {
    static contextType = UserContext
    constructor(props) {
        super(props)
        this.state = {
            value: props.value ?? null,
            additional: {
                page: 1
            },
        }
    }

    loadOptions = async (search, loadedOptions, { page }) => {
        const user = this.context
        const promise = new Promise(function (resolve, reject) {
            fetcher(
                apiUrl(page, 10, search, user.organizationId),
                {},
                (response) => {
                    const applications = response.data.data.map(x => {
                        return {
                            value: x.id,
                            label: x.userFullName,
                            data: x
                        }
                    })
                    resolve(applications)
                },
                () => {
                    reject()
                }
            )
        });

        const result = await promise;
        return {
            options: result,
            hasMore: result.length == 10,
            additional: {
                page: page + 1,
            },
        }
    }

    onChange = (selectedItem) => {
        this.setState({ value: selectedItem })
        this.props.onChange(selectedItem?.data)
    }

    filterOption = (option) => {
        const { removeOptions } = this.props;
        return !removeOptions.includes(option.value);
    }

    render() {
        const { value } = this.state;
        return (
            <AsyncPaginate
                styles={this.props.styles}
                value={value}
                loadOptions={this.loadOptions}
                onChange={this.onChange}
                additional={{
                    page: 1
                }}
                filterOption={this.props.removeOptions ? this.filterOption : false}
                isClearable={true}
                className={this.props.className}
                classNamePrefix="react-select"
                isDisabled={this.props.isDisabled}
            />
        );
    }
}