import React from "react";
import LinkedAppEditor from "../../../Components/LinkedAppEditor";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';

class CreateLinkedApp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {}
        }
      }
  
    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Application/CreateApplication`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify(data),
            },
            () => this.props.history.push('/linkedApps'),
            (ex) =>
            {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }

        )
    }

    render() {
        const { errors } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">Create Application</h5>
            </div>
        );

        return <LinkedAppEditor errors={errors} header={header} submit={this.submit}/>
    }
}

export default CreateLinkedApp