import React, { Component } from 'react';
import { AsyncPaginate } from "react-select-async-paginate";
import fetcher from '../utils/fetcher';
import { webserviceUrl } from '../utils/configuration'

const apiUrl = (page, pageSize, searchTerm) => {
    let apiUrl = `${webserviceUrl}/api/Organization/AvailableOrganization?page=${page}&pageSize=${pageSize}`;
    if (searchTerm) {
        apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
    }
    return apiUrl
}

export default class OrganizationSelector extends Component {
    constructor(props) {
        super(props)
        this.state = {
            value: null,
            additional: {
                page: 1
            },
        }
    }

    loadOptions = async (search, loadedOptions, { page }) => {
        const promise = new Promise(function (resolve, reject) {
            fetcher(
                apiUrl(page, 10, search),
                {},
                (response) => {
                    const organizations = response.data.data.map(x => {
                        return {
                            value: x.organizationName,
                            label: x.organizationName,
                            data: x
                        }
                    })
                    resolve(organizations)
                },
                () => {
                    reject()
                }
            )
        });

        const result = await promise;
        return {
            options: result,
            hasMore: result.length == 10,
            additional: {
                page: page + 1,
              },
        }
    }

    onChange = (selectedItem) => {
        this.setState({ value: selectedItem })
        this.props.onChange(selectedItem.data)
    }

    render() {
        const { value } = this.state;
        return (
            <AsyncPaginate
                styles={this.props.styles}
                value={value}
                loadOptions={this.loadOptions}
                onChange={this.onChange}
                additional={{
                    page: 1
                }}
                className={this.props.className}
                classNamePrefix="react-select"
                isDisabled={this.props.isDisabled}
            />
        );
    }
}