import React from "react";
import ScopesEditor from "../../../Components/ScopesEditor";
import fetcher from '../../../utils/fetcher'
import { showErrorAlert } from '../../../Components/Alert'
import { webserviceUrl } from '../../../utils/configuration'
import ChangePageTitle from "../../../Components/ChangePageTitle";

class CreateScope extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {}
        }
      }
    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Scope/CreateScope`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify(data),
            },
            () => this.props.history.push('/AppSubscription/apiscopes'),
            (ex) =>
            {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    render() {
        const title = "Create Scope";
        const { errors } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}</h5>
            </div>
        );
        
        return (
            <div>
                <ChangePageTitle pageTitle={title} />
                <ScopesEditor errors={errors} header={header} submit={this.submit} buttontype="Create" />
            </div>
        )
    }
}

export default CreateScope