import React from 'react'
import FormInput from '../Components/FormInput'

function DeleteConfirmModel(props) {
    const [reason, setReason] = React.useState("");
    const [name, setName] = React.useState("");
    const [errors, setErrors] = React.useState({})
    const [wasValidated, setWasValidated] = React.useState(false)

    const onDelete = () => {
        let isValid = true;
        const errors = {};
        if (props.requiredName && !name) {
            isValid = false
            errors.name = "Organization Name is a required field."
        }
        if (!reason) {
            isValid = false
            errors.reason = "Reason is a required field."
        }

        if (reason && reason.length > 500) {
            isValid = false
            errors.reason = "Reason cannot exceed 500 characters."
        }

        if (!isValid) {
            setErrors(errors);
            setWasValidated(true)
            return
        }
        if (props.requiredName) {
            props.delete(props.id, name, reason);
            return
        }
        props.delete(props.id, reason);
    }

    return (
        <React.Fragment>
            <div className="modal fade show" tabIndex="-1" role="dialog" style={{ display: "block", opacity: 1, transition: "opacity .15s linear" }}>
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">Delete Confirmation</h5>
                            <button onClick={props.closeModel} type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <form>
                                <div className="form-group row">
                                    {props.requiredName && <FormInput
                                        label="Name"
                                        name="deleteName"
                                        autoComplete="deleteName"
                                        type="textarea"
                                        id="deleteName"
                                        rows="1"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                        error={errors.name}
                                        wasValidated={wasValidated}
                                        isRequired={true} />}
                                </div>
                                <div className="form-group row">
                                    <FormInput
                                        label="Reason"
                                        name="deleteReason"
                                        autoComplete="deleteReason"
                                        type="textarea"
                                        id="deleteReason"
                                        rows="5"
                                        value={reason}
                                        onChange={(e) => setReason(e.target.value)}
                                        error={errors.reason}
                                        wasValidated={wasValidated}
                                        isRequired={true} />
                                </div>
                            </form>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" onClick={onDelete}>Proceed</button>
                            <button type="button" className="btn btn-secondary" onClick={props.closeModel} data-dismiss="modal">Cancel</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal-backdrop fade show"></div>
        </React.Fragment>
    )
}

export default DeleteConfirmModel