import React, { useContext } from 'react'
import { UserContext } from '../Context/UserContext'
import { Route, Redirect } from "react-router-dom";

function ProtectedComponent(props) {
    var user = useContext(UserContext);
    const found = !props.requirePermissions || props.requirePermissions.length == 0 || user.permissions?.find(permission => props.requirePermissions.includes(permission.name));
    if (true) {
        return props.children
    }
    if (props.action == "disable") {
        return <span className="unauthorized-disabled">{props.children}</span>
    }
    if (props.isRoute) {
        return <Route component={() => <Redirect to='/404' />} />
    }
    return null
}

export default ProtectedComponent