import React from "react";
import LinkedAppEditor from "../../../Components/LinkedAppEditor";
import fetcher from '../../../utils/fetcher'
import { showErrorAlert } from '../../../Components/Alert'
import { webserviceUrl } from '../../../utils/configuration'
import { DefaultErrorMessage } from '../../../utils/Constant'
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop'

class EditLinkedApp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {},
            name: ""
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Application/UpdateApplication`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id })
            },
            () => this.props.history.push('/linkedApps'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    componentDidMount() {
        fetcher(
            `${webserviceUrl}/api/Application/GetApplication?id=${this.props.match.params.id}`,
            {},
            (result) => {
                const {
                    name,
                    description,
                    appUrl,
                    icon
                } = result.data;
                this.apiClientData = {
                    name,
                    description,
                    appUrl,
                    icon
                }
                this.setState({ name })
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    render() {
        const { errors, name } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">Edit App: {name}</h5>
            </div>
        );
        const { isLoading } = this.props;
        return (
            <React.Fragment>
                {!isLoading && <LinkedAppEditor header={header} errors={errors} submit={this.submit} {...this.apiClientData} isEdit={true} />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(EditLinkedApp)