import { UserManager, Log } from 'oidc-react'
import { appCenterUrl, id4Url, client_id } from './configuration'

if(process.env.MODE == "local"){
    Log.level = Log.DEBUG
    Log.logger = console
}


const config = {
    authority: id4Url,
    client_id: client_id,
    redirect_uri: `${appCenterUrl}`,
    response_type: "code",
    scope: "openid profile ascentiscx email",
    automaticSilentRenew: true,
    silent_redirect_uri: `${appCenterUrl}`,
    post_logout_redirect_uri: `${appCenterUrl}`
};

export const userManagerService = new UserManager(config);

userManagerService.events.addUserSignedOut(x => {
    userManagerService.signinRedirect()
})

userManagerService.originalSignin = userManagerService.signinRedirect

userManagerService.signinRedirect = (params) => userManagerService.originalSignin(params).catch(error => {
    window.location.replace(`${appCenterUrl}/ConnectionDown`);
})

userManagerService.originalSigninRedirectCallback = userManagerService.signinRedirectCallback

userManagerService.signinRedirectCallback = (params) => userManagerService.originalSigninRedirectCallback(params).catch(error => {
    window.location.replace(`${appCenterUrl}/Unauthorized`);
})

userManagerService.events.addAccessTokenExpiring(x => {
    userManagerService.signinSilent()
})

userManagerService.events.addAccessTokenExpired(x => {
    userManagerService.signinSilent()
})