import React from "react";
import ScopesEditor from "../../../Components/ScopesEditor";
import fetcher from '../../../utils/fetcher';
import { showErrorAlert } from '../../../Components/Alert';
import { webserviceUrl } from '../../../utils/configuration';
import { DefaultErrorMessage } from '../../../utils/Constant';
import withLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class EditPermission extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showDeleteConfirmModel: false,
            errors: {},
            name: ""
        }
    }

    submit = (data) => {
        fetcher(
            `${webserviceUrl}/api/Scope/UpdateScope`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({ ...data, id: this.props.match.params.id })
            },
            () => this.props.history.push('/AppSubscription/apiscopes'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if(!result.Data) 
                { 
                    showErrorAlert(result.message) 
                }
                else {
                    this.setState({ errors: result.Data});
                }
            }
        )
    }

    componentDidMount() {

        fetcher(
            `${webserviceUrl}/api/scope/GetScope?id=${this.props.match.params.id}`,
            {},
            (result) => {
                const {
                    name,
                    description,
                    displayName,
                    application,
                    status
                } = result.data;

                this.scope = {
                    name,
                    description,
                    displayName,
                    application,
                    status
                }
                this.setState({ name })
                this.props.setLoading(false)
            },
            () => {
                this.props.setLoading(false)
                showErrorAlert(DefaultErrorMessage)
            }
        )
    }

    render() {
        const title = "Edit Scope";
        const { errors, name } = this.state;
        const header = (
            <div className="card-header card-header-flex">
                <h5 className="card-header-text">{title}: {name}</h5>
            </div>
        );

        const { isLoading } = this.props;
        return (
            <React.Fragment>
                <ChangePageTitle pageTitle={title} />
                {!isLoading && <ScopesEditor errors={errors} header={header} submit={this.submit} {...this.scope} isEdit={true} />}
            </React.Fragment>
        )
    }
}

export default withLoadingBackDrop(EditPermission)