import React, { Fragment } from 'react';
import DataTable from 'react-data-table-component';
import TableHeader from '../../../Components/TableHeader';
import CustomDataTablePagination from '../../../Components/CustomDataTablePagination';
import CustomTableStyle from '../../../utils/CustomTableStyle'
import fetcher from '../../../utils/fetcher';
import { webserviceUrl } from '../../../utils/configuration';
import WithLoadingBackDrop from '../../../Components/HOC/WithLoadingBackDrop';
import { showErrorAlert } from '../../../Components/Alert'
import { DefaultErrorMessage } from '../../../utils/Constant'
import FormActions from '../../../Components/FormActions';
import ChangePageTitle from "../../../Components/ChangePageTitle";

class AssignUser extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: {},
            items: [],
            page: 1,
            pageSize: 10,
            total: 0,
            sortColumn: "",
            sortDirection: "desc",
            searchTerm: "",
            searchText: "",
            assignedOrganization: [],
            isCheckAll: false
        };
    }

    setPage = (page) => this.setState({ page });

    toggleCheckAll = () => {
        let { isCheckAll, items } = this.state;
        isCheckAll = !isCheckAll;
        let assignedOrganization;
        if (isCheckAll) {
            assignedOrganization = items.map(x => x.id);
        } else {
            assignedOrganization = [];
        }
        this.setState({ isCheckAll, assignedOrganization });
    }

    setPageSize = (pageSize) => {
        pageSize = parseInt(pageSize);
        const page = this.preventPageOverflow(pageSize);
        this.setState({ pageSize, page })
    };

    setSortColumn = (column, sortDirection) => this.setState({ sortColumn: column.selector, sortDirection });

    preventPageOverflow = (newPageSize) => {
        const { page, pageSize } = this.state;
        let startItem = (page - 1) * pageSize + 1;
        return Math.ceil(startItem / newPageSize);
    }

    apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/Organization/GetOrganizations?page=${page}&pageSize=${pageSize}`;
        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}&isDesc=${sortDirection == "desc"}`)
        }
        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    getItems = () => {
        const { page, pageSize, searchTerm, sortColumn, sortDirection } = this.state;
        const url = this.apiUrl(page, pageSize, searchTerm, sortColumn, sortDirection);
        const getOrganizationsPromise = new Promise((resolve, reject) => {
            fetcher(
                url,
                {},
                (result) => {
                    this._isMounted && this.setState({
                        items: result.data.data,
                        total: result.data.total
                    });
                    resolve();
                },
                () => {
                    showErrorAlert(DefaultErrorMessage);
                    reject();
                }
            )
        })
        return getOrganizationsPromise;
    }

    submit = () => {
        fetcher(
            `${webserviceUrl}/api/User/AssignUser`,
            {
                method: 'POST',
                cache: 'no-cache',
                mode: 'cors',
                body: JSON.stringify({
                    organizationIds: this.state.assignedOrganization,
                    userId: this.props.match.params.id
                })
            },
            () => this.props.history.push('/users'),
            (ex) => {
                const result = JSON.parse(ex.message);
                if (!result.Data) {
                    showErrorAlert(result.message)
                }
                else {
                    this.setState({ errors: result.Data });
                }
            }
        )
    }

    formActions = [
        {
            text: "Update",
            onClick: this.submit,
            className: "btn btn-primary submitButton"
        },
        {
            text: "Cancel",
            onClick: () => this.props.history.goBack(),
            className: "btn btn-secondary clear-form btn-outline"
        }
    ]

    componentDidMount() {
        this._isMounted = true;
        const getOrganizationsPromise = this.getItems();
        const getAssignOrganizationApi = `${webserviceUrl}/api/User/AssignedOrganization?UserId=${this.props.match.params.id}`;
        const getUserDataPromise = new Promise((resolve, reject) => {
            fetcher(
                `${webserviceUrl}/api/User/GetUser?userId=${this.props.match.params.id}`,
                {},
                (result) => {
                    const { staffId, email, mobileNumber } = result.data;
                    const userName = email ?? mobileNumber ?? staffId;
                    this.setState({ userName });
                    resolve();
                },
                () => {
                    showErrorAlert(DefaultErrorMessage)
                    reject();
                }
            )
        })

        const getAssignedOrganizationPromise = new Promise((resolve, reject) => {
            fetcher(
                getAssignOrganizationApi,
                {},
                (result) => {
                    this._isMounted && this.setState({
                        assignedOrganization: result.data ?? [],
                    });
                    resolve();
                },
                () => {
                    showErrorAlert(DefaultErrorMessage);
                    reject();
                }
            )
        });
        Promise.all([getOrganizationsPromise, getAssignedOrganizationPromise, getUserDataPromise]).then(x => {
            this.props.setLoading(false);
        });
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    componentDidUpdate(prevProps, prevState) {
        const { page, pageSize, searchTerm } = this.state;
        if (
            prevState.page != page
            || prevState.pageSize != pageSize
            || prevState.searchTerm != searchTerm
            || this.props.shouldReload
        ) {
            this.getItems();
        }
    }

    handleInputChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    onChangeAssignOrganiztion = (event) => {
        const currentAssignedOrganization = this.state.assignedOrganization;
        if (event.target.checked) {
            currentAssignedOrganization.push(event.target.value);
            this.setState({ currentAssignedOrganization });
            return;
        }

        const index = currentAssignedOrganization.indexOf(event.target.value);
        if (index > -1) {
            currentAssignedOrganization.splice(index, 1);
            this.setState({ currentAssignedOrganization });
        }
    }

    search = () => this.setState({ searchTerm: this.state.searchText });

    onSearchInput = (e) => {
        if (!e.target.value) {
            this.setState({ searchText: "", searchTerm: "" })
        }
        this.setState({ searchText: e.target.value })
    }

    render() {
        const { items, pageSize, total, page, searchText, assignedOrganization, isCheckAll } = this.state;

        const columns = [
            {
                name: <input style={{ top: 0 }} type="checkbox" checked={isCheckAll} onChange={this.toggleCheckAll} />,
                maxWidth: "3%",
                minWidth: "70px",
                cell: row => <input style={{ top: 0 }} type="checkbox" value={row.id} checked={assignedOrganization.includes(row.id)} onChange={this.onChangeAssignOrganiztion} />
            },
            {
                name: 'Organization',
                selector: 'organizationName',
                sortable: true,
                maxWidth: "97%",
                cell: row => row.organizationName
            }
        ];

        const tableHeader = (
            <TableHeader
                searchText={searchText}
                onChangePageSize={this.setPageSize}
                pageSize={pageSize}
            />
        )

        const tableSearchElement = (
            <React.Fragment>
                <div>
                    <div className="table-search-input-wrapper">
                        <div className="input-group md-form form-sm form-1 pl-0">
                            <input onChange={this.onSearchInput} className="form-control my-0 py-1" type="text" placeholder="Organization" aria-label="Search" />
                            <div className="input-group-prepend">
                                <span onClick={this.search} className="input-group-text color-primary btn-primary lighten-3 btn-search" id="basic-text1">
                                    <i className="la la-search text-white" aria-hidden="true"></i>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )

        return (
            <Fragment>
                <ChangePageTitle pageTitle="Assign Organization" />
                <div className="main-content">
                    <section className="page-content container-fluid">
                        <div className="card">
                            <div className="card-header card-header-flex">
                                <h5 className="card-header-text">Assign Organization: {this.state.userName}</h5>
                            </div>
                            <div className="row">
                                <div className="col-lg-12 col-xl-12">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="container-fluid">
                                                <DataTable
                                                    title={tableSearchElement}
                                                    actions={tableHeader}
                                                    columns={columns}
                                                    data={items}
                                                    pagination={true}
                                                    paginationServer={true}
                                                    customStyles={CustomTableStyle}
                                                    paginationTotalRows={total}
                                                    onChangePage={this.setPage}
                                                    sortServer={false}
                                                    striped={true}
                                                    responsive={true}
                                                    paginationComponent={(props) => <CustomDataTablePagination {...props} currentPage={page} rowsPerPage={pageSize} />}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="card-footer bg-light">
                                <FormActions actions={this.formActions} />
                            </div>
                        </div>
                    </section>
                </div>
            </Fragment>
        )
    }
}

export default WithLoadingBackDrop(AssignUser)