import React, { useContext } from 'react';
import ManagermentTable from '../../../Components/ManagermentTable';
import { Link } from 'react-router-dom';
import { webserviceUrl } from '../../../utils/configuration'
import { showErrorAlert, showSuccessAlert } from '../../../Components/Alert';
import fetcher from '../../../utils/fetcher';
import usericon from '../../../assets/img/usericon.png';
import { SuccessfullyDeleted } from '../../../utils/Constant';
import DeleteConfirmModel from '../../../Components/ConfirmDeleteModel';
import { UserContext } from '../../../Context/UserContext';
import PermissionsConstant from '../../../assets/permission.config.json';
import Protected from '../../../Components/ProtectedComponent';
import ChangePageTitle from "../../../Components/ChangePageTitle";

export default function UserManagermentTable() {
    const [id, setId] = React.useState("");
    const [showDeleteConfirmModel, setShowDeleteConfirmModel] = React.useState(false);
    const [shouldReload, setshouldReload] = React.useState(false);
    const user = useContext(UserContext);

    const resendActiveEmail = (id) => {
        fetcher(
            `${webserviceUrl}/api/User/ResendActiveEmail?id=${id}`,
            {
                method: 'GET',
                cache: 'no-cache',
                mode: 'cors',
                headers: {
                    'content-type': 'application/json',
                }
            },
            () => showSuccessAlert('Activation Email Sent'),
        );
    };
    const columns = [
        {
            name: 'Name',
            selector: 'userFullName',
            sortable: true,
            maxWidth: "35%",
            cell: row => (
                <div><img src={row.profilePicture ? row.profilePicture : usericon} className="w-20 h-20 rounded-circle" />&nbsp;&nbsp;{row.userFullName ? row.userFullName : '{PendingActivation}'}</div>
            )
        },
        {
            name: 'Staff Id',
            selector: 'staffId',
            sortable: true,
            maxWidth: "15%"
        },
        {
            name: 'Email Address',
            selector: 'email',
            sortable: true,
            maxWidth: "20%"
        },
        {
            name: 'Role',
            selector: 'userRole',
            sortable: false,
            maxWidth: "10%"
        },
        {
            name: 'Enabled',
            selector: 'status',
            sortable: true,
            cell: row => row.status,
            maxWidth: "10%"
        },
        {
            name: 'Actions',
            selector: 'edit',
            sortable: false,
            maxWidth: "12%",
            cell: row =>
                <React.Fragment>
                    <Protected action="disable" requirePermissions={[PermissionsConstant.UserModify]}>
                        <Link to={`/users/edit/${row.id}`}><i className="la la-edit tooltipTop icon-big"><span className="tooltiptextTop">Manage</span></i></Link>&nbsp;
                        {row.passwordNotCreated && row.email?.length > 0 ?
                            <div>{row.email && <i onClick={() => resendActiveEmail(row.id)} className="la la-envelope hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Verify email</span>&nbsp;</i>}</div> :
                            <Link to={`/users/ResetUserPassword/${row.id}`}><i className="la la-expeditedssl tooltipTop icon-big"><span className="tooltiptextTop">Reset Password</span>&nbsp;</i></Link>}
                    </Protected>
                    {row.canBeAssign && <Protected requirePermissions={[PermissionsConstant.AssignUser]}>
                        <Link to={`/users/assignuser/${row.id}/${row.userFullName}`}><i className="la la-user-plus tooltipTop icon-big"  ><span className="tooltiptextTop">Assign Organization</span>&nbsp;</i></Link>
                    </Protected>}
                    <Protected action="disable" requirePermissions={[PermissionsConstant.UserDelete]}>
                        <div><i onClick={() => { setShowDeleteConfirmModel(true), setId(row.id) }} className="la la-trash hover-pointer tooltipTop icon-big"><span className="tooltiptextTop">Delete</span>&nbsp;</i></div>
                    </Protected>                   
                </React.Fragment>
        },
    ];

    const closeDeleteConfirmModel = () => {
        setShowDeleteConfirmModel(false)
        setshouldReload(true)
    }

    const deleteUser = (id, reason) => {
        fetcher(
            `${webserviceUrl}/api/User/DeleteUser?userId=${id}&organizationId=${user.organizationId}&reason=${reason} `,
            {
                method: 'DELETE',
                cache: 'no-cache',
                mode: 'cors'
            },
            () => showSuccessAlert(SuccessfullyDeleted, closeDeleteConfirmModel),
            (ex) => showErrorAlert(JSON.parse(ex.message).message, closeDeleteConfirmModel)
        )
    }

    const title = "Search Users";

    const apiUrl = (page, pageSize, searchTerm, sortBy, sortDirection) => {
        let apiUrl = `${webserviceUrl}/api/User/GetUsers?page=${page}&pageSize=${pageSize}`;

        if (searchTerm) {
            apiUrl = "".concat(apiUrl, `&searchTerm=${encodeURIComponent(searchTerm)}`)
        }
        if (sortBy) {
            apiUrl = "".concat(apiUrl, `&sortBy=${sortBy}`);
        }
        if ((sortBy).length == 0) {
            apiUrl = "".concat(apiUrl, `&sortBy=userFullName`);
            sortDirection = "asc"
        }

        apiUrl = "".concat(apiUrl, `&organizationId=${user.organizationId}`)
        apiUrl = "".concat(apiUrl, `&isDesc=${sortDirection == "desc"}`)
        return apiUrl;
    }

    const createButton = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.UserModify]}>
            <Link
                to={"/users/create"}
                className="btn btn-primary"
                aria-haspopup="true" aria-expanded="true"
            >
                Create New
            </Link>
        </Protected>
    )

    const createButtonMobile = (
        <Protected action="disable" requirePermissions={[PermissionsConstant.UserModify]}>
            <Link to={"/users/create"} className="dropdown-item">
                Create New
            </Link>
        </Protected>
    )


    return (
        <React.Fragment>
            <ChangePageTitle pageTitle="Search Users" />

            <ManagermentTable
                shouldReload={shouldReload}
                setShouldReload={setshouldReload}
                columns={columns}
                title={title}
                apiUrl={apiUrl}
                createButton={createButton}
                createButtonMobile={createButtonMobile}
                searchType="Search *"
            />
            {showDeleteConfirmModel && <DeleteConfirmModel id={id} delete={deleteUser} closeModel={closeDeleteConfirmModel} />}
        </React.Fragment>
    )
}