import React from 'react';
import { withRouter } from 'react-router-dom';
import FormInput from '../../../Components/FormInput';
import { makeRandomSecureString } from '../../../utils/random';
import ChangePageTitle from "../../../Components/ChangePageTitle";
import Popover from "../../../Components/Popover";

function ClientId(props) {
  const handleInputChange = props.handleInputChange;
  const grantTypeOptions = [
    {
      name: 'authorization_code',
      value: 'authorization_code',
    },
    {
      name: 'client_credentials',
      value: 'client_credentials',
    },
    {
      name: 'password',
      value: 'password',
    },
  ];
  const { clientApi, errors, wasValidated } = props;

  const resetClientSecret = () => {
    handleInputChange("clientSecrets", makeRandomSecureString(26))
  }

  const resetClientId = () => {
    handleInputChange("clientId", makeRandomSecureString(26))
  }

  const inputClasses = {
    labelClass: "control-label col-md-12",
    colWidthClass: "col-md-12",
    inputClass: "form-control"
  }

  return (
    <React.Fragment>
      <ChangePageTitle pageTitle="API Credential Client ID" />
      <div className="row">
        <div className="col">
          {props.header}
          <div className="card">
            <div className="card-body">
              <form
                noValidate
                action="#"
                className="form-horizontal"
              >
                <div className="form-body">
                  <div className="form-group row">
                    <label className={inputClasses.labelClass}>A Client Id is used to identify a single client application to AscentisCX SSO. Each instance of your app (web/mobile/desktop app/etc) requires it's own client ID.</label>
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="Client App Name"
                      value={clientApi.clientAppName}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      name="clientAppName"
                      autoComplete="clientAppName"
                      type="text"
                      id="clientAppName"
                      error={errors.clientAppName}
                      wasValidated={wasValidated}
                      isRequired={true}
                    />
                  </div>
                  {props.isEdit && <div className="form-group row">
                    <FormInput
                      label="Client Id"
                      value={clientApi.clientId}
                      name="clientId"
                      autoComplete="clientId"
                      type="text"
                      id="clientId"
                      disabled={true}
                    />
                    <i onClick={() => resetClientId()} className="la la-refresh hover-pointer refresh-icon tooltipTop">
                      <span className="tooltiptextTop">Reset Client Id (If you change this, you will need to change your client application.)</span>
                    </i>
                  </div>}
                  {props.isEdit && <div className="form-group row">
                    <FormInput
                      label="Client Secret"
                      value={clientApi.clientSecrets}
                      name="clientSecrets"
                      autoComplete="clientSecrets"
                      type="text"
                      id="clientSecrets"
                      disabled={true}
                    />
                    <i onClick={() => resetClientSecret()} className="la la-refresh hover-pointer refresh-icon tooltipTop">
                      <span className="tooltiptextTop">Reset Client Secret (If you change this, you will need to change your client application.)</span>
                    </i>
                  </div>}
                  <div className="form-group row">
                    <FormInput
                      label="Grant Type"
                      value={clientApi.allowedGrantType}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      name="allowedGrantType"
                      autoComplete="allowedGrantType"
                      type="select"
                      id="allowedGrantType"
                      options={grantTypeOptions}
                      isRequired={true}
                    />
                    <Popover
                      id="tooltiphoverGrantType"
                      content="<br/><ol style=padding-right:25px;padding-bottom:-15px;text-align:justify;><li>Authorization Code: for user to login using SSO login form.</li><li>Client Credentials: for system to login to SSO.</li><li>Password: for user to login using Client App login form.</li></ol>"
                      placement="top"
                      maxWidth="275px"
                      iconClass="la la-question-circle la-2x"
                    />
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="Login redirect URL"
                      value={clientApi.redirectUris}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      name="redirectUris"
                      autoComplete="redirectUris"
                      type="text"
                      id="redirectUris"
                      error={errors.redirectUris}
                      wasValidated={wasValidated}
                      isRequired={true}
                    />
                    < Popover
                      id="tooltiphoverLoginURL"
                      content="<center>This is where AscentisCx SSO will return the token to.</center>"
                      placement="top"
                      maxWidth="275px"
                      iconClass="la la-question-circle la-2x"
                    />
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="Logout redirect URL"
                      value={clientApi.postLogoutRedirectUris}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      name="postLogoutRedirectUris"
                      autoComplete="postLogoutRedirectUris"
                      type="text"
                      id="postLogoutRedirectUris"
                      error={errors.postLogoutRedirectUris}
                      wasValidated={wasValidated}
                    />
                    < Popover
                      id="tooltiphoverLogoutURL"
                      content="<center>This is where AscentisCx SSO will trigger logout for client application. This is optional.</center>"
                      placement="top"
                      maxWidth="275px"
                      iconClass="la la-question-circle la-2x"
                    />
                  </div>
                  <div className="form-group row">
                    <FormInput
                      label="JavaScript client origin"
                      value={clientApi.allowedCorsOrigins}
                      onChange={(e) =>
                        handleInputChange(
                          e.target.name,
                          e.target.value
                        )
                      }
                      name="allowedCorsOrigins"
                      autoComplete="allowedCorsOrigins"
                      type="text"
                      id="allowedCorsOrigins"
                      error={errors.allowedCorsOrigins}
                      wasValidated={wasValidated}
                    />
                    < Popover
                      id="tooltiphoverJavaScriptClientOrigin"
                      content="<center>This is the domain name of the client application, if any. It is required if the client application is hosted on a different domain than Ascentis domain.</center>"
                      placement="top"
                      maxWidth="275px"
                      iconClass="la la-question-circle la-2x"
                    />
                  </div>
                  {props.isEdit && <div className="form-group row">
                    <FormInput
                      label="Created Date"
                      value={clientApi.createdDate}
                      name="createdDate"
                      autoComplete="createdDate"
                      type="text"
                      id="createdDate"
                      disabled={true}
                    />
                  </div>}
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

ClientId.propTypes = {

};

export default withRouter(ClientId);
