import { userManagerService } from './AuthorizationService';
import { SUCCESS } from './Constant'

export default async function fetcher(url, options, onSuccess = () => { }, onError = () => { }) {
    const update = { ...options };
    update.headers = { ...update.headers ?? {} }
    const user = await userManagerService.getUser()

    if (update.method == "POST") {
        update.headers['content-type'] = 'application/json';
    }
    if (user) {
        update.headers.Authorization = `Bearer ${user.access_token}`;
        update.headers.OrganizationId = window.organizationId;
    }

    return fetch(url, update)
        .then(result => {
            if (!result.ok && result.status == 401) {
                throw new Error("Unauthorized")
            }
            return result;
        })
        .then(result => result.json())
        .then(jsonResult => {
            const isValid = jsonResult.status == SUCCESS;
            if (!isValid) {
                throw new Error(JSON.stringify(jsonResult))
            }
            onSuccess(jsonResult);
            return jsonResult;
        })
        .catch((ex) => {
            if (ex.message == "Unauthorized") {
                userManagerService.removeUser();
                window.location = '/Unauthorized'
                return ex
            }
            onError(ex)
        });
}
